import {
  Component,
  ElementRef,
  OnInit,
  ViewChild,
  OnDestroy,
} from "@angular/core";
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { Subscription } from 'rxjs';
import { MatPaginator } from "@angular/material/paginator";
import { MatSort, Sort } from "@angular/material/sort";
import { LiveAnnouncer } from "@angular/cdk/a11y";
import { MatTableDataSource } from "@angular/material/table";
import { rumangoAnimations } from "app/shared/animations/rumango-animations";
import { DownloadService } from "app/shared/services/download.service";
import { baseDenomData } from "app/shared/models/base_denom_summary";

@Component({
  selector: 'app-base-denomination-summary',
  templateUrl: './base-denomination-summary.component.html',
  styleUrls: ['./base-denomination-summary.component.scss'],
  animations: rumangoAnimations
})
export class BaseDenominationSummaryComponent implements OnInit {

  @ViewChild("searchVal") searchVal: ElementRef;
  selectedFilterIndex: number = 0;
  displayedColumns: string[] = ['baseDenominationId', 'denomination', 'currency', 'authStatus', 'recordStatus', 'action'];

  filterByOption: { key: string; value: string }[] = [
    { key: "denomination", value: "Denomination" },
    { key: "baseDenominationId", value: "Base Denomination Id" },
    { key: "currency", value: "Currency" },
  ];
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;

  dataSource = new MatTableDataSource();
  public userdata: any[];
  public items: any[];
  public getItemSub: Subscription;
  currentUser: string;
  loading: boolean;
  fetchBaseDenomValue: string;
  baseDenomDetails:baseDenomData[] = [];
  constructor(
    private dialog: MatDialog,
    private snack: MatSnackBar,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,
    private apiService: ApiService,
    private router:Router,
    private ls:LocalStoreService,
    private dataService:DataService,
    private _liveAnnouncer: LiveAnnouncer,
    private downloadService: DownloadService
  ) { }

  ngOnInit() {
    this.currentUser = this.ls.getItem("ICUST_USER");
    // this.loading = true;
    this.getItemSub = this.apiService.getDenomInfoList().subscribe(data => {
      this.items = data;
      this.getItems();
      // this.loading = false;
    })
  }
  ngOnDestroy() {
    if (this.getItemSub) {
      this.getItemSub.unsubscribe()
    }
  }
  getItems() {
    // this.loading = true;
    this.getItemSub = this.apiService.getDenomInfoList()
      .subscribe((data : any) => {
        if(data){
          for (let index = 0; index < data.length; index++) {
            data[index].baseDenominationId = String(data[index].baseDenominationId);
            data[index].denomination = String(data[index].denomination);
            
            if(data[index].authStatus == 'U'){
              data[index].authStatus = 'Unauthorized'
            }
            if(data[index].authStatus == 'A') {
              data[index].authStatus = 'Authorized'
            } 
            if(data[index].recordStatus == 'O'){
              data[index].recordStatus = 'Open'
            } 
            if(data[index].recordStatus == 'C'){
              data[index].recordStatus = 'Closed'
            }
          }
        }
        this.dataSource = new MatTableDataSource(data);
        this.userdata = data;
        console.log(this.dataSource);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      })
      // this.loading = false;
  }

   doFilter(filterBy, e) {
    this.filterTable(filterBy);
    this.applyFilter(e);
  }

  filterTable(filterBy) {
    this.dataSource.filterPredicate = (data: any, filter: string): boolean => {
      return (
        data[filterBy]?.toLocaleLowerCase().toString().includes(filter)
      )
    }
  }


  clearFilter() {
    this.dataSource.filter = "";
    this.searchVal.nativeElement.value = "";
    this.selectedFilterIndex = 0;
  }

  applyFilter(event: any) {
    let filterValue = (event.target as HTMLInputElement).value;
    !isNaN(event.key)? filterValue = String(filterValue) : '';
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  announceSortChange(sortState: Sort) {
    if (sortState.direction) {
      this._liveAnnouncer.announce(`Sorted ${sortState.direction}ending`);
    } else {
      this._liveAnnouncer.announce("Sorting cleared");
    }
  }
  baseDenominationMaintenance(){
    this.downloadService.downloadFiles("baseDenominationMaintenance",this.userdata);
  }
  onFileChange(event) {
    console.log('event', event.path[0].files[0]);
    const file: File = event.path[0].files[0];
    const uploadData = new FormData();
    uploadData.append('screenType', 'baseDenomination');
    uploadData.append('file', file);
    this.apiService.uploadExelFile(uploadData).subscribe((response) => {
      console.log(response);
      this.getItems();
      this.snack.open(`File Uploaded Successfully`, "OK", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
  }


  deleteItem(row) {
    row.baseDenominationId = +row.baseDenominationId;
    row.denomination = +row.denomination;
    this.confirmService.confirm({ message: `Delete ${row.baseDenominationId}?` })
      .subscribe(res => {
        if (res) {
          this.loader.open();
          this.apiService.removeBaseDenominationById(row.baseDenominationId)
            .subscribe((res: any) => {
            }, (err: any) => {
              ''
              if (err.status == 200) {
                this.loader.close();
                this.getItems();
                this.snack.open('Member deleted!', 'OK', { duration: 4000 })

              }
            })
        }
      })
  }

  openPopUp(row){
    row.baseDenominationId = +row.baseDenominationId;
    row.denomination = +row.denomination;
    this.getDenomId(row);
    this.router.navigate([`/maintenance/main/baseDenomination/${row.baseDenominationId}`]);
  }
 

  getDenomId(row){
    this.loader.open();
    this.apiService.getDenomInfoById(row.baseDenominationId).subscribe((data : any)=> {
      this.fetchBaseDenomValue=data;
      
      if(this.fetchBaseDenomValue != null){
        localStorage.setItem('fetchBaseDenomValue',JSON.stringify(this.fetchBaseDenomValue));
        }
      });
      this.loader.close();  
  }

  addNew(){
    this.router.navigate(["/maintenance/main/baseDenomination/add"]);
  }

  goBack(){
    this.router.navigate(["/maintenance/main"]);
  }

  trackByFilter(filter){
    return filter.value;
  }
}
