import { Component, Input, OnInit } from '@angular/core';
import { DataService } from 'app/shared/services/data.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-audit-log',
  templateUrl: './audit-log.component.html',
  styleUrls: ['./audit-log.component.scss']
})
export class AuditLogComponent implements OnInit {

  clickEventsubscription: Subscription;

  constructor(private dataService: DataService) {
    this.dataService.getClickEvent().subscribe(() => {
      console.log("test");
      this.auditLogDataObject = this.dataService.getAuditLogData();
      if (this.auditLogDataObject.oneTimeAuth == "N") {
        this.auditLogDataObject.oneTimeAuth = "No"
      }
      else if (this.auditLogDataObject.oneTimeAuth == "Y") {
        this.auditLogDataObject.oneTimeAuth = "Yes"
      }
      if (this.auditLogDataObject.authStatus == "U") {
        this.auditLogDataObject.authStatus = "Unauthorized"
      }
      else if (this.auditLogDataObject.authStatus == "A") {
        this.auditLogDataObject.authStatus = "Authorized"
      }
      if (this.auditLogDataObject.recordStatus == "O") {
        this.auditLogDataObject.recordStatus = "Open"
      }
      else if (this.auditLogDataObject.recordStatus == "C") {
        this.auditLogDataObject.recordStatus = "Close"
      }
    })
  }
  auditLogDataObject: any = {};

  ngOnInit(): void {
    this.auditLogDataObject = this.dataService.getAuditLogData();
    console.log(this.auditLogDataObject);
    if (this.auditLogDataObject.oneTimeAuth == "N") {
      this.auditLogDataObject.oneTimeAuth = "No"
    }
    else if (this.auditLogDataObject.oneTimeAuth == "Y") {
      this.auditLogDataObject.oneTimeAuth = "Yes"
    }
    if (this.auditLogDataObject.authStatus == "U") {
      this.auditLogDataObject.authStatus = "Unauthorized"
    }
    else if (this.auditLogDataObject.authStatus == "A") {
      this.auditLogDataObject.authStatus = "Authorized"
    }
    if (this.auditLogDataObject.recordStatus == "O") {
      this.auditLogDataObject.recordStatus = "Open"
    }
    else if (this.auditLogDataObject.recordStatus == "C") {
      this.auditLogDataObject.recordStatus = "Close"
    }
  }

}
