import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { ApiService } from 'app/shared/services/api.service';

@Component({
  selector: 'app-transaction-type-popup-table',
  templateUrl: './transaction-type-popup-table.component.html',
  styleUrls: ['./transaction-type-popup-table.component.scss']
})
export class TransactionTypePopupTableComponent implements OnInit {
  public itemForm: FormGroup;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<TransactionTypePopupTableComponent>,
    private fb: FormBuilder,private api: ApiService,
  ) {
    
   }

 

  ngOnInit() {
    this.buildItemForm(this.data.payload)
    let payload = {
    };
  }
  buildItemForm(item) {
    this.itemForm = this.fb.group({
      transactionTypeCode: [item.transactionTypeCode || '', Validators.required],
      transactionTypeName: [item.transactionTypeName || '', Validators.required],
      transactionTypeDescription: [item.transactionTypeDescription || '' ,Validators.required],
      transactionTypeId: [item.transactionTypeId || null]
    })
  }

  submit() {
    this.dialogRef.close(this.itemForm.value)
  }

}
