import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router, ActivatedRoute } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { MenuDialogComponent } from '../menu-dialog/menu-dialog.component';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-menu-details',
  templateUrl: './menu-details.component.html',
  styleUrls: ['./menu-details.component.scss']
})
export class MenuDetailsComponent implements OnInit {
  currentUser: any;
  menuForm: FormGroup;
  navItem: any;
  auditLogData: any;
  parentMenus: any = [];

  loggedInUser: any;
  id: any;
  readorWrite: boolean=true;
  isEdit: boolean = false;
  isAuditData: boolean = false;
  parentId: any;

  constructor(private apiService: ApiService,
    private fb: FormBuilder,
    private dialogue: MatDialog,
    private dataService: DataService,
    private ls: LocalStoreService,
    private loader: AppLoaderService,
    private snack: MatSnackBar,
    private router: Router,
    private route: ActivatedRoute,) { }

  ngOnInit(): void {
    this.currentUser = this.ls.getItem("ICUST_USER");
    this.loggedInUser = this.ls.getItem('ICUST_USER');
    this.getParentMenu();

    this.dataService.currentNav.subscribe((data: any) => {
      if (!this.isEmpty(data)) {
        this.navItem = data;
        this.id = data.id;
        this.auditLogData = data;
        this.isEdit = true;
        this.isAuditData = true;
        this.buildMenuForm(this.navItem);
        this.dataService.setAuditLogData(this.auditLogData);
      } else {
        this.readorWrite=false;
        this.goBack();
      }
    });
    this.route.queryParamMap.subscribe((params: any) => {
      this.parentId=params.get('id');
      if(this.parentId){
        this.apiService.editMenuMaintenance(this.parentId).subscribe((resp)=>{
          console.log(resp);
          })
      }
    
      console.log("param",params)
     
    })
  }

  buildMenuForm(item: any) {
    this.menuForm = this.fb.group({
      id: [item.id || null],
      screenCode: [item.screenCode || ""],
      screenName: [item.screenName || ""],
      parentId: [item.parentId || ""],
      routing: [item.routing || ""],
      status: true,
    });
  }

  isEmpty(obj) {
    for (var prop in obj) {
      if (Object.prototype.hasOwnProperty.call(obj, prop)) {
        return false;
      }
    }

    return JSON.stringify(obj) === JSON.stringify({});
  }

  getParentMenu() {
    this.apiService.getMaintenanceParentMenu().subscribe((res: any) => {
      this.parentMenus = res;
      console.log(this.parentMenus)
    }, (err: any) => {

    })
  }

  onSubmit() {
    if (this.menuForm.invalid) {
      return;
    }
    console.log(this.menuForm.value);

    let payload: any = {};
    payload = this.menuForm.value;
    payload.loggedInUser = this.loggedInUser.userName;
    this.loader.open();
    this.apiService.upsertMenu(payload).subscribe(res => {
      this.loader.close();
      this.goBack();
    }, err => {
      this.loader.close();
    })

  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteMenu(this.auditLogData.id)
        .subscribe(data => {
          this.router.navigate([`/maintenance/main/tillVaultStatus`]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
          this.router.navigate([`/maintenance/main/tillVaultStatus`]);
        
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
    payload.id = this.id;
    payload.operation = operation;
    payload.loggedInUser = this.loggedInUser.userName;
    this.apiService.getAuditLogsForMenu(payload).subscribe((data) => {
      console.log(data);
      this.auditLogData = data;
      this.dataService.setAuditLogData(this.auditLogData);
      this.isAuditData = true;
      this.dataService.sendClickEvent();

      let message = '';
      switch (operation) {
        case 'open':
          message = 'opened'
          break;
        case 'close':
          message = 'closed'
          break;
        case 'auth':
          message = 'authorized'
          break;
        default:
          break;
      }
      this.snack.open(`Record ${message} successfully!'`, "OK", {
        duration: 2000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
      }}))
    
  }

  openDialogue(data: any = {},isEdit : string) {
    this.dataService.setchildMenuDetails(data);
    this.router.navigate(['/maintenance/main/addEditMenu/menuDialog'],{ queryParams: { id: data.id, parenId : this.id,isEdit:true } });
  }

  goBack() {
    this.router.navigate([`/maintenance/main/menu-config`]);
  }

}
