import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AuditLogComponent } from '../../audit-log/audit-log.component';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-add-currency-pair-maintanence',
  templateUrl: './add-currency-pair-maintanence.component.html',
  styleUrls: ['./add-currency-pair-maintanence.component.scss']
})
export class AddCurrencyPairMaintanenceComponent implements OnInit {
  @ViewChild('auditLog') auditLog : AuditLogComponent
  public branchForm: FormGroup;
  // entity: string[] = [];
  // bankCode: string[] = [];
  currencies:any = [];
  data:any;
  entityCode: any[] = [];
  bankCode: any[] = [];
  entity: any;
  loggedInUser : any;
  isReadOnly: boolean = true;
  isEdit: boolean = false;
  isView: boolean = true;
  show = false;
  editNow : boolean = false;
  entityIsEdit: any;
  id: any;
  auditData : any;
  isAuditData : boolean = false;
    entityCodeArr: any[] = [];
    readorWrite: boolean=true;

  constructor(private router: Router, private fb: FormBuilder, private apiService: ApiService,
    private snack: MatSnackBar, private cdr: ChangeDetectorRef, private ls:LocalStoreService,private route : ActivatedRoute,private dataService: DataService) { }

  ngOnInit(): void {
    this.loggedInUser = this.ls.getItem('ICUST_USER');
    console.log(this.loggedInUser);
    this.getEntityDropdown();
    this.buildItemForm({});
    this.getBranchCurrency();
    this.getBankCode();
   
    this.route.queryParamMap.subscribe((params: any) => {
      console.log(params.get('id'));
      this.entityIsEdit = params.get('isEdit');
      console.log("this.entityIsEdit",this.entityIsEdit);
      if(params.get('isEdit') == 'Yes'){
         this.isReadOnly = false;
       this.isView= true;
       this.editNow = true;
      }
      else if(params.get('isEdit') == 'No'){
         this.isReadOnly = true;
         this.isView= true;
        this.editNow = false;
      }
      else if(params.get('isEdit') == 'false'){
        this.isReadOnly = false;
        this.isView= false;
        this.editNow = false;
      }
      else if(params.get('isEdit') == null){
      this.isView= false;
      }
      if(params.get('id')){
        this.isEdit = true;
        this.id = params.get('id');
        this.apiService.editEntityById(params.get('id')).subscribe((resp) =>{
           console.log(resp);
           this.buildItemForm(resp);
        });
          this.apiService.getCurrencyPairById(params.get('id')).subscribe((resp) =>{
          console.log(resp);
          this.buildItemForm(resp);
          this.auditData = resp;
          this.isAuditData = true;
          this.dataService.setAuditLogData(this.auditData);
       });
      }
      else{
        this.readorWrite=false;
        this.buildItemForm({});
      }
    }
    ); 
  }

  buildItemForm(item) {
    console.log("selected item 2::",item)
    //this.getEntityDropdown();
 
    this.branchForm = this.fb.group({
      entityCode: [item.entityCode || '', Validators.required], 
      bankCode: [item.bankCode || '', Validators.required],
      currency1: [item.currency1 || '', Validators.required],  
      currency2: [item.currency2 || '', Validators.required],
      throughCurrency: [item.throughCurrency || '', Validators.required],
      rateDirect: [item.rateDirect || '', Validators.required],
      spreadAdder: [item.spreadAdder || '',Validators.required],
      id: [item.id || ''],
      authStatus: [item.authStatus || ''],
      isActive:[item.isActive || true],
      oneTimeAuth:[item.oneTimeAuth || ''],
      recordStatus:[item.recordStatus || ''],
      version:[item.version || ''],
      loggedInUser:[item.loggedInUser || ''],

    });
    

  }

  // getEntityCode() {
  //   this.apiService.getEntityCode().subscribe((resp) => {
  //     console.log("bankCodeData", resp)
  //     this.entity = resp;
  //   });
  // }

  getBankCode() {
    this.apiService.getBankCode().subscribe((resp) => {
     // this.bankCode = resp;
      for(let i=0;i<resp.length;i++){
        if(resp[i].authStatus == 'A'){
          this.bankCode.push(resp[i]);
        }
      }
    });
  }

  getEntityDropdown(){
    this.apiService.getEntityDropdown().subscribe((data) => {
      console.log(data);
      for(let i=0; i<data.length;i++){
        if(data[i].authStatus==='A'){
          this.entityCode.push(data[i]);
          console.log('auth',i)
        }
      }

      // this.entityCode = entityCodeResp;
    });   
  }

  getBranchCurrency(){
    // this.apiService.getCountryValues().subscribe(_list =>{
    //   this.currencies = _list;
    // },(err:any) =>{
    //   console.error("Error", err);
    // })
    this.apiService.getCurrencyDetails().subscribe(data=>{
      console.log("Auth currecy details", data)
      for(let i=0; i<data.length;i++){
        this.currencies.push(data[i].currencyId)
      }
      console.log(this.currencies);
    })
  }

    
  submit() {
    console.log("Form:",this.branchForm.value);
    if(this.branchForm.invalid){
    return;
    }else{
    console.log("payload:",this.branchForm.value);
    this.branchForm.value.loggedInUser = this.loggedInUser.userName;
    this.apiService.saveCurrencyPair(this.branchForm.value).subscribe(data => {
      console.log("data",data);
      if(data != null && this.isEdit == true){
        this.router.navigate([`/maintenance/main/currencyPairMaintenance`]).then(_=>{
                this.snack.open('Data Updated!', 'OK', 
                { duration: 4000 , 
                  verticalPosition: "top",
                  horizontalPosition: "right",});
                });       
        this.cdr.markForCheck();
      }
      else if(data != null || this.isEdit == true){
       // 
        console.log("data",data);
        this.router.navigate([`maintenance/main/currencyPairMaintenance`]).then(_=>{
          this.snack.open('Data Saved!', 'OK', 
          { duration: 4000 , 
            verticalPosition: "top",
            horizontalPosition: "right",});
          });
        this.cdr.markForCheck();
       }
    }) 
    }  
  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteCurrencyPair(this.auditData.id)
        .subscribe(data => {
          this.router.navigate(["/maintenance/main/currencyPairMaintenance"]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
        
        
      })
      }}))
   
         
       
  }
editRecord(){
this.readorWrite=false;
}

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        console.log(this.id);
        let payload: any = {};
        payload.id = this.id;
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser.userName;
        this.apiService.getAuditLogsForCurrencyPair(payload).subscribe((data) => {
          console.log(data);
          this.auditData = data;
          this.dataService.setAuditLogData(this.auditData);
          this.isAuditData = true;
          this.dataService.sendClickEvent();
          let message = '';
          switch (operation) {
            case 'open':
              message = 'opened'
              break;
            case 'close':
              message = 'closed'
              break;
            case 'auth':
              message = 'authorized'
              break;
            default:
              break;
          }
          this.snack.open(`Record ${message} successfully!'`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        });
      }}))
   
  }

  goBack(){
    this.router.navigate(["/maintenance/main/currencyPairMaintenance"]);
  }
  


}
