import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { Subscription } from 'rxjs';
import { Router } from '@angular/router';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import {MatSort, Sort} from '@angular/material/sort';
import { LiveAnnouncer } from '@angular/cdk/a11y';
import { DownloadService } from 'app/shared/services/download.service';

@Component({
  selector: 'app-customer-charge-category',
  templateUrl: './customer-charge-category.component.html',
  styleUrls: ['./customer-charge-category.component.scss']
})
export class CustomerChargeCategoryComponent implements OnInit {

  @ViewChild("searchVal") searchVal: ElementRef;
  selectedFilterIndex: number = 0;

  dataSource = new MatTableDataSource();
  displayedColumns: string[] = [
    "chargeCategory",
    "categoryDescription",
    "oneTimeAuth",
    "authStatus",
    "recordStatus",
    "action",
  ];

  filterByOption: { key: string; value: string }[] = [
    { key: "chargeCategory", value: "Charge Category" },
  ];

  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  public users: any[];
  public items: any[];
  entity: any;
  bankCode: any;
  cutomerCategoryMasterValues: any;
  public getItemSub: Subscription;
  fetchcutomerCategory: any;
  id: any;

  constructor(
    private dialog: MatDialog,
    private snack: MatSnackBar,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,
    private api: ApiService,
    private router: Router,
    private dataService: DataService,
    private _liveAnnouncer: LiveAnnouncer,
    private ls: LocalStoreService,
    private downloadService: DownloadService
  ) {}

  ngOnInit() {
    this.getEntityCode();
    this.getBankCode();
    this.getItems();
  }
  ngOnDestroy() {
    if (this.getItemSub) {
      this.getItemSub.unsubscribe();
    }
  }
  addNew() {
    this.router.navigate([`/maintenance/main/customerChargeCategoryCreate`]);
  }

  trackByFnFilterByOption(index, item) {
    return index;
  }
  onFileChange(event) {
    console.log("event", event.path[0].files[0]);
    const file: File = event.path[0].files[0];
    const uploadData = new FormData();
    uploadData.append("screenType", "cutomerCategoryltMaster");
    uploadData.append("file", file);
    this.api.uploadExelFile(uploadData).subscribe((response) => {
      console.log(response);
      this.getItems();
      this.snack.open(`File Uploaded Successfully`, "OK", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
  }

  getItems() {
    this.api.getCustomerChargeCategory().subscribe((resp: any) => {
      if (resp) {
        console.log(resp);
        for (let i = 0; i < resp.length; i++) {
          if (resp[i].authStatus == "U") {
            resp[i].authStatus = "Unauthorized";
          }
          if (resp[i].authStatus == "A") {
            resp[i].authStatus = "Authorized";
          }
          if (resp[i].recordStatus == "O") {
            resp[i].recordStatus = "Open";
          }
          if (resp[i].recordStatus == "C") {
            resp[i].recordStatus = "Closed";
          }
        }
        this.cutomerCategoryMasterValues = resp;
        this.items = this.cutomerCategoryMasterValues;
        this.dataSource = new MatTableDataSource(resp);
        this.users = resp;
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      }
    });
  }
  getBankCode() {
    this.api.getBankCode().subscribe((bnkCode: any) => {
      this.bankCode = bnkCode;
    });
  }
  getEntityCode() {
    this.api.getEntityCode().subscribe((resp) => {
      this.entity = resp;
    });
  }
  doFilter(filterBy, e) {
    this.filterTable(filterBy);
    this.applyFilter(e);
  }

  filterTable(filterBy) {
    this.dataSource.filterPredicate = (data: any, filter: string): boolean => {
      return data[filterBy]?.toLocaleLowerCase().includes(filter);
    };
  }

  clearFilter() {
    this.dataSource.filter = "";
    this.searchVal.nativeElement.value = "";
    this.selectedFilterIndex = 0;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  announceSortChange(sortState: Sort) {
    if (sortState.direction) {
      this._liveAnnouncer.announce(`Sorted ${sortState.direction}ending`);
    } else {
      this._liveAnnouncer.announce("Sorting cleared");
    }
  }
  openPopUp(data: any = {}, isEdit: string) {
    this.router.navigate(["/maintenance/main/customerChargeCategoryCreate"], {
      queryParams: { id: data.id, isEdit: isEdit },
    });
  }

  masterpdf() {
    this.downloadService.downloadFiles(
      "cutomerChargeCategoryMaintenance",
      this.users
    );
  }

  fetchcutomerCategoryById() {
    this.api.getCustomerChargeCategoryById(this.id).subscribe((resp) => {
      this.fetchcutomerCategory = resp;
    });
    this.dataService.sendNavParam(this.fetchcutomerCategory);
    this.router.navigate([
      `/maintenance/main/cutomerCategoryltMaster/add/${this.id}`,
    ]);
  }

  goBack() {
    this.router.navigate([`/maintenance/main/`]);
  }

  deleteItem(row) {
    this.confirmService
      .confirm({ message: `Delete ${row.chargeCategory}?` })
      .subscribe((res) => {
        if (res) {
          this.loader.open();
          this.api.deleteCustomerChargeCategory(row.id).subscribe(
            (res: any) => {},
            (err: any) => {
              ("");
              if (err.status == 200) {
                this.loader.close();
                this.getItems();
                this.snack.open("Member deleted!", "OK", { duration: 4000 });
              }
            }
          );
        }
      });
  }

}
