import { Component, ElementRef, OnInit} from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { MatTableDataSource } from '@angular/material/table';
import { ViewChild } from '@angular/core';
import {MatSort, Sort} from '@angular/material/sort';
import {LiveAnnouncer} from '@angular/cdk/a11y';
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { Subscription } from 'rxjs';

export interface PeriodicElement {
  name: string;
  CountryCode: string;
  CountryCode2: string;
  RegionCode: string;
  IBAN : String;
}


@Component({
  selector: 'app-state-maintenance',
  templateUrl: './state-maintenance.component.html',
  styleUrls: ['./state-maintenance.component.scss']
})
export class StateMaintenanceComponent implements OnInit {

  @ViewChild('searchVal') searchVal: ElementRef;
  selectedFilterIndex : number = 0;

  dataSource = new MatTableDataSource();
  displayedColumns: string[] = ['stateCode', 'stateName', 'stateCode2', 'stateNumCode','recordStatus', 'action'];

  filterByOption: { key: string, value: string }[] = [
    { key: 'stateCode', value: 'State Code' },
    { key: 'stateName', value: 'State Name' },
    { key: 'stateNumCode', value: 'State Numric Code' },
    { key: 'recordStatus', value: 'Record Status' },
  ];

  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;


  constructor(private router:Router,
    private snack: MatSnackBar,
    private loader: AppLoaderService,
      private confirmService: AppConfirmService,
      private apiService:ApiService,
      private _liveAnnouncer: LiveAnnouncer) { }

  demoData : PeriodicElement[] = [];

  ngOnInit(): void {
    this.getAllState();
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }


  getAllState() {
    this.apiService.getAllState().subscribe((data: any) => {
     
      this.dataSource = new MatTableDataSource(data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    });
  }

  onFileChange(event) {
    console.log('event', event.path[0].files[0]);
    const file: File = event.path[0].files[0];
    const uploadData = new FormData();
    uploadData.append('screenType', 'stateMaint');
    uploadData.append('file', file);
    this.apiService.uploadExelFile(uploadData).subscribe((response) => {
      console.log(response);
      this.getAllState();
      this.snack.open(`File Uploaded Successfully`, "OK", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
     this.getAllState();
  }

  openPopUp(data: any = {}, isNew?) {
    this.router.navigate(["/maintenance/main/statepopup"],{ queryParams: { id: data.id } });
  }

  goBack(){
    this.router.navigate([`/maintenance/main`]);
  }
  doFilter(filterBy, e){
    
    this.filterTable(filterBy);
    this.applyFilter(e);
  }

  filterTable(filterBy) {
    this.dataSource.filterPredicate = (data: any, filter: string): boolean => {
      return (
        data[filterBy].toLocaleLowerCase().includes(filter)
      )
    }
  }

  clearFilter(){
    this.dataSource.filter = '';
    this.searchVal.nativeElement.value = '';
    this.selectedFilterIndex = 0;
   }
   
  deleteItem(row) {
    this.confirmService.confirm({message: `Delete ${row.stateName}?`})
      .subscribe(res => {
        if (res) {
          this.loader.open();
          this.apiService.deleteState(row.id)
            .subscribe(data => {
              
            },    err=>{
              if (err.status == 200) {
                this.loader.close();
                this.snack.open('Record Deleted Successfully', 'OK', {
                  duration: 4000,
                  verticalPosition: 'top',
                  horizontalPosition: 'right'
                });
              }
    
            
            this.getAllState();
            
          })
        }
      })
  }

   

   applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  announceSortChange(sortState: Sort) {
    if (sortState.direction) {
      this._liveAnnouncer.announce(`Sorted ${sortState.direction}ending`);
    } else {
      this._liveAnnouncer.announce('Sorting cleared');
    }
  }

  addNew(){
    this.router.navigate(["/maintenance/main/statepopup"]);
  }
  dataByFilter(index,filter){
    return filter.value;
  }
}
