import { Component, Inject, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Router } from "@angular/router";
import { tillVaultBranchMap } from "app/shared/models/tillVaultBranchMap";
import { ApiService } from "app/shared/services/api.service";
import { AppLoaderService } from "app/shared/services/app-loader/app-loader.service";
import { DataService } from "app/shared/services/data.service";
import { LocalStoreService } from "app/shared/services/local-store.service";
import Swal from 'sweetalert2';

@Component({
  selector: "app-till-vault-branch-map",
  templateUrl: "./till-vault-branch-map.component.html",
  styleUrls: ["./till-vault-branch-map.component.scss"],
})
export class TillVaultBranchMapComponent implements OnInit {
  public tillVaultBranchMapForm: FormGroup;
  till: any[] = [];
  vault: any[] = [];
  branchCode: string[] = [];
  bankCode: string[] = [];
  id: number;
  vaultItem:tillVaultBranchMap;
  entityCode: string[] = [];
  authEntityCode:string[]= [];
  tillData: tillVaultBranchMap;
  vaultData: tillVaultBranchMap;
  loggedInUser : string;
  auditData : tillVaultBranchMap;
  isAuditData : boolean = false;
  isEdit: boolean = false;
  authStatus:any;
  entityCodeArr: any[] = [];
  readorWrite: boolean=true;

  constructor(
    // @Inject(MAT_DIALOG_DATA) public data: any,
    // public dialogRef: MatDialogRef<TillVaultBranchMapComponent>,
    private fb: FormBuilder,
    private apiService: ApiService,
    private snack: MatSnackBar,
    private router:Router,
    private dataService:DataService,
    private ls:LocalStoreService,
    private loader: AppLoaderService,
  ) {}

  ngOnInit() {
    // this.dataService.getTillVaultMap.subscribe(data =>{
    //   console.log(data)
    //   this.vaultItem = typeof this.vaultItem === 'object' ? data : {};
    // });
    
    setTimeout(() => {  
      this.vaultItem = JSON.parse(localStorage.getItem('fetchBrnMap'));
      if(this.vaultItem == null || this.vaultItem == undefined){
        this.buildItemForm({})
        this.readorWrite=false;
      }       
      else if(this.vaultItem.id != null || this.vaultItem.id != undefined){
      this.loader.open();
      this.id = this.vaultItem.id
      this.buildItemForm(this.vaultItem)
      this.auditData = this.vaultItem;
      this.isEdit = true;
          this.isAuditData = true;
          this.dataService.setAuditLogData(this.auditData);
      this.loader.close();
      }
    }, 1000)

    this.getBankCode();
    this.getBranchCode();
    this.getTillId();
    this.getParentVault();
    this.getEntityCode();

    this.loggedInUser = this.ls.getItem('ICUST_USER').userName;
    
  }

  // fetchTellVaultBranchMap(){
  //   this.apiService.fetchTellerVaultBranchMap(id).subscribe(result=>{
  //       console.log(result);
  //       this.buildItemForm(result);
  //     })
  // }

  buildItemForm(item) {
    this.tillVaultBranchMapForm = this.fb.group({
      bankCode: [item?.bankCode || "", Validators.required],
      branchCode: [item?.branchCode || "", Validators.required],
      entityCode: [item?.entityCode || "",[Validators.required]],
      tillId: [item?.tillId || "",Validators.required],
      id: [item.id ? item.id : null],
      locked: [item.locked === "0" ? true : false || "", false],
      parentVaultId: [item?.parentVaultId || "", Validators.required],
      shortOvgGl: [item?.shortOvgGl || "", Validators.required],
      loggedInuser : [item.loggedInUser || '']
     
    });
  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteTellerVaultBrnMap(this.auditData.id)
        .subscribe(data => {   
    this.router.navigate([`/maintenance/main/tillVaultltBranch`]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
         
    this.router.navigate([`/maintenance/main/tillVaultltBranch`]);
        
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }


  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
        payload.id = this.id;
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser;
        this.apiService.getAuditLogsFortillvaulmap(payload).subscribe((data) => {
          this.auditData = data;
          this.dataService.setAuditLogData(this.auditData);
          this.isAuditData = true;
          this.dataService.sendClickEvent();
          let message = '';
          switch (operation) {
            case 'open':
              message = 'opened'
              break;
            case 'close':
              message = 'closed'
              break;
            case 'auth':
              message = 'authorized'
              break;
            default:
              break;
          }
          this.snack.open(`Record ${message} successfully!'`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        });
      }}))
    
  }

  submit() {
    if(this.tillVaultBranchMapForm.invalid){
      return;
    }else{
      this.loader.open();
      this.tillData = this.till.find(x => x.tillVaultCode == this.tillVaultBranchMapForm.value.tillId);
      this.tillVaultBranchMapForm.value.tillId = this.tillData.id;
      this.vaultData = this.vault.find(x => x.tillVaultCode == this.tillVaultBranchMapForm.value.parentVaultId);
      this.tillVaultBranchMapForm.value.parentVaultId = this.vaultData.id;
      this.tillVaultBranchMapForm.value.loggedInUser = this.loggedInUser;
      this.apiService.saveTellerVaultBranchMap(this.tillVaultBranchMapForm.value).subscribe((resp) => {
      this.loader.close();
      this.router.navigate([`/maintenance/main/tillVaultltBranch`]).then(_=>{
          this.snack.open('Data Saved Successfully!', 'OK', 
          { duration: 4000 , 
            verticalPosition: "top",
            horizontalPosition: "right",});
        });
      },(err:any) =>{
        this.loader.close();
        this.snack.open(`Error!`, 'OK', { duration: 4000,  
          verticalPosition: "top",
          horizontalPosition: "right",});
        }
      )
      }  

      // this.tillVaultBranchMapForm.value.locked === true
      //   ? (this.tillVaultBranchMapForm.value.locked = "0")
      //   : (this.tillVaultBranchMapForm.value.locked = "1");
      // this.dialogRef.close(this.tillVaultBranchMapForm.value);
   
  }

  alhanumericValidation(event){
    var inp = String.fromCharCode(event.keyCode);
    if (/[a-zA-Z0-9]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }

  getEntityCode() {
    this.apiService.getEntityCode().subscribe((resp) => {
     //this.entityCode = resp;
 
      for(let i=0; i<resp.length; i++){
         if(resp[i].authStatus === 'A') {
          this.entityCode.push(resp[i]);
        } 
      }
    });
  }
  getBankCode() {
    this.apiService.getBankCode().subscribe((resp) => {
   // this.bankCode = resp;
    
      for(let i=0;i<resp.length;i++){
        if(resp[i].authStatus==='A'){
          this.bankCode.push(resp[i]);
        }
      }
    });
  }
  getBranchCode() {
    this.apiService.getBranchCode().subscribe((resp) => {
    //  this.branchCode = resp;
      for(let i=0; i<resp.length;i++){
        if(resp[i].authStatus==='A'){
          this.branchCode.push(resp[i]);
        }
      }
    });
  }
  getTillId() {
    let tillOrVault ="T";
    this.apiService.getTillData(tillOrVault).subscribe((resp) => {
      this.till = resp;
    });
  }

  getParentVault(){
    let tillOrVault ="V";
    this.apiService.getTillData(tillOrVault).subscribe((resp) => {
      this.vault = resp;
    }); 
  }
  goBack() {
    localStorage.removeItem('fetchBrnMap');

    this.router.navigate([`/maintenance/main/tillVaultltBranch`]);
  }
  dataByEntityCode(index,item){
   // console.log("item",item)
    // if(item.authStatus == 'A' || item.authStatus == 'Authorized'){
      return item.entityCode;
    // }
   
  }
  dataByBankCode(index,item){
    return item.bankCode;
  }
  dataByBranchCode(index,item){
    return item.branchCode;
  }
  dataByTillId(index,item){
    return item.tillVaultCode;
  }
  dataParentVaultId(index,item){
    return item.tillVaultCode;
  }
  
  
  
}
