  buildCalenderArr(calendar, month, year, colIdx, colChecked, rowChecked, rowIdx,i1,i2) {
    let date = new Date(month + '1, ' + year);
    this.disableSave=false;
    for (let index = 0; index < calendar.length; index++) {
      const element = calendar[index];
      if (element[colIdx].value != '' && element[colIdx].value != ' ') {
        let dateValue = (+element[colIdx].value);
        var d = new Date(year, date.getMonth(), dateValue);
        var responseDate = moment(d).format('YYYY-MM-DD');
        if (colChecked && rowChecked) {
          if (index == rowIdx) {
            this.calendarDate.push(responseDate);
          }
        } else if (colChecked && !rowChecked) {
          this.calendarArrFinalNew[i1][i2][index][colIdx].disabled=true; 
          this.cdr.detectChanges();
          this.calendarDate.push(responseDate);
        } else {
          this.calendarArrFinalNew[i1][i2][index][colIdx].disabled=false;                  
          this.cdr.detectChanges();
          this.calendarDate.splice(this.calendarDate.indexOf(responseDate), 1);
        }

      }
    }
    console.log(this.calendarDate);
  }
  showOptionsRow(event: MatCheckboxChange, i1, i2, row,year) {
    this.rowChecks[i1][i2][row].value = event.checked;
    const index = this.checks[i1][i2].findIndex(x => x.value == true);
    if (index != -1) {
      for (let colIdx = 0; colIdx < this.checks.length; colIdx++) {
        const element = this.checks[colIdx];
      }
    } else {
      this.snack.open('Select Column To Add Date', 'OK');
    }

  }

  onDateSelection(date,i1, i2, year){
    console.log(date,i1, i2, year);
    let month = this.monthArr[i1][i2];
    let dateSample = new Date(month + '1, ' + year);
    var d = new Date(year, dateSample.getMonth(), date);
    var responseDate = moment(d).format('YYYY-MM-DD');
    if(this.calendarDate.includes(responseDate)){
      var index = this.calendarDate.indexOf(responseDate);
      if (index !== -1) {
        this.calendarDate.splice(index, 1);
      }
    }
    else{
      this.calendarDate.push(responseDate);
    }
    if(this.calendarDate.length > 0){
      this.disableSave = false;
    }
    console.log(this.calendarDate);
  }

  checkDateAlreadySelected(date,i1, i2, year) : boolean{
    let month = this.monthArr[i1][i2];
    let dateSample = new Date(month + '1, ' + year);
    var d = new Date(year, dateSample.getMonth(), date);
    var responseDate = moment(d).format('YYYY-MM-DD');
    if(this.calendarDate.includes(responseDate)){
       return true;
    }
    else{
      return false;
    }
  }

  saveHoliday(year) {
    this.isEdit=false;
    let details: any[] = [];
    this.calendarDate.forEach((v, k) => {
      let payload = {
        entityCode: 'common',
        bankCode: 'common',
        branchCode: 'common',
        calenderYear: year,
        calenderDate: v,
        description:'common'
      }
      details.push(payload);
    });

    this.apiService.updateBranchHolidayDetails(details,this.isEdit)
    .subscribe(data => {
      if (data != null) {
        this.disableSave=true;
        this.snack.open('Data Saved' + ' !', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        // this._location.back();
        this.cdr.markForCheck();
      }

    },error=>{
      this.disableSave=true;
      console.log('error :: ',error);
      this.snack.open(`${error.error}` + ' !', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right',
        panelClass: 'snackbar-error'
      });
    
    });


  }

