import { Component, OnInit, Inject, ChangeDetectorRef, ViewChild } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { ApiService } from 'app/shared/services/api.service';
import { Location } from '@angular/common';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ActivatedRoute } from '@angular/router';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { DataService } from 'app/shared/services/data.service';
import { AuditLogComponent } from '../../audit-log/audit-log.component';
import { ngxDetails } from 'app/shared/models/ngxtable';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-ngx-table-popup',
  templateUrl: './ngx-table-popup.component.html'
})
export class NgxTablePopupComponent implements OnInit {

  // @ViewChild('auditLog') auditLog : AuditLogComponent


  public branchForm: FormGroup;
  loggedInUser : string;
  auditData : ngxDetails;
  banks: string[]=[];
  currencies: string[] = [];
  countries: string[] = [];
  states: string[] = [];
  isAuditData : boolean = false;
  cities: string[] = [];
  isEdit: boolean = false;
  isReadOnly: boolean = true;
  id: number;
  show = false;
  bankCode: string[] = [];
  entityCode:  string[] = [];
  readorWrite: boolean = true;
  currentUser: any;
  bankDetails: any;
  constructor(
    private route : ActivatedRoute,
    private fb: FormBuilder,
    private apiService: ApiService,
    private _location: Location,
    private ls : LocalStoreService,
    private router:Router,
    private cdr: ChangeDetectorRef,
    private snack: MatSnackBar,
    private dataService : DataService
  ) { }

  ngOnInit() {
    this.currentUser = this.ls.getItem("ICUST_USER");
    this.route.queryParamMap.subscribe((params: any) => {
     
      if(params.get('id')){
        this.isEdit = true;
        this.id = params.get('id');
        this.apiService.getBranchById(params.get('id')).subscribe((resp) =>{
        
           this.buildItemForm(resp);           
           this.auditData = resp;
           this.isAuditData = true;
           this.dataService.setAuditLogData(this.auditData);
         
         
        });
      }
      else{
        this.readorWrite=false;
        this.buildItemForm({});
      }
    }
    );  
    this.getEntityCode();
    this.getAuthBankDetails();
    this.getAllBanks();
    this.getBranchCurrency();
    this.getCountryName();
    this.loggedInUser = this.ls.getItem('ICUST_USER').userName;

    
    this.buildItemForm({});

  }
  buildItemForm(item) {
    
    this.branchForm = this.fb.group({
      bankCode: [item.bankCode || '', Validators.required], //dropdown
      entityCode: [item.entityCode || '', Validators.required],
      branchCode: [item.branchCode || '', Validators.required],
      branchCcy: [item.branchCcy || '', Validators.required],  //dropdown
      branchDetails: [item.branchDetails || '', Validators.required],
      branchName: [item.branchName || '', Validators.required],
      address1: [item.address1 || '', Validators.required],
      address2: [item.address2 || ''],
      country: [item.country || '', Validators.required],
      state: [item.state || '', Validators.required],
      city: [item.city || '', Validators.required],
      pincode: [item.pincode || '', Validators.required],
      id: [item.id || ''],
    });

    if (item.country) {
      this.getStateName(item.country);
      this.getCityName(item.state);
      
    }
    if (item.entityCode) {
      this.getBankCode1(item.entityCode);
    }
  }



  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }

  


  getAllBanks() {
    this.apiService.getBankCode().subscribe(_list => {
      this.banks = _list;
    }, (err: any) => {
      
    })
  }

  Onlynumber(event): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }
  keyPressAlphanumeric(event) {
    var inp = String.fromCharCode(event.keyCode);
    if (/[0-9a-zA-Z]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }

 getBranchCurrency(){
    // this.apiService.getCountryValues().subscribe(_list =>{
    //   this.currencies = _list;
    // },(err:any) =>{
     
    // })
    this.apiService.getCurrencyDetails().subscribe(data=>{
      console.log("Auth currecy details", data)
      for(let i=0; i<data.length;i++){
        this.currencies.push(data[i])
      }
      console.log(this.currencies);
    })
  }
  getEntityCode() {
    this.apiService.getEntityCode().subscribe((resp) => {
      console.log(resp);
      for(let i=0; i<resp.length;i++){
        if(resp[i].authStatus==='A'){
          this.entityCode.push(resp[i]);
        }
      }
      console.log(this.entityCode);
      // this.entityCode = resp;
    });
  }

  getBankCode1(entityCode) {
    this.apiService.getBankInfoByEntityCode(entityCode).subscribe((resp) => {
      this.bankCode = resp;
      console.log(this.bankCode);
    });
  }
  getAuthBankDetails(){
    this.apiService.fetchAuthBankInfo().subscribe(resp => {
      this.bankDetails = resp
    })
    }
getEntityCodeByBankCode(bankCode){

  for (let index = 0; index < this.bankDetails.length; index++) {
    if(this.bankDetails[index].bankCode == bankCode){
      this.branchForm.get('entityCode').setValue(this.bankDetails[index].entityCode)
      return;
    }
    
  }
}
  // getBankCode(entityCode:any) {
  //   this.apiService.getBankInfoByEntityCode(entityCode).subscribe((resp) => {
  //     for(let i=0; i<resp.length;i++){
  //       if(resp[i]===entityCode){
  //         this.bankCode.push(resp[i]);
  //       }
  //     }
  //     console.log(this.bankCode);
  //   });
  // }


  getCountryName() {
    this.apiService.getAllCountries().subscribe((data: any) => {
      console.log('resp',data)
      for(let i=0; i<data.length;i++){
        if(data[i].authStatus==='A'){
          console.log(data[i])
          this.countries.push(data[i].countryName);
          
        }
      }console.log(this.countries)
    });
  }

  getStateName(countryName){
    this.apiService.getAllCountries().subscribe((data: any) => {
      for(let i=0; i<data.length;i++){       
        if(data[i].countryName === countryName){
          this.apiService.getStateByCountry(data[i].id).subscribe((resp: any) => {
          this.states = resp
          })
          break;   
        }
      }
    })
    }
    
  getCityName(state: any) {
    let entityName = "city";
    
    this.apiService.getDropDownValues1(entityName, state).subscribe((data) => {
      this.cities = data;
    });
  }

  submit() {
    // let details: any = {};
    // details = this.branchForm.value;
    // details.id = this.id;

    let loggedInUser = this.loggedInUser;
    // 
    this.apiService.updateBranchDetails(this.branchForm.value,loggedInUser).subscribe(data => {
     
      if(data != null){
        this.snack.open('Data Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.cdr.markForCheck();
      }
    })
    
    this.router.navigate([`/maintenance/main/branch`]);
  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteBranch(this.auditData.id,this.currentUser.userName)
        .subscribe(data => {
          this.router.navigate([`/maintenance/main/branch`]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
          this.router.navigate([`/maintenance/main/branch`]);
        
      })
      }}))
   
         
       
  }
  
 editRecord(){
 this.readorWrite=false;
 }

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
        payload.id = this.id;
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser;
        this.apiService.getAuditLogsForBranch(payload).subscribe((data) => {
       
          this.auditData = data;
          this.dataService.setAuditLogData(this.auditData);
          this.isAuditData = true;
          this.dataService.sendClickEvent();
          let message = '';
          switch (operation) {
            case 'open':
              message = 'opened'
              break;
            case 'close':
              message = 'closed'
              break;
            case 'auth':
              message = 'authorized'
              break;
            default:
              break;
          }
          this.snack.open(`Record ${message} successful!'`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        });
      }}))
   
  }

  goBack() {
    //this._location.back();
    this.router.navigate([`/maintenance/main/branch`]);
  }
  dataByBank(index,bank){
    return bank.bankCode;
  }
  dataByCurrency(index,currency){
    return currency.currencyCode;
  }
  dataByCountry(index,country){
    return country;
  }
  dataByState(index,state){
    return state.value1;
  }
  dataByCity(index,city){
    return city.value1;
  }

  // edit() {
  //  this.show = !this.show;
  // }
}
