import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ApiService } from 'app/shared/services/api.service';


@Component({
  selector: 'app-transaction-denomination-history',
  templateUrl: './transaction-denomination-history.component.html',
  styleUrls: ['./transaction-denomination-history.component.scss']
})
export class TransactionDenominationHistoryComponent implements OnInit {

  public itemForm: FormGroup;
  countryCurrency: any[]=[];
  tillId:string[]=[];
  branchCode:string[]=[];
  bankCode:string[]=[];
  DenominationID:number;
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<TransactionDenominationHistoryComponent>,
    private fb: FormBuilder,
    private apiService: ApiService
  ) { }

  ngOnInit() {
    this.DenominationID = this.data.payload? this.data.payload.id: null;
    this.buildItemForm(this.data.payload);
    this.countryCurrency=this.data.currency;
    this.tillId=this.data.till;
    this.branchCode=this.data.branchCode;
    this.bankCode=this.data.bankCode;
  }
  buildItemForm(item) {
    this.itemForm = this.fb.group({
      txtReference:  [item.txtReference || '', Validators.required],
      txtDate:  [new Date(item.txtDate) || '', Validators.required],
      denomCode: [item.denomCode || '', Validators.required],
      denomUnit: [item.denomUnit || '', Validators.required],
      
    })
  }
  
  submit() {
    let payloadOBJ : any ={}
    payloadOBJ = this.itemForm.value;
    payloadOBJ.id = this.DenominationID;
    this.dialogRef.close(this.itemForm.value)
    
    
  }

  alphakeyPress(event: any) {
    const pattern = /[0-9a-zA-Z]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

}
