import { LiveAnnouncer } from '@angular/cdk/a11y';
import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort, Sort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { DownloadService } from 'app/shared/services/download.service';

@Component({
  selector: 'app-currency-maint-summary',
  templateUrl: './currency-maint-summary.component.html',
  styleUrls: ['./currency-maint-summary.component.scss']
})
export class CurrencyMaintSummaryComponent implements OnInit {

  displayedColumns: string[] = ['currencyCode', 'currencyName', 'country','authStatus','recordStatus','action'];
  filterByOption: { key: string, value: string }[] = [
    { key: 'currencyCode', value: 'Currency Code' },
    { key: 'currencyName', value: 'Currency Name' }
  ];
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  getItemSub: any;
  items: any;
  public users: any[];
  dataSource = new MatTableDataSource();
  @ViewChild('searchVal') searchVal: ElementRef;
  selectedFilterIndex: number;

  constructor(
    private apiService: ApiService,
    private router:Router,
    private _liveAnnouncer: LiveAnnouncer,
    private confirmService: AppConfirmService,
    private snack: MatSnackBar,
    private downloadService: DownloadService
  ) { }

  ngOnInit() {
      this.fetchCurrencyMaintDetails();
  }
  onFileChange(event) {
    console.log('event', event.path[0].files[0]);
    const file: File = event.path[0].files[0];
    const uploadData = new FormData();
    uploadData.append('screenType', 'currencyMaint');
    uploadData.append('file', file);
    this.apiService.uploadExelFile(uploadData).subscribe((response) => {
      console.log(response);
      this.fetchCurrencyMaintDetails();
      this.snack.open(`File Uploaded Successfully`, "OK", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
  }
  fetchCurrencyMaintDetails() {
    this.getItemSub = this.apiService.fetchCurrencyMaintDetails()
      .subscribe(data => {
        console.log("data",data);
        for(let i=0;i<data.length;i++){
          if(data[i].authStatus == 'U'){
            data[i].authStatus = 'Unauthorized'
          }
          if(data[i].authStatus == 'A') {
            data[i].authStatus = 'Authorized'
          } 
          if(data[i].recordStatus == 'O'){
            data[i].recordStatus = 'Open'
          } 
          if(data[i].recordStatus == 'C'){
            data[i].recordStatus = 'Closed'
          }
        }
        this.dataSource = new MatTableDataSource(data);
        this.users = data;
        console.log(this.dataSource);
        this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      })
  }

  addNew() {
    this.router.navigate(["/maintenance/main/currencyMaint/add"], { queryParams: { currencyId: null, isEdit : "No" } });
  }
  announceSortChange(sortState: Sort) {
    if (sortState.direction) {
      this._liveAnnouncer.announce(`Sorted ${sortState.direction}ending`);
    } else {
      this._liveAnnouncer.announce('Sorting cleared');
    }
    
  }
  clearFilter(){
    this.dataSource.filter = '';
    this.searchVal.nativeElement.value = '';
    this.selectedFilterIndex = 0;
   }
   doFilter(filterBy, e){
    console.log(filterBy,e);
    this.filterTable(filterBy);
    this.applyFilter(e);
  }
  goBack() {
    this.router.navigate([`/maintenance/main`]);
  }
  openPopUp(data: any = {},isEdit : string) {
    console.log("data",JSON.stringify(data.currencyId));
    this.router.navigate([`/maintenance/main/currencyMaint/add`],{ queryParams: { currencyId: JSON.stringify(data.currencyId), isEdit : isEdit } });
  }

  applyFilter(e: any) {
    //throw new Error('Method not implemented.');
    const filterValue = (e.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  filterTable(filterBy: any) {
    //throw new Error('Method not implemented.');
    this.dataSource.filterPredicate = (filteredDdata: any, filter: string): boolean => {
      return (
        filteredDdata[filterBy]?.toLocaleLowerCase().includes(filter) || filteredDdata.currencyId[filterBy]?.toLocaleLowerCase().includes(filter)
      )
    }
  }
  currencyMaintenance(){
    this.downloadService.downloadFiles("courrencyMaintenance", this.users);
  }

  deleteItem(row) {
    console.log(row);
    this.confirmService.confirm({message: `Are you sure? You want to delete?`})
      .subscribe(res => {
        if (res) {
          this.apiService.deleteCurrencyMaint(row.currencyId)
            .subscribe(data => {
              if (data !== null) {
                this.snack.open('Record Deleted Successfully', 'OK', {
                  duration: 4000,
                  verticalPosition: 'top',
                  horizontalPosition: 'right'
                });
              }
            this.fetchCurrencyMaintDetails();
            },
            err=>{
          })
        }
      })
  }
}
