import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { Subscription } from 'rxjs';
import { TillVaultCcybalHispopupComponent } from '../till-vault-ccybal-hispopup/till-vault-ccybal-hispopup.component';

@Component({
  selector: 'app-till-vault-ccybal-his-summary',
  templateUrl: './till-vault-ccybal-his-summary.component.html',
  styleUrls: ['./till-vault-ccybal-his-summary.component.scss']
})
export class TillVaultCcybalHisSummaryComponent implements OnInit {

  public items: any[];
  users: string[] = [];
  currency: string[] = [];
  till: string[] = [];
  branchCode: string[] = [];
  bankCode: string[] = [];
  public getItemSub: Subscription;
  constructor(
    private dialog: MatDialog,
    private snack: MatSnackBar,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,
    private apiService: ApiService,
  ) { }

  ngOnInit() {
    this.getItems();
    this.getvaulCurrencyBalHistory();
    this.getCurrency();
    this.getBankCode();
    this.getBranchCode();
    this.getTillId();

  }
  ngOnDestroy() {
    if (this.getItemSub) {
      this.getItemSub.unsubscribe()
    }
  }
  getItems() {
    this.items = this.users;
  }
  openPopUp(data: any = {}, isNew?) {
    let title = isNew ? 'Add new Till Vault Currency Balance History' : 'Update Till Vault Currency Balance History';
    let dialogRef: MatDialogRef<any> = this.dialog.open(TillVaultCcybalHispopupComponent, {
      width: '720px',
      disableClose: true,
      data: {
        title: title,
        payload: data,
        currency: this.currency,
        till: this.till,
        branchCode: this.branchCode,
        bankCode: this.bankCode,
      },
    })
    dialogRef.afterClosed().subscribe(res => {
        if(!res) {
          // If user press cancel
          return;
        }
        if (isNew) {
          this.loader.open();
          this.apiService.saveoftillVaultCurrencyBalHistory(res)
            .subscribe(data => {
              this.items = data;
              this.getvaulCurrencyBalHistory();
              this.loader.close();
              this.snack.open('Record Added!', 'OK', { duration: 4000 })  
            })
        } else {
          this.loader.open();
          this.apiService.saveoftillVaultCurrencyBalHistory( res)
            .subscribe(data => {
              this.items = data;
              this.getvaulCurrencyBalHistory();
              this.loader.close();
              this.snack.open('Record Updated!', 'OK', { duration: 4000 })
            })
        }
      })
  }
  deleteItem(row) {
    this.confirmService.confirm({message: `Delete ${row.entityCode}?`})
      .subscribe(res => {
        if (res) {
          this.loader.open();
          this.apiService.removetillVaultCurrencyBalHistory(row.id)
            .subscribe((res: any) => {        
                }, (err: any) => {''
             
              if (err.status == 200) {
                this.loader.close();
                this.getvaulCurrencyBalHistory();
                this.snack.open('Record deleted!', 'OK', { duration: 4000})
              
                 } })
        }
      })
  }
  getvaulCurrencyBalHistory() {
    this.apiService.gettillCurrencyBalHistoryResp().subscribe((resp) => {
      this.users = resp;
      this.items = this.users;
    });
  }
  getCurrency() {
    this.apiService.getCountryValues().subscribe((resp) => {
      this.currency = resp;
    });
  }
  getBankCode() {
    this.apiService.getBankCode().subscribe((resp) => {
      this.bankCode = resp;
    });
  }
  getBranchCode() {
    this.apiService.getBranchCode().subscribe((resp) => {
      this.branchCode = resp;
    });
  }
  getTillId() {
    this.apiService.getTillId().subscribe((resp) => {
      this.till = resp;
    });
  }


}
