import {
  ChangeDetectorRef,
  Component,
  Inject,
  OnInit,
  ViewChild,
} from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { ApiService } from "app/shared/services/api.service";
import { ActivatedRoute, Router } from "@angular/router";
import { MatSnackBar } from "@angular/material/snack-bar";
import { DataService } from "app/shared/services/data.service";
import { LocalStoreService } from "app/shared/services/local-store.service";
import { AuditLogComponent } from "app/views/maintenance/audit-log/audit-log.component";
import Swal from "sweetalert2";
@Component({
  selector: "app-cutomer-category-create",
  templateUrl: "./cutomer-category-create.component.html",
  styleUrls: ["./cutomer-category-create.component.scss"],
})
export class CutomerCategoryCreateComponent implements OnInit {
  @ViewChild("auditLog") auditLog: AuditLogComponent;
  public itemForm: FormGroup;
  cutomerCategoryForm: FormGroup;
  isReadOnly: boolean = true;
  isEdit: boolean = false;
  isView: boolean = true;
  show = false;
  editNow: boolean = false;
  entityIsEdit: boolean;
  id: number;
  isAuditData: boolean = false;
  readorWrite: boolean = true;
  loggedInUser: string;
  auditData: any;

  constructor(
    private router: Router,
    private snack: MatSnackBar,
    private fb: FormBuilder,
    private api: ApiService,
    private route: ActivatedRoute,
    private cdr: ChangeDetectorRef,
    private dataService: DataService,
    private ls: LocalStoreService,
    private apiService: ApiService
  ) {}

  ngOnInit() {
    this.buildItemForm({});

    this.route.queryParamMap.subscribe((params: any) => {
      this.entityIsEdit = params.get("isEdit");
      if (params.get("isEdit") == "Yes") {
        this.isReadOnly = false;
        this.isView = true;
        this.editNow = true;
      } else if (params.get("isEdit") == "No") {
        this.isReadOnly = true;
        this.isView = true;
        this.editNow = false;
      } else if (params.get("isEdit") == "false") {
        this.isReadOnly = false;
        this.isView = false;
        this.editNow = false;
      } else if (params.get("isEdit") == null) {
        this.isView = false;
      }
      if (params.get("id")) {
        this.isEdit = true;
        this.id = params.get("id");
        this.api.editEntityById(params.get("id")).subscribe((resp) => {
          this.buildItemForm(resp);
        });
        this.api
          .getCustomerCategoryById(params.get("id"))
          .subscribe((resp: any) => {
            this.buildItemForm(resp);
            if (resp.authStatus == "U") {
              resp.authDate = "";
              resp.authBy = "";
            }
            this.auditData = resp;
            this.isAuditData = true;
            this.dataService.setAuditLogData(this.auditData);
          });
      } else {
        this.readorWrite = false;
        this.buildItemForm({});
      }
    });

    this.loggedInUser = this.ls.getItem("ICUST_USER").userName;
  }

  buildItemForm(item) {
    this.itemForm = this.fb.group({
      id: [item.id || ""],
      authStatus: [item.authStatus || ""],
      categoryCode: [item.categoryCode || "", [Validators.required]],
      categoryDescription: [
        item.categoryDescription || "",
        [Validators.required],
      ],
      categoryName: [item.categoryName || "", [Validators.required]],
      isActive: [item.isActive || true],
      oneTimeAuth: [item.oneTimeAuth || ""],
      recordStatus: [item.recordStatus || ""],
      version: [item.version || ""],
      createdDate: [item.createdDate || ""],
      lastModifiedDate: [item.lastModifiedDate || ""],
      authBy: [item.authBy || ""],
      authDate: [item.authDate || ""],
      modifiedBy: [item.modifiedBy || ""],
      createdBy: [item.createdBy || ""],
      noOfModification: [item.noOfModification || ""],
    });
  }

  goBack() {
    this.router.navigate([`/maintenance/main/customerCategory`]);
  }
  deleteRecord(operation) {
    Swal.fire({
      text:
        "You are trying to " +
        operation +
        " record." +
        " Do you want to proceed?",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      cancelButtonText: "NO",
      confirmButtonText: "YES",
      icon: "info",
    }).then((result) => {
      if (result.isConfirmed == true) {
        this.apiService.deleteCustomerCategory(this.auditData.id).subscribe(
          (data) => {
            this.router.navigate([`/maintenance/main/customerCategory`]);
          },
          (err) => {
            if (err.status == 200) {
              this.snack.open("Record Deleted Successfully", "OK", {
                duration: 4000,
                verticalPosition: "top",
                horizontalPosition: "right",
              });
            }
            this.router.navigate([`/maintenance/main/customerCategory`]);
          }
        );
      }
    });
  }
  editRecord() {
    this.readorWrite = false;
  }

  updateRecord(operation) {
    Swal.fire({
      text:
        "You are trying to " +
        operation +
        " record." +
        " Do you want to proceed?",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      cancelButtonText: "NO",
      confirmButtonText: "YES",
      icon: "info",
    }).then((result) => {
      if (result.isConfirmed == true) {
        let payload: any = {};
        payload.id = this.id;
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser;
        this.apiService
          .getAuditLogsFortillvaultMaster(payload)
          .subscribe((data) => {
            if (data.authStatus == "U") {
              data.authDate = "";
              data.authBy = "";
            }

            this.auditData = data;
            this.dataService.setAuditLogData(this.auditData);
            this.isAuditData = true;
            this.dataService.sendClickEvent();
            let message = "";
            switch (operation) {
              case "open":
                message = "opened";
                break;
              case "close":
                message = "closed";
                break;
              case "auth":
                message = "authorized";
                break;
              default:
                break;
            }
            this.snack.open(`Record ${message} successfully!'`, "OK", {
              duration: 2000,
              verticalPosition: "top",
              horizontalPosition: "right",
            });
          });
      }
    });
  }

  submit() {
    if (this.itemForm.invalid) {
      return;
    } else {
      this.itemForm.value.loggedInUser = this.loggedInUser;
      this.api.saveCustomerCategory(this.itemForm.value).subscribe((data) => {
        if (data != null && this.isEdit == true) {
          this.router
            .navigate([`/maintenance/main/customerCategory`])
            .then((_) => {
              this.snack.open("Data Updated!", "OK", {
                duration: 4000,
                verticalPosition: "top",
                horizontalPosition: "right",
              });
            });
          this.cdr.markForCheck();
        } else if (data != null || this.isEdit == true) {
          this.router
            .navigate([`/maintenance/main/customerCategory`])
            .then((_) => {
              this.snack.open("Data Saved!", "OK", {
                duration: 4000,
                verticalPosition: "top",
                horizontalPosition: "right",
              });
            });
          this.cdr.markForCheck();
        }
      });
    }
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  keyPressAlphanumeric(event) {
    var inp = String.fromCharCode(event.keyCode);
    if (/[0-9a-zA-Z]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }
  dataByBank(index, bankC) {
    return bankC.bankCode;
  }
}
