import { Component, Inject, Input, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { DataService } from 'app/shared/services/data.service';
import { AuditLogComponent } from '../../audit-log/audit-log.component';
import { ActivatedRoute } from '@angular/router';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-bank-details',
  templateUrl: './bank-details.component.html',
  styleUrls: ['./bank-details.component.scss']
})
export class BankDetailsComponent implements OnInit {
  public bankForm: FormGroup;
  currencies:string[] = [];
  countries: any = [];
  states: any = [];
  cities: any = [];
  bankItem: any = {};
  currentUser: any;
  entityCodeArr: any = [];
  isAllSameDigitVal: boolean = false;
  loggedInUser : string;
  auditLogData : any;
  id : number;

  isEdit: boolean = false;
  readorWrite: boolean = true;

  @ViewChild('auditLog') auditLog: AuditLogComponent


  constructor(private apiService: ApiService,
    private fb: FormBuilder,
    private dataService: DataService,
    private ls: LocalStoreService,
    private loader: AppLoaderService,
    private snack: MatSnackBar,
    private router: Router,
    private route: ActivatedRoute,
  ) { }

  ngOnInit(): void {
    this.currentUser = this.ls.getItem("ICUST_USER");
    this.dataService.getBankItem.subscribe(data =>{
    this.bankItem = typeof this.bankItem === 'object' ? data : {};
    this.id = this.bankItem.id;
    if(this.id){
      this.isEdit = true;
    }
    else{
      this.isEdit = false;
      this.readorWrite=false;
    }
    if( this.bankItem.authStatus=="Unauthorized" ){
      this.bankItem.authDate =""
      this.bankItem.authBy=""
    }
    console.log("this.bankItemt",this.bankItem)
    this.auditLogData = this.bankItem;
    this.dataService.setAuditLogData(this.auditLogData);
    });

    this.getBranchCurrency();
    this.getCountryName('country');
    this.getEntityDropdown();
    this.loggedInUser = this.ls.getItem('ICUST_USER').userName;

    this.buildBankForm(this.bankItem);
  }

  buildBankForm(item) {
    this.bankForm = this.fb.group({
      bankCode: [item.bankCode || '', Validators.pattern("(?=.*[0-9])(?=.*[a-zA-Z])([a-zA-Z0-9]+){4,4}$")],
      id: [item.id ? item.id : null],
      bankCurrency: [item.bankCurrency || '', Validators.required],
      bankName: [item.bankName || '', Validators.required],
      defaultLanguage: [item.defaultLanguage || '', Validators.required],
      denomRequired: [item.denomRequired || ''],
      entityCode: [item.entityCode || '', Validators.required],
      headOfficeBrn: [item.headOfficeBrn || ''],
      //swiftCode: [item.swiftCode || '', Validators.required],
      address1: [item.address1 || '', Validators.required],
      address2: [item.address2 || ''],
      country: [item.country || '', Validators.required],
      state: [item.state || '', Validators.required],
      city: [item.city || '', Validators.required],
      pincode: [item.pincode || '', Validators.required],
      telephone: [item.telephone || ''],
      mobile: [item.mobile || '', Validators.required],
      swiftCode: [item ? item.swiftCode : '', Validators.pattern("(?=.*[0-9])(?=.*[a-zA-Z])([a-zA-Z0-9]+){8,8}$")],
    });

    if (item.country) {
      this.getStateName(item.country);
      this.getCityName(item.state);
    }
  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteBank(this.auditLogData.id,this.currentUser.userName)
        .subscribe(data => {
          this.router.navigate([`/maintenance/main/bank/list`]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
          this.router.navigate([`/maintenance/main/bank/list`]);
        
      })
      }}))
   
         
       
  }

editRecord(){
this.readorWrite=false;
}

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
        payload.id = this.id;
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser;
        this.apiService.getAuditLogsForBank(payload).subscribe((data) => {
          if( data.authStatus=="U" ){
            data.authDate = ""
            data.authBy = ""
            }
          this.auditLogData = data;
          this.dataService.setAuditLogData(this.auditLogData);
          this.dataService.sendClickEvent();
    
          let message = '';
          switch (operation) {
            case 'open':
              message = 'opened'
              break;
            case 'close':
              message = 'closed'
              break;
            case 'auth':
              message = 'authorized'
              break;
            default:
              break;
          }
          this.snack.open(`Record ${message} successfully!'`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        });
      }}))
   
  }


  getBranchCurrency(){
    // this.apiService.getCountryValues().subscribe(_list =>{
    //   this.currencies = _list;
    // },(err:any) =>{

    // })
    this.apiService.getCurrencyDetails().subscribe(data=>{
      console.log("Auth currecy details", data)
      for(let i=0; i<data.length;i++){
        this.currencies.push(data[i])
      }
      console.log(this.currencies);
    })
  }

  // getCountryName(key: string) {
  //   this.apiService.getDropDownValues(key).subscribe((data) => {
  //     this.countries = data;
  //   });
  // }
  getCountryName(key: string) {
    this.apiService.getAllCountries().subscribe((data: any) => {
      console.log('resp',data)
      for(let i=0; i<data.length;i++){
        if(data[i].authStatus==='A'){
          console.log(data[i])
          this.countries.push(data[i].countryName);
          
        }
      }console.log(this.countries)
    });
  }
  getStateName(countryName){
    this.apiService.getAllCountries().subscribe((data: any) => {
      for(let i=0; i<data.length;i++){       
        if(data[i].countryName === countryName){
          this.apiService.getStateByCountry(data[i].id).subscribe((resp: any) => {
          this.states = resp
          })
          break;   
        }
      }
    })
    }

  

  getCityName(state: any) {
    let entityName = "city";
    this.apiService.getDropDownValues1(entityName, state).subscribe((data) => {
      this.cities = data;
    });
  }

  getEntityDropdown() {
    this.apiService.getEntityDropdown().subscribe((data) => {
      console.log(data);
      for(let i=0; i<data.length;i++){
        if(data[i].authStatus==='A'){
          this.entityCodeArr.push(data[i]);
         
        }
      }
      //this.entityCodeArr = data;
    });
  }

  onSubmit() {
    if (this.bankForm.invalid) {
      return;
    } else {
      let payload: any = {};
      payload = this.bankForm.value;
      if (this.id) {
        payload.id = this.id;
      }
    //  payload.entityCode = payload.entityCode.entityCode;
      payload.loggedInUser = this.loggedInUser;
      this.loader.open();
      this.apiService.createOrUpdateBank(payload, this.currentUser.userName)
        .subscribe(data => {
          this.loader.close();
          this.router.navigate(['/maintenance/main/bank/list']).then(_ => {
            this.snack.open('Data Saved Successfully!', 'OK',
              {
                duration: 4000,
                verticalPosition: "top",
                horizontalPosition: "right",
              });
          });
        }, (err: any) => {
          this.loader.close();
          this.snack.open(`Error!`, 'OK', { duration: 4000 });
        })
    }
  }
  _keyPress1(event: any) {
    const pattern = /[a-zA-Z0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  _keyPressForAlphabets(event:any)
  {
      var keyCode = (event.which) ? event.which : event.keyCode
      if ((keyCode < 65 || keyCode > 90) && (keyCode < 97 || keyCode > 123) && keyCode != 32)
       
      return false;
          return true;
  }

  keyPressAlphanumeric(event) {
    var inp = String.fromCharCode(event.keyCode);
    if (/[0-9a-zA-Z]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }

  ngOnDestroy() {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    this.dataService.shareBankItem({});
  }
  goBack() {
    this.router.navigate([`/maintenance/main/bank/list`]);
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }
  isAllSameDigit(event) {
    let count = 0, number = event.target.value;
    if (number != '') {
      for (var i = 0; i < number.length; i++) {
        if (0 == number[i]) {
          count++;
        }
      }
      if (count == number.length) {
        this.isAllSameDigitVal = true;
      }
      else {
        this.isAllSameDigitVal = false;
      }
    } else {
      this.isAllSameDigitVal = false;
    }
  }
  dataByCurrency(index,currency){
    return currency.currencyCode;
  }
  dataByEntityCode(index,entity){
    return entity.entityCode;
  }
  dataByCountry(index,state){
    return state.value1;
  }
  dataByState(index,city){
    return city.value1;
  }
  dataByCity(index,country){
    return country;
  }
}
