import { ChangeDetectorRef, Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatSnackBar } from "@angular/material/snack-bar";
import { ActivatedRoute, Router } from "@angular/router";
import { ApiService } from "app/shared/services/api.service";
import { DataService } from "app/shared/services/data.service";
import { LocalStoreService } from "app/shared/services/local-store.service";
import { AuditLogComponent } from "../../audit-log/audit-log.component";
import * as moment from 'moment';
import Swal from 'sweetalert2';

@Component({
  selector: "app-add-currency-pair-rates-maintenance",
  templateUrl: "./add-currency-pair-rates-maintenance.component.html",
  styleUrls: ["./add-currency-pair-rates-maintenance.component.scss"],
})
export class AddCurrencyPairRatesMaintenanceComponent implements OnInit {
  @ViewChild("auditLog") auditLog: AuditLogComponent;
  public branchForm: FormGroup;
 
  currencies: any = [];
  data: any;
 
  loggedInUser: any;
  isReadOnly: boolean = true;
  isEdit: boolean = false;
  isView: boolean = true;
  show = false;
  editNow: boolean = false;
  currencyPairRateIsEdit: any;
  id: any;
  auditData: any;
  isAuditData: boolean = false;
  currency: any;
  branchCode: any[] = [];
  minDate = new Date();
  currencyPair: any[] = [];
  readorWrite: boolean=true;

  constructor(
    private router: Router,
    private fb: FormBuilder,
    private apiService: ApiService,
    private snack: MatSnackBar,
    private cdr: ChangeDetectorRef,
    private ls: LocalStoreService,
    private route: ActivatedRoute,
    private dataService: DataService
  ) {}

  ngOnInit(): void {
    
    this.route.queryParamMap.subscribe((params: any) => {
      console.log(params.get("id"));
      this.currencyPairRateIsEdit = params.get("isEdit");
      console.log("this.entityIsEdit", this.currencyPairRateIsEdit);
      if (params.get("isEdit") == "Yes") {
        this.isReadOnly = false;
        this.isView = true;
        this.editNow = true;
      } else if (params.get("isEdit") == "No") {
        this.isReadOnly = true;
        this.isView = true;
        this.editNow = false;
      } else if (params.get("isEdit") == "false") {
        this.isReadOnly = false;
        this.isView = false;
        this.editNow = false;
      } else if (params.get("isEdit") == null) {
        this.isView = false;
      }
      if (params.get("id")) {
        this.isEdit = true;
        this.id = params.get("id");
        this.apiService
          .getCurrencyPairRatesById(params.get("id"))
          .subscribe((resp) => {
            console.log(resp);
            this.buildItemForm(resp);
            this.auditData = resp;
            this.isAuditData = true;
            this.dataService.setAuditLogData(this.auditData);
          });
      } else {
        this.readorWrite=false;
        this.buildItemForm({});
      }
    });
    this.buildItemForm({});
    this.fetchCurrencyPair();
    this.getBranchCode();
    this.loggedInUser = this.ls.getItem("ICUST_USER");
    console.log(this.loggedInUser);
  }

  buildItemForm(item) {
    console.log("selected item 2::", item);
    console.log("item.rateDate",item.rateDate)
   
    if(item.rateDate){
      item.rateDate = item ? moment(item.rateDate).format() : "";
    }
    console.log("item.rateDate",item.rateDate)
    this.branchForm = this.fb.group({
      currencyPairId: [item.currencyPairId || "", Validators.required],
      branchCode: [item.branchCode || "", Validators.required],
      rateType: [item.rateType || "", Validators.required],
      rateDate: [item.rateDate || "", Validators.required],
      rateSeq: [item.rateSeq || "", Validators.required],
      buySpread: [item.buySpread || "", Validators.required],
      sellSpread: [item.sellSpread || "", Validators.required],
      id: [item.id || ""],
      midRate: [item.midRate || "", Validators.required],
      buyRate: [item.buyRate || "", Validators.required],
      sellRate: [item.sellRate || "", Validators.required],
      authStatus: [item.authStatus || ""],
      isActive: [item.isActive || true],
      oneTimeAuth: [item.oneTimeAuth || ""],
      recordStatus: [item.recordStatus || ""],
      version: [item.version || ""],
      loggedInUser: [item.loggedInUser || ""],
    });
  }

  submit() {
    let startDate = this.convertDate(this.branchForm.controls.rateDate.value);
    this.branchForm.value['rateDate'] = startDate;
    console.log("Form:", this.branchForm.value);
    // if (this.branchForm.invalid) {
    //   return;
    // } else {
      console.log("payload:", this.branchForm.value);
      this.branchForm.value.loggedInUser = this.loggedInUser.userName;
      this.apiService.saveCurrencyPairRates(this.branchForm.value).subscribe((data) => {
          console.log("data", data);
          if (data != null && this.isEdit == true) {
            
                this.snack.open("Data Updated!", "OK", {
                  duration: 4000,
                  verticalPosition: "top",
                  horizontalPosition: "right",
                });
              
            this.cdr.markForCheck();
          } else if (data != null || this.isEdit == true) {
            // 
            console.log("data", data);
              this.snack.open("Data Saved!", "OK", {
                duration: 4000,
                verticalPosition: "top",
                horizontalPosition: "right",
              });
             
            this.cdr.markForCheck();
          }
        });
        this.router.navigate([`/maintenance/main/currencyPairRateMaintenance`]);
    // }
    
  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteEntity(this.auditData.pairRateId)
        .subscribe(data => {
          this.router.navigate(["/maintenance/main/currencyPairRateMaintenance"]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
          this.router.navigate(["/maintenance/main/currencyPairRateMaintenance"]);
        
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        console.log(this.id);
        let payload: any = {};
        payload.id = this.id;
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser.userName;
        this.apiService.updateAuditLogsForCurrencyPairRates(payload).subscribe((data) => {
            console.log(data);
            this.auditData = data;
            this.dataService.setAuditLogData(this.auditData);
            this.isAuditData = true;
            this.dataService.sendClickEvent();
            let message = "";
            switch (operation) {
              case "open":
                message = "opened";
                break;
              case "close":
                message = "closed";
                break;
              case "auth":
                message = "authorized";
                break;
              default:
                break;
            }
            this.snack.open(`Record ${message} successfully!'`, "OK", {
              duration: 2000,
              verticalPosition: "top",
              horizontalPosition: "right",
            });
          });
      }}))
    
  }

  _keyPress(event: any) {
    console.log("PRessed", event);
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  convertDate(value) {
    let date = moment(value).format('YYYY-MMM-DD');
    // let date = moment(value).format('YYYY-MM-DDThh:mm:ss');
    console.log("date", date);
    if (date)
      return date;
  }

  fetchCurrencyPair(){
    this.apiService.getCurrencyPair().subscribe((resp:any) =>{
     console.log("currencyPair",resp);
     for(let i=0; i< resp.length;i++){
      if(resp[i].authStatus=="A" )
      this.currencyPair.push(resp[i]);
     }
    })
  }

  getBranchCode(){
    this.apiService.getBranchCode().subscribe(branchCodeResp=>{
      console.log(branchCodeResp);
      for(let i=0; i<branchCodeResp.length;i++){
        if(branchCodeResp[i].authStatus==='A'){
          this.branchCode.push(branchCodeResp[i]);
        }
      }
      console.log(this.branchCode);
      console.log("branchCode",branchCodeResp);
      // this.branchCode=branchCodeResp;
    });
  }

  goBack() {
    this.router.navigate(["/maintenance/main/currencyPairRateMaintenance"]);
  }
}
