import { LiveAnnouncer } from '@angular/cdk/a11y';
import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort, Sort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';

@Component({
  selector: 'app-product-maintenance',
  templateUrl: './product-maintenance.component.html',
  styleUrls: ['./product-maintenance.component.scss']
})
export class ProductMaintenanceComponent implements OnInit {
  @ViewChild('searchVal') searchVal: ElementRef;
  selectedFilterIndex : number = 0;

  dataSource = new MatTableDataSource();
  displayedColumns: string[] = ['bankCode', 'entityCode', 'productCode', 'productName',  'oneTimeAuth', 'authStatus', 'recordStatus', 'action'];

  filterByOption: { key: string, value: string }[] = [
    { key: 'productCode', value: 'Product Code' },
    { key: 'productName', value: 'Product Name' },
    { key: 'entityCode', value: 'Entity Code' },
  ];

  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  users: string[] = [];
  public items: string[];

  constructor(private router:Router,private snack: MatSnackBar,
    private apiService:ApiService,private confirmService: AppConfirmService,
    private _liveAnnouncer: LiveAnnouncer) { }

    ngOnInit(): void {
      this.getProductInfo();
      this.getItems();
    }

    onFileChange(event) {
      console.log('event', event.path[0].files[0]);
      const file: File = event.path[0].files[0];
      const uploadData = new FormData();
      uploadData.append('screenType', 'product');
      uploadData.append('file', file);
      this.apiService.uploadExelFile(uploadData).subscribe((response) => {
        console.log(response);
        this.getProductInfo();
        this.snack.open(`File Uploaded Successfully`, "OK", {
          duration: 4000,
          verticalPosition: "top",
          horizontalPosition: "right",
        });
        this.getProductInfo();
      });
    }
    getItems() {
      this.items = this.users;
    }
    getProductInfo(){
      this.apiService.getProductDetails().subscribe((data: any) => {
        for(let i=0;i<data.length;i++){
          if(data[i].authStatus == 'U'){
            data[i].authStatus = 'Unauthorized'
          }
          if(data[i].authStatus == 'A') {
            data[i].authStatus = 'Authorized'
          } 
          if(data[i].recordStatus == 'O'){
            data[i].recordStatus = 'Open'
          } 
          if(data[i].recordStatus == 'C'){
            data[i].recordStatus = 'Closed'
          }
        }
        this.users = data;
        this.items = this.users;
        this.dataSource = new MatTableDataSource(data);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      });
    }
  doFilter(filterBy, e){
    this.filterTable(filterBy);
    this.applyFilter(e);
  }
  
  filterTable(filterBy) {
   
    this.dataSource.filterPredicate = (data: any, filter: string): boolean => {
      if(data[filterBy] !=null){
      return (
        data[filterBy].trim().toLocaleLowerCase().includes(filter)
      )}
    }
  }
  
  clearFilter(){
    this.dataSource.filter = '';
    this.searchVal.nativeElement.value = '';
    this.selectedFilterIndex = 0;
   }
 
 
   applyFilter(event: Event) {
     
     const filterValue = (event.target as HTMLInputElement).value;
     this.dataSource.filter = filterValue.trim().toLocaleLowerCase();
   }
   announceSortChange(sortState: Sort) {
     if (sortState.direction) {
       this._liveAnnouncer.announce(`Sorted ${sortState.direction}ending`);
     } else {
       this._liveAnnouncer.announce('Sorting cleared');
     }
   }
   addNew(){
    this.router.navigate(["/maintenance/main/product/addProduct"]);
  }
  openEdit(data: any = {},isEdit : string) {  
    this.router.navigate(["/maintenance/main/product/addProduct"],{ queryParams: { id: data.id, isEdit : isEdit } });
  }
  goBack(){
    this.router.navigate(["/maintenance/main"]);
  }
  deleteItem(element) {
    this.confirmService.confirm({message: `Delete  ${element.productName}?`})
      .subscribe(res => {
        if (res) {
          this.apiService.deleteProductById(element.id)
            .subscribe(data => {
            },
              err=>{
                if (err.status == 200) {
                  this.snack.open('Record Deleted Successfully', 'OK', {
                    duration: 4000,
                    verticalPosition: 'top',
                    horizontalPosition: 'right'
                  });
                }
                this.getProductInfo();
            })
        }
      });
  }
  dataByFilter(index,item){
    return item.key
  }
}
