import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, Router } from '@angular/router';
import { addTax } from 'app/shared/models/addtaxchrg';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-add-txncharges',
  templateUrl: './add-txncharges.component.html',
  styleUrls: ['./add-txncharges.component.scss']
})
export class AddTxnchargesComponent implements OnInit {
  addTxnChargesForm:FormGroup;
  isEdit: boolean = false;
  isReadOnly: boolean = true;
  id: number;
  chargeCurrencyArr: string[]=[];
  loggedInUser : string;
  auditData : addTax;
  isAuditData : boolean = false;
  minDate = new Date();
  readorWrite: boolean=true;
 // chargeCurrencyArr: string[] = ['INR', 'Dollar', '	EUR','GBP'];

  constructor(private formBuilder: FormBuilder,private apiService:ApiService,
    private router:Router,private snack: MatSnackBar, private route : ActivatedRoute, private ls: LocalStoreService, private dataService: DataService,
    private cdr: ChangeDetectorRef) { this.addTxnChargesForm = this.formBuilder.group({
    chargeAmount: [ '',[Validators.required]],
    chargeCurrency: [ '',[Validators.required]],
    chargeDesc: [ '',[Validators.required]],
    txnDate: [ '',[Validators.required]],
    txnReference: [ ''],
    id: [ ''],
    loggedInuser : [''],


  })}

  ngOnInit(): void {
    this.getCountrynameValues();
    this.route.queryParamMap.subscribe((params: any) => {
      if(params.get('id')){
        this.isEdit = true;
        this.id = params.get('id');
        this.apiService.getTxnChargesById(params.get('id')).subscribe((resp) =>{
           this.buildItemForm(resp);
           this.auditData =resp;
           this.isEdit = true;
           this.isAuditData = true;
           this.dataService.setAuditLogData(this.auditData);
           this.dataService.sendClickEvent();
        });
      }
      else{
        this.readorWrite=false;
        this.buildItemForm({});
      }
    }
    );
    this.loggedInUser = this.ls.getItem('ICUST_USER').userName;
  }
  getCountrynameValues() {
    // this.apiService.getCountryValues().subscribe((allCountryValues: any) => {
    //   this.chargeCurrencyArr = allCountryValues;
    // });
    this.apiService.getCurrencyDetails().subscribe(data=>{
      console.log("Auth currecy details", data)
      for(let i=0; i<data.length;i++){
        this.chargeCurrencyArr.push(data[i].currencyId)
      }
      console.log(this.chargeCurrencyArr);
    })
  }
  buildItemForm(item) {
    if(item.txnDate){
      item.txnDate = item ? moment(item.txnDate).format() : '';
    }
    this.addTxnChargesForm = this.formBuilder.group({
      chargeAmount: [item.chargeAmount || '',[Validators.required]],
      chargeCurrency: [item.chargeCurrency ||'',[Validators.required]],
      chargeDesc: [item.chargeDesc ||'',[Validators.required]],
      id: [ item.id ||''],
      txnDate: [item.txnDate ||'',[Validators.required]],
      txnReference: [item.txnReference ||''],
      loggedInuser : [item.loggedInUser || ''],

    })
  }
  goBack(){
    this.router.navigate(["/maintenance/main/transactionCharges"]);
  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteTxnChargesById(this.auditData.id)
        .subscribe(data => {
          this.router.navigate(["/maintenance/main/transactionCharges"]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
          this.router.navigate(["/maintenance/main/transactionCharges"]);
        
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
    payload.id = this.id;
    payload.operation = operation;
    payload.loggedInUser = this.loggedInUser;
    this.apiService.getAuditLogsForTransactioncharge(payload).subscribe((data) => {
      this.auditData = data;
      this.dataService.setAuditLogData(this.auditData);
      this.isAuditData = true;
      this.dataService.sendClickEvent();
      let message = '';
      switch (operation) {
        case 'open':
          message = 'opened'
          break;
        case 'close':
          message = 'closed'
          break;
        case 'auth':
          message = 'authorized'
          break;
        default:
          break;
      }
      this.snack.open(`Record ${message} successful!'`, "OK", {
        duration: 2000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
      }}))
    
  }
  submit(){
    
    // let now = new Date();
    let startDate = this.convertDate(this.addTxnChargesForm.controls.txnDate.value);
    this.addTxnChargesForm.value['txnDate'] = startDate;
    // now=this.addTxnChargesForm.value.txnDate;
    // var dateStringWithTime = moment(now).format('YYYY-MMM-DD HH:MM:SS');
    this.addTxnChargesForm.value.loggedInUser = this.loggedInUser;

    this.addTxnChargesForm.value.chargeAmount=parseInt(this.addTxnChargesForm.value.chargeAmount)
    this.addTxnChargesForm.value.id=parseInt(this.addTxnChargesForm.value.id)
   
    let payload={
      chargeAmount: this.addTxnChargesForm.value.chargeAmount,
      chargeCurrency: this.addTxnChargesForm.value.chargeCurrency,
      chargeDesc: this.addTxnChargesForm.value.chargeDesc,
      txnDate : this.addTxnChargesForm.value.txnDate,
      // txnDate:dateStringWithTime,
      id:this.addTxnChargesForm.value.id,
      txnReference: this.addTxnChargesForm.value.txnReference,
      loggedInUser: this.loggedInUser,
      //txnId:101,

    }
  
  this.apiService.updateTxnChargeDetails(payload).subscribe(response => {
    if(response != null && this.isEdit){
      this.snack.open(`Record Updated!'`, "OK", {
        duration: 2000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
      this.cdr.markForCheck();
    }
    else if(response != null && !this.isEdit){
      this.snack.open(`Record Saved!'`, "OK", {
        duration: 2000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
      this.cdr.markForCheck();
    }
    this.router.navigate(["/maintenance/main/transactionCharges"]);
  });
 
  
  }

  convertDate(value) {
    let date = moment(value).format('YYYY-MMM-DD');
    // let date = moment(value).format('YYYY-MM-DDThh:mm:ss');
    if (date)
      return date;
  }

    dataByCharge(index,chargeCurrency){
    return chargeCurrency.currencyCode;
  }
}
