import { ChangeDetectorRef, Component, OnInit, OnDestroy } from '@angular/core';
import { MatDialogRef, MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Subject, Subscription } from 'rxjs';
import { rumangoAnimations } from 'app/shared/animations/rumango-animations';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { Location } from '@angular/common';
import { ActivatedRoute, Data, Router } from '@angular/router';
import { HttpClient, HttpParams } from '@angular/common/http';
import { FormArray, FormBuilder, FormGroup } from '@angular/forms';
import { Validators } from '@angular/forms';
import { SendHolidayScreenService } from 'app/shared/services/send-holiday-screen.service';
import { DataService } from 'app/shared/services/data.service';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-add-holiday',
  templateUrl: './add-holiday.component.html',
  styleUrls: ['./add-holiday.component.scss']
})
export class AddHolidayComponent implements OnInit {
  addBranchHoliday: FormGroup;
  icBranchHolidayModel: any[] = [];


  yearArr: any[] = [2025, 2024, 2023, 2022, 2021, 2020];
  branchCode: string[] = [];
  bankCode: string[] = [];
  entityCode: string[] = [];
  addBranchHolidayFormData: { data: any; holidayModel:any; action:any};

  dateList: any[];
  disableSave:boolean=false;
  isEdit: boolean=false;
  id: any;
  holidayModelResp: any;
  screen: string;
  auditLogData: any;
  readorWrite: boolean=true;
  loggedInUser: any;

  constructor(
    private _location: Location,
    private router: Router,
    private dialog: MatDialog,
    private cdr: ChangeDetectorRef,
    private formBuilder: FormBuilder,
    private snack: MatSnackBar,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,private route : ActivatedRoute,
    private apiService: ApiService,
    private dataService:DataService,
    private screenService: SendHolidayScreenService,
    private ls: LocalStoreService
  ) {
    this.screenService.holidayScr.subscribe(res=>{
      console.log(res);
      this.screen=res;
      this.cdr.checkNoChanges();
      this.cdr.markForCheck();
      console.log(this.screen);
     });
    console.log("screen name",this.screen);
    this.disableSave=false;
   this.buildItemForm({});
  
  }
  buildItemForm(item) {
    this.addBranchHoliday = this.formBuilder.group({
      bankCode: [item.bankCode || '', [Validators.required]],
      branchCode: [item.branchCode || ''],
      currencyCode: [item.currencyCode || ''],
      calenderYear: [item.calenderYear || '', [Validators.required]],
      entityCode: [item.entityCode || '', [Validators.required]],
      description: [item.description || ''],
      holidayType: [item.holidayType || '']
    })
  }

  ngOnInit(): void {
    console.log("screen name",this.screen);
    this.loggedInUser = this.ls.getItem('ICUST_USER').userName;
    this.loadData();
    this.route.queryParamMap.subscribe((params: any) => {
      if(params.get('id')){
        this.isEdit = true;
        this.id = params.get('id');
        if(this.screen==="Branch"){
        this.apiService.editeditBranchHoliday(params.get('id')).subscribe((resp) =>{
           this.buildItemForm(resp);
           this.dateList.push(resp.calenderDate)
           console.log(resp)
           this.auditLogData=resp;
           this.dataService.setAuditLogData(this.auditLogData);
           this.dataService.sendClickEvent();
           this.holidayModelResp=resp;
           this.assignFormValue('','UPDATE');
        });
      }else if(this.screen==="Currency"){
        this.apiService.editCurrencyHoliday(params.get('id')).subscribe((resp)=>{
          this.buildItemForm(resp);
          this.auditLogData=resp;
          this.dataService.setAuditLogData(this.auditLogData);
          this.dataService.sendClickEvent();
          this.holidayModelResp=resp;
          this.assignFormValue('','UPDATE');
        })
      }
    }
      else{
        this.readorWrite=false;
        this.buildItemForm({});
      }
    }
    );
  }

  commonHoliday(){
    if(this.screen==='branch'){
      this.router.navigate(["/maintenance/main/holiday/BranchHoliday/commonHoliday"]);
    }else 
    this.router.navigate(["/maintenance/main/holiday/BranchHoliday/commonHoliday"]);
  }
  addBranch(){
    this.router.navigate(["/maintenance/main/holiday/BranchHoliday/addBranch"]);
    
  }
  deleteRecord(operation){
    Swal.fire(
      {
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteCurrencyHoliday(this.auditLogData.id)
        .subscribe(data => {
          if(this.screen==="Branch"){
            this.router.navigate(["/maintenance/main/holiday/BranchHoliday/addHoliday"]);
          }
          else if(this.screen==="Currency"){
            this.router.navigate(["/maintenance/main/holiday/currencyholiday"]);
          }
          
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
          if(this.screen==="Branch"){
            this.router.navigate(["/maintenance/main/holiday/BranchHoliday/addHoliday"]);
          }
          else if(this.screen==="Currency"){
            this.router.navigate(["/maintenance/main/holiday/currencyholiday"]);
          }
        
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }

  resetFormSubject: Subject<boolean> = new Subject<boolean>();


  loadData() {
    this.getBankCode();
    if(this.screen==="Branch"){
      this.getBranchCode();
    }else if(this.screen==="Currency"){
      this.getCurrencyCode();
    }
    this.getEnityCode();
  }
  goBack() {
    this._location.back();
  }
  keyPress1(event: any) {
    const pattern = /[a-zA-Z0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  assignFormValue(event,action) {   
    if(action === 'UPDATE'){
      console.log(this.addBranchHoliday);

      if(this.screen==="Branch"){
        var params1 = new HttpParams().append('bankCode', this.addBranchHoliday.value.bankCode)
      .append('branchCode', this.addBranchHoliday.value.branchCode)
      .append('calenderYear', this.addBranchHoliday.value.calenderYear)
      .append('entityCode', this.addBranchHoliday.value.entityCode);
      console.log("param", params1);
      this.apiService.findByCalenderYearAndEntityCodeAndBankCodeAndBranchCode(params1)
      .subscribe(holidayModel=>{
        this.addBranchHolidayFormData = {
          'data': this.addBranchHoliday.value,
          'holidayModel':holidayModel,
          'action':action
        }
      })
      }
      else if(this.screen==="Currency"){
        var params1 = new HttpParams().append('bankCode', this.addBranchHoliday.value.bankCode)
        .append('currencyCode', this.addBranchHoliday.value.currencyCode)
        .append('calenderYear', this.addBranchHoliday.value.calenderYear)
        .append('entityCode', this.addBranchHoliday.value.entityCode);
        console.log("param", params1);
        this.apiService.findByCalenderYearAndEntityCodeAndBankCodeAndCurrencyCode(params1)
      .subscribe(holidayModel=>{
        this.addBranchHolidayFormData={
          'data': this.addBranchHoliday.value,
          'holidayModel': holidayModel,
          'action': action
        }
      })

      }

     
    }else{
      this.addBranchHolidayFormData = {
        'data': this.addBranchHoliday.value,
        'holidayModel':'',
        'action':action
      }

    }
  }

  public doSomething(dateList: any[]): void {

    this.dateList = [];
    this.dateList = dateList;

  }
  resetChildForm() {
    this.resetFormSubject.next(true);
  }

  saveHoliday(addBranchHoliday) {
    console.log("add branch holiday", addBranchHoliday)
    this.isEdit=false;
    let details: any[] = [];

    this.dateList.forEach((v, k) => {
      console.log("date", v)
      if(this.screen==="Branch"){
        let payload = {
          entityCode: addBranchHoliday.entityCode,
          bankCode: addBranchHoliday.bankCode,
          branchCode: addBranchHoliday.branchCode,
          calenderYear: addBranchHoliday.calenderYear,
          description:  addBranchHoliday.description,
          holidayType: this.ls.getItem('holidayType'),
          calenderDate: v,
          id:this.auditLogData?.id,
          createdBy: this.loggedInUser
        }
        details.push(payload);
      }else if(this.screen==="Currency"){
        let payload = {
          entityCode: addBranchHoliday.entityCode,
          bankCode: addBranchHoliday.bankCode,
          currencyCode: addBranchHoliday.currencyCode,
          calenderYear: addBranchHoliday.calenderYear,
          description:  addBranchHoliday.description,
          holidayType:  this.ls.getItem('holidayType'),
          calenderDate: v,
          id:this.auditLogData?.id,
          createdBy: this.loggedInUser
        }
        details.push(payload);
      }
      
    });
    if(this.screen==="Branch"){
      this.apiService.updateBranchHolidayDetails(details,this.isEdit)
    .subscribe(data => {
      if (data != null) {
        this.disableSave=true;
        this.snack.open('Data Saved' + ' !', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this._location.back();
        this.cdr.markForCheck();
      }

    },error=>{
      this.disableSave=true;
      this.snack.open(`${error.error}` + ' !', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right',
        panelClass: 'snackbar-error'
      });
    
    });
    }else if(this.screen==="Currency"){

      this.apiService.upsertCurrencyHolidayDetails(details)
    .subscribe(data => {
      if (data != null) {
        this.disableSave=true;
        this.snack.open('Data Saved' + ' !', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this._location.back();
        this.cdr.markForCheck();
      }

    },error=>{
      this.disableSave=true;
      this.snack.open(`${error.error}` + ' !', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right',
        panelClass: 'snackbar-error'
      });
    
    });

    }
    

    

  }
  updateRecord(operation) {
    console.log("operations")
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
        payload.id = this.id;
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser;
        if(this.screen==="Branch"){
          this.apiService.getAuditLogsForHolidayBranch(payload).subscribe((data) => {
            this.auditLogData = data;
            this.dataService.setAuditLogData(this.auditLogData);
            this.dataService.sendClickEvent();
            let message = '';
            switch (operation) {
              case 'open':
                message = 'opened'
                break;
              case 'close':
                message = 'closed'
                break;
              case 'auth':
                message = 'authorized'
                break;
              default:
                break;
            }
            this.snack.open(`Record ${message} successful!'`, "OK", {
              duration: 2000,
              verticalPosition: "top",
              horizontalPosition: "right",
            });
          });
        }
        else if(this.screen==="Currency"){
          this.apiService.getAuditLogsForCurrencyBranch(payload).subscribe((data) => {
            this.auditLogData = data;
            this.dataService.setAuditLogData(this.auditLogData);
            this.dataService.sendClickEvent();
            let message = '';
            switch (operation) {
              case 'open':
                message = 'opened'
                break;
              case 'close':
                message = 'closed'
                break;
              case 'auth':
                message = 'authorized'
                break;
              default:
                break;
            }
            this.snack.open(`Record ${message} successful!'`, "OK", {
              duration: 2000,
              verticalPosition: "top",
              horizontalPosition: "right",
            });
          });
        }else{
          
        }
       
      }
    }))
    
  }
  getBranchCode() {
    this.apiService.getBranchCode().subscribe(branchCodeResp => {
      console.log(branchCodeResp);
      for(let i=0; i<branchCodeResp.length;i++){
        if(branchCodeResp[i].authStatus==='A'){
          this.branchCode.push(branchCodeResp[i].branchCode);
        }
      }
      console.log(this.branchCode);
      // this.branchCode = branchCodeResp;
    });
  }
  getCurrencyCode(){
    this.apiService.fetchCurrencyMaintDetails().subscribe(currencyResp =>{
      console.log(currencyResp);
      for(let i=0; i<currencyResp.length;i++){
        if(currencyResp[i].authStatus==='A'){
          this.branchCode.push(currencyResp[i].currencyId.currencyCode);
        }
      }
    })
  }

  trackByFnBranchCode(index, item) {
    return index; 
  }

  trackByFnYearArr(index, item) {
    return index; 
  }

  trackByFnEntityCode(index, item) {
    return index; 
  }

  trackByFnBankCode(index, item) {
    return index; 
  }

  getBankCode() {
    this.apiService.getBankCode().subscribe(bankCodeResp => {
      console.log(bankCodeResp);
      for(let i=0; i<bankCodeResp.length;i++){
        if(bankCodeResp[i].authStatus==='A'){
          this.bankCode.push(bankCodeResp[i]);
        }
      }
      console.log(this.bankCode);
      // this.bankCode = bankCodeResp;
    });
  }

  getEnityCode() {
    this.apiService.fetchEntityDetails().subscribe(entityCodeResp => {
      console.log(entityCodeResp);
      for(let i=0; i<entityCodeResp.length;i++){
        if(entityCodeResp[i].authStatus==='A'){
          this.entityCode.push(entityCodeResp[i]);
        }
      }
      console.log(this.entityCode);
      // this.entityCode = entityCodeResp;
    });
  }
  addNew(screen) {
    console.log(screen)
    if(screen==='Currency'){
      this.router.navigate(["/maintenance/main/holiday/currencyholiday"]);
    }else
    this.router.navigate(["/maintenance/main/holiday/BranchHoliday"]);
  }
  dataByYear(index,year){
    return year;
  }
  dataByEntity(index,entityC){
    return entityC.entityCode;
  }
  dataByBank(index,bankC){
    return bankC.bankCode;
  }
  dataByBranch(index,branch){
    return branch.branchCode;
  }
}
