import { Component, OnInit,ElementRef, ViewChild } from '@angular/core';
import { LiveAnnouncer } from '@angular/cdk/a11y';
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort, Sort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { DataService } from 'app/shared/services/data.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-currency-pair-maintenance',
  templateUrl: './currency-pair-maintenance.component.html',
  styleUrls: ['./currency-pair-maintenance.component.scss']
})
export class CurrencyPairMaintenanceComponent implements OnInit {
  @ViewChild('searchVal') searchVal: ElementRef;
  selectedFilterIndex : number = 0;

  dataSource = new MatTableDataSource();
  displayedColumns: string[] = ['id','bankCode', 'entityCode', 'currency1', 'currency2',  'oneTimeAuth', 'authStatus', 'recordStatus', 'action'];

  filterByOption: { key: string, value: string }[] = [
    { key: 'bankCode', value: 'Bank Code' },
    { key: 'entityCode', value: 'Entity Code' },
    { key: 'currency1', value: 'Currency 1' },
    { key: 'currency2', value: 'Currency 2'}
  ];

  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  // bankCode: any;
  // entityCode: any;
  // currencies: any;
  currencies: string[] = [];
  bankCode: string[] = [];
  entityCode:string[] = [];
  users: any[] = [];
  public items: any[];
  entity: any;
  tillVaultMasterValues: any;
  fetchTillVault: any;
  fetchCurrencyPair:any;
  currencyPairMaintValues:any;
  public getItemSub: Subscription;
  id:any;
  constructor(private router: Router, private _liveAnnouncer: LiveAnnouncer, private confirmService: AppConfirmService, 
    private apiService: ApiService, private snack: MatSnackBar,private dataService:DataService) { }

  ngOnInit(): void {
    this.getBranchCurrency();
    this.getBankCode();
    this.getEntityDropdown();
    this.getItems();
  }

  ngOnDestroy() {
    if (this.getItemSub) {
      this.getItemSub.unsubscribe()
    }
  }


  getItems() {
    this.apiService.getCurrencyPair().subscribe((currencyPairInfo : any)=> {
      if(currencyPairInfo){  
        for(let i=0;i<currencyPairInfo.length;i++){
          if(currencyPairInfo[i].authStatus == 'U'){
            currencyPairInfo[i].authStatus = 'Unauthorized'
          }
          if(currencyPairInfo[i].authStatus == 'A') {
            currencyPairInfo[i].authStatus = 'Authorized'
          } 
          if(currencyPairInfo[i].recordStatus == 'O'){
            currencyPairInfo[i].recordStatus = 'Open'
          } 
          if(currencyPairInfo[i].recordStatus == 'C'){
            currencyPairInfo[i].recordStatus = 'Closed'
          }
        }
      this.currencyPairMaintValues = currencyPairInfo;
        this.items =  this.currencyPairMaintValues;
        console.log("items4::", this.items);
        this.dataSource = new MatTableDataSource(currencyPairInfo);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      }
      });
  
  }

  onFileChange(event) {
    console.log('event', event.path[0].files[0]);
    const file: File = event.path[0].files[0];
    const uploadData = new FormData();
    uploadData.append('screenType', 'currencyPair');
    uploadData.append('file', file);
    this.apiService.uploadExelFile(uploadData).subscribe((response) => {
      this.getItems();
      console.log(response);
      this.snack.open(`File Uploaded Successfully`, "OK", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
    
  }
  fetchCurrencyPairById(){
    this.apiService.getCurrencyPairById(this.id).subscribe((resp) =>{
     console.log("currencyPairById",resp);
     this.fetchCurrencyPair=resp;
    })
    console.log(this.fetchCurrencyPair)
    this.dataService.sendNavParam(this.fetchCurrencyPair);
    this.router.navigate([`/maintenance/main/currencyPairMaintenance/addCurrencyPair/${this.id}`]);
  }
  getBankCode() {
    this.apiService.getBankCode().subscribe((resp) => {
      this.bankCode = resp;
    });
  }
  getEntityDropdown(){
    this.apiService.getEntityDropdown().subscribe((data) => {
      this.entityCode = data;
      console.log("Entity Code",this.entityCode)  
    });
    
  }

  getBranchCurrency(){
    this.apiService.getCountryValues().subscribe(_list =>{
      this.currencies = _list;
    },(err:any) =>{
      console.error("Error", err);
    })
  }
  
  addNew(){
    this.router.navigate(["/maintenance/main/currencyPairMaintenance/addCurrencyPair"]);
  }

  doFilter(filterBy, e){
    console.log(filterBy,e);
    this.filterTable(filterBy);
    this.applyFilter(e);
  }
  applyFilter(event: Event) {
     
    const filterValue = (event.target as HTMLInputElement).value;
    console.log("filterValue",filterValue)
    this.dataSource.filter = filterValue.trim().toLocaleLowerCase();
  }

  clearFilter(){
    this.dataSource.filter = '';
    this.searchVal.nativeElement.value = '';
    this.selectedFilterIndex = 0;
   }

   filterTable(filterBy) {
   
    this.dataSource.filterPredicate = (data: any, filter: string): boolean => {
      console.log('data :: ',data[filterBy]);
      if(data[filterBy] !=null){
      return (
        data[filterBy].trim().toLocaleLowerCase().includes(filter)
      )}
    }
  }

   announceSortChange(sortState: Sort) {
    if (sortState.direction) {
      this._liveAnnouncer.announce(`Sorted ${sortState.direction}ending`);
    } else {
      this._liveAnnouncer.announce('Sorting cleared');
    }
  }
  openEdit(data: any = {},isEdit : string) {
    console.log("data",isEdit);
    console.log(".id",data.id);
    console.log("item1::",data);
    
    this.router.navigate(["/maintenance/main/currencyPairMaintenance/addCurrencyPair"],{ queryParams: { id: data.id, isEdit : isEdit } });
  }

  deleteItem(element) {
    console.log("element",element.id);
    this.confirmService.confirm({message: `Delete  ${element.id}?`})
      .subscribe(res => {
        if (res) {
          this.apiService.deleteCurrencyPair(element.id)
            .subscribe(data => {
              this.getItems();
            },
              err=>{
                if (err.status == 200) {
                  this.snack.open('Record Deleted Successfully', 'OK', {
                    duration: 4000,
                    verticalPosition: 'top',
                    horizontalPosition: 'right'
                  });
                 
                }
            })  
        } 
      });
  }
  
  goBack(){
    this.router.navigate(["/maintenance/main"]);
  }
 
}
