import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { ApiService } from 'app/shared/services/api.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AuditLogComponent } from '../../audit-log/audit-log.component';
import { entityData } from 'app/shared/models/entityMaintenance';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-add-entity-details',
  templateUrl: './add-entity-details.component.html',
  styleUrls: ['./add-entity-details.component.scss']
})
export class AddEntityDetailsComponent implements OnInit {

  public entityMaintenance: FormGroup;

  @ViewChild('auditLog') auditLog : AuditLogComponent

  countries: string[] = [];
  states: string[] = [];
  cities: string[] = [];
  id: number;
  isEdit: boolean = false;
  isReadOnly: boolean = true;
  isView: boolean = true;
  show = false;
  editNow : boolean = false;
  loggedInUser : string;
  auditLogData : entityData;
  readorWrite: boolean=true;
  
  constructor(

    private fb: FormBuilder,
    private apiService: ApiService,
    private router:Router,
    private snack: MatSnackBar,
    private cdr: ChangeDetectorRef,
    private route : ActivatedRoute,
     private dataService : DataService,
     private ls : LocalStoreService,
    
  ) { }

  ngOnInit() {
    this.route.queryParamMap.subscribe((params: any) => {
      if(params.get('isEdit') == 'Yes'){
        this.isReadOnly = false;
        this.isView= false;
        this.editNow = true;
      }
      else if(params.get('isEdit') == 'No'){
        this.isReadOnly = true;
        this.isView= true;
        this.editNow = false;
      }
      else if(params.get('isEdit') == 'false'){
        this.isReadOnly = false;
        this.isView= false;
        this.editNow = false;
      }
      else if(params.get('isEdit') == null){
        this.isView= false;
        this.isReadOnly = false;
        this.editNow = true;
      }
      if(params.get('id')){
        this.isEdit = true;
        this.id = params.get('id');
        this.apiService.editEntityById(params.get('id')).subscribe((resp) =>{
           this.buildItemForm(resp);
           if( resp.authStatus=="U" ){
            resp.authDate =""
            resp.authBy=""
          }
           this.auditLogData = resp;
         console.log(this.auditLogData)
           this.dataService.setAuditLogData(this.auditLogData);
           this.dataService.sendClickEvent();
        });
      }
      else{
        this.readorWrite=false;
        this.buildItemForm({});
      }
    }
    );    
    this.buildItemForm({});
    this.getCountryName("country");
    this.loggedInUser = this.ls.getItem('ICUST_USER').userName;
  }
  buildItemForm(item) {
    this.entityMaintenance = this.fb.group({
      entityCode: [item.entityCode || '', Validators.required],
      entityName: [item.entityName || '', Validators.required],
      // oneTimeAuth: [item.oneTimeAuth || ''],
      // authStatus: [item.authStatus || ''],
      // recordStatus: [item.recordStatus || ''],
      version: [item.version || ''],
      denomRequired: [item.denomRequired || ''],
      mobile: [item.mobile || '', Validators.compose([Validators.required,Validators.pattern("^((\\+91-?)|0)?[0-9]{6,10}$")])],
      telephone: [item.telephone || '',Validators.pattern("^((\\+91-?)|0)?[0-9]{6,10}$")],
      country: [item.country || '',Validators.required],
      state: [item.state || '',Validators.required],
      city: [item.city || '',Validators.required],
      loggedInUser: [item.loggedInUser || ''],
      id: [item.id || ''],
      pincode: [item.pincode || '', Validators.compose([Validators.required,Validators.pattern("^((\\+91-?)|0)?[0-9]{6,10}$")])],
    });

    if(item.country){
      this.getStateName(item.country);
      this.getCityName(item.state);
    }

  }

  getCountryName(key: string) {
    this.apiService.getAllCountries().subscribe((data: any) => {
      console.log('resp',data)
      for(let i=0; i<data.length;i++){
        if(data[i].authStatus==='A'){
          console.log(data[i])
          this.countries.push(data[i].countryName);
          console.log(this.countries)
        }
      }
      // this.countries = data;
    });
  }

  getStateName(countryName){
    this.apiService.getAllCountries().subscribe((data: any) => {
      for(let i=0; i<data.length;i++){       
        if(data[i].countryName === countryName){
          this.apiService.getStateByCountry(data[i].id).subscribe((resp: any) => {
            for(let i=0; i<resp.length;i++){
              if(resp[i].authStatus==='A'){
                this.states.push(resp[i].stateName);
              }
            }
          })
          break;   
        }
      }
    })
    }
  getCityName(state: any) {
    let entityName = "city";
    this.apiService.getDropDownValues1(entityName, state).subscribe((data) => {
      this.cities = data;
    });
  }

  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteEntity(this.auditLogData.id)
        .subscribe(data => {
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
          this.router.navigate([`/maintenance/main/entity/list`]);
        
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
        payload.id = this.id;
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser;
        this.apiService.getAuditLogsForEntity(payload).subscribe((data) => {
          if(data.authStatus=="U" ){
            data.authDate =""
            data.authBy=""
          }
          this.auditLogData = data;
          this.dataService.setAuditLogData(this.auditLogData);
          this.dataService.sendClickEvent();
          let message = '';
          switch (operation) {
            case 'open':
              message = 'opened'
              break;
            case 'close':
              message = 'closed'
              break;
            case 'auth':
              message = 'authorized'
              break;
            default:
              break;
          }
          this.snack.open(`Record ${message} successful!'`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        });
      }
    }))
    
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  keyPressAlphanumeric(event) {
    var inp = String.fromCharCode(event.keyCode);
    if (/[0-9a-zA-Z]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }

  submit() {
    this.entityMaintenance.value.loggedInUser = this.loggedInUser;
    this.apiService.updateEntityDetails(this.entityMaintenance.value).subscribe(data => {
      if(data != null && this.isEdit == true){
        this.snack.open('Data Updated!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        
        this.cdr.markForCheck();
      }
      else if(data != null || this.isEdit == true){
        this.snack.open('Data Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        
        this.cdr.markForCheck();

      }
    })
    
    this.router.navigate([`/maintenance/main/entity/list`]);
  }

  
  goBack() {
    this.router.navigate([`/maintenance/main/entity/list`]);
  }
  dataByCountryCode(index,item){
    return item;
  }
  dataByStateCode(index,item){
    return item;
  }
  dataByCityCode(index,item){
    return item;
  }




}
