import {
  Component,
  ElementRef,
  OnInit,
  ViewChild,
  OnDestroy,
} from "@angular/core";
import { MatDialog, MatDialogRef } from "@angular/material/dialog";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Router } from "@angular/router";
import { ApiService } from "app/shared/services/api.service";
import { AppConfirmService } from "app/shared/services/app-confirm/app-confirm.service";
import { AppLoaderService } from "app/shared/services/app-loader/app-loader.service";
import { DataService } from "app/shared/services/data.service";
import { Subscription } from "rxjs";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort, Sort } from "@angular/material/sort";
import { LiveAnnouncer } from "@angular/cdk/a11y";
import { MatTableDataSource } from "@angular/material/table";
import { rumangoAnimations } from "app/shared/animations/rumango-animations";
import { DownloadService } from "app/shared/services/download.service";

import jsPDF from 'jspdf';
import autoTable from 'jspdf-autotable'
@Component({
  selector: "app-till-vault-summary",
  templateUrl: "./till-vault-summary.component.html",
  styleUrls: ["./till-vault-summary.component.scss"],
  animations: rumangoAnimations
})
export class TillVaultSummaryComponent implements OnInit {
  @ViewChild("searchVal") searchVal: ElementRef;
  selectedFilterIndex: number = 0;
  displayedColumns: string[] = ['entityCode','authStatus','recordStatus','action'];
  filterByOption: { key: string; value: string }[] = [
    { key: "entityCode", value: "Entity Code" },
  ];
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;

  dataSource = new MatTableDataSource();

  public items: any[];
  public users: any[];
  currency: string[] = [];
  till: any;
  branchCode: string[] = [];
  bankCode: string[] = [];
  public getItemSub: Subscription;
  fetchVaultConfig: any;
  loading: boolean;
  tillData: any;
  data: any;
  constructor(
    private dialog: MatDialog,
    private router: Router,
    private dataService: DataService,
    private snack: MatSnackBar,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,
    private apiService: ApiService,
    private _liveAnnouncer: LiveAnnouncer,
    private downloadService: DownloadService
  ) {}
  download() {  
    let doc = new jsPDF(); 
    const head = [['Entity Code', 'Auth Status', 'Record Status']] 
    const body=[];
    
    this.users.map(u=>{
      var row = [];
      row.push(u.entityCode);
      row.push(u.authStatus);
      row.push(u.recordStatus);
      body.push(row)
    });
    autoTable(doc, {
      head: head,
      body: body,
      didDrawCell: (prepare) => { },
    });
    doc.save('test.pdf');  
  }
  uploadtillvaultconfig(event) {

    const file:File = event.path[0].files[0];
      const uploadData = new FormData();
      uploadData.append('screenType', "tillVaultConfig");
      uploadData.append('file', file);
      

      this.apiService.getTillVaultConfigUpload(uploadData).subscribe(data => {
        if(uploadData != null){
          this.getvaultConfiguration();
          this.snack.open('File Uploaded Successfully!', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
       
        }
      
      // this.getTillVaultConfig();
      })
      //  err => {
      //   console.log("error", err)
      // }
    }
  
    upload(){
        
    }
  ngOnInit() {
    this.getItems();
    this.getTillId();
    this.getvaultConfiguration();
    this.getCurrency();
    this.getBankCode();
    this.getBranchCode();
  }
  ngOnDestroy() {
    if (this.getItemSub) {
      this.getItemSub.unsubscribe();
    }
  }
  getItems() {
    this.loading = true;
    this.getItemSub = this.apiService.gettillVaultResp().subscribe((data:any) => {
      for(let i=0;i<data.length;i++){
        if(data[i].authStatus == 'U'){
          data[i].authStatus = 'Unauthorized'
        }
        if(data[i].authStatus == 'A') {
          data[i].authStatus = 'Authorized'
        } 
        if(data[i].recordStatus == 'O'){
          data[i].recordStatus = 'Open'
        } 
        if(data[i].recordStatus == 'C'){
          data[i].recordStatus = 'Closed'
        }
      }
      this.items = data;
      this.dataSource = new MatTableDataSource(data);
      this.users = data;
      console.log(this.dataSource);
      this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    });
    this.loading = false;
  }
  // getTillVaultConfig(){
  //   this.apiService.getTillVaultConfigUpload().subscribe((data: any) => {
  //     this.users = data;
  //     console.log("this.users::", this.users);
  //     this.items = this.users;
  //     console.log("items4::", this.items);
  //     this.dataSource = new MatTableDataSource(data);
  //     this.dataSource.paginator = this.paginator;
  //     this.dataSource.sort = this.sort;
  //   });
  // }

  doFilter(filterBy, e) {
    this.filterTable(filterBy);
    this.applyFilter(e);
  }

  filterTable(filterBy) {
    this.dataSource.filterPredicate = (data: any, filter: string): boolean => {
      return data[filterBy].toLocaleLowerCase().includes(filter);
    };
  }

  clearFilter() {
    this.dataSource.filter = "";
    this.searchVal.nativeElement.value = "";
    this.selectedFilterIndex = 0;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  announceSortChange(sortState: Sort) {
    if (sortState.direction) {
      this._liveAnnouncer.announce(`Sorted ${sortState.direction}ending`);
    } else {
      this._liveAnnouncer.announce("Sorting cleared");
    }
  }

  addNew() {
    this.router.navigate(["/maintenance/main/tillVaultConfig/add"]);
  }

  openPopUp(row) {
    this.getTillVaultConfigById(row);
    // this.dataService.sendNavParam(this.fetchVaultConfig);
    this.router.navigate([`/maintenance/main/tillVaultConfig/${row.id}`]);
  }
  tillVaultConfiguration(){
    this.downloadService.downloadFiles("tillVaultConfiguration",this.users);
  }

  deleteItem(row) {
    this.confirmService
      .confirm({ message: `Delete ${row.entityCode}?` })
      .subscribe((res) => {
        if (res) {
          this.loader.open();
          this.apiService.removetillVaultConfig(row.id).subscribe(
            (res: any) => {},
            (err: any) => {
              ("");
              console.log(err);
              if (err.status == 200) {
                this.loader.close();
                this.getItems();
                this.snack.open("Record deleted!", "OK", { 
                  duration: 2000,
                  verticalPosition: "top",
                  horizontalPosition: "right",
                 });
              }
            }
          );
        }
      });
  }

  goBack() {
    this.router.navigate(["/maintenance/main"]);
  }

  getvaultConfiguration() {
    // this.loading = true;
    this.apiService.gettillVaultResp().subscribe((resp) => {
      console.log('resp',resp)
      this.users = resp;
      // this.items = this.users;
      // this.loading = false;
    });
  }
  getTillVaultConfigById(row) {
    // this.loader.open();
    this.apiService.fetchtillVaultConfigById(row.id).subscribe((resp) => {
      this.data = resp;
      if (this.data != null) {     

        this.tillData = this.till.find((x) => x.id == this.data.tillId);
        if (this.tillData != null) {
          this.data.tillId = this.tillData.tillVaultCode;
        }
        this.fetchVaultConfig = this.data;
      }
      if (this.fetchVaultConfig != null) {
        localStorage.setItem(
          "fetchVaultConfig",
          JSON.stringify(this.fetchVaultConfig)
        );
        //  this.loader.close();
      }
    });
  }
  getCurrency() {
    this.apiService.getCountryValues().subscribe((resp) => {
      this.currency = resp;
    });
  }
  getBankCode() {
    this.apiService.getBankCode().subscribe((resp) => {
      this.bankCode = resp;
    });
  }
  getBranchCode() {
    this.apiService.getBranchCode().subscribe((resp) => {
      this.branchCode = resp;
    });
  }
  getTillId() {
    let tillOrVault = "T";
    this.apiService.getTillData(tillOrVault).subscribe((resp) => {
      this.till = resp;
    });
  }

  dataByfilterByOption(index,filter)
{
  return filter.value;
}
}
