import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import Swal from 'sweetalert2';
import { auditLogInterface } from '../country-state-maintenance/country-state-popup/country-state-popup';
import { roleAuditLogInterface } from '../role-maintenance/add-role-maintenance.component';

@Component({
  selector: 'app-add-edit-security-policy',
  templateUrl: './add-edit-security-policy.component.html',
  styleUrls: ['./add-edit-security-policy.component.scss']
})
export class AddEditSecurityPolicyComponent implements OnInit {

  isEdit: boolean = false;
  loggedInUser : any;
  id : any;
  editNow: boolean = false;
  readorWrite: boolean=true;
  securityForm: FormGroup;
  auditData : roleAuditLogInterface;
  auditLogData : auditLogInterface;
  isAuditData : boolean = false
  currentUser = this.ls.getItem("ICUST_USER");

  constructor(private router: Router,
    private route: ActivatedRoute,
    private formBuilder: FormBuilder,
    private cdr: ChangeDetectorRef,
    private apiService: ApiService,
    private ls : LocalStoreService,
    private snack: MatSnackBar,
    private dataService : DataService,
  ) { }

  ngOnInit(): void {
    this.buildForm('')
    this.route.queryParamMap.subscribe((params: any) => {
      if(params.get('id')){
        this.isEdit = true;
        this.apiService.fetchSecurityPolicy(params.get('id')).subscribe((data) => {
          if(data){
             this.buildForm(data);
             console.log(data.version);
             this.auditLogData = data;
             this.dataService.setAuditLogData(this.auditLogData);
             this.dataService.sendClickEvent();
          }
        });
      }
      else{
        this.buildForm(null);
        this.readorWrite=false;
      }
    });
    this.loggedInUser = this.ls.getItem('ICUST_USER');
  }

  buildForm(item) {
    console.log(item);
    this.securityForm = this.formBuilder.group({
      activeEdit: [item ? item.isActive : ""],
      authStatus: [item ? item.authStatus : ""],
      authBy: [item ? item.authBy : ""],
      authDate: [item ? item.authDate : ""],
      maxInvLogins: [item ? item.maxInvLogins : "", Validators.required],
      maxPswdLength: [item ? item.maxPswdLength : "", Validators.required],
      minPswdLength: [item ? item.minPswdLength : "", Validators.required],
      modifiedBy: [item ? item.modifiedBy : ""],
      modifiedTime: [item ? item.modifiedTime : ""],
      loggedInUser: [""],
      notifyPasswordExpiryInDays: [
        item ? item.notifyPasswordExpiryInDays : "",
        Validators.required,
      ],
      firstTimeAuth: [item ? item.firstTimeAuth : ""],
      pswdComplexLcase: [item ? item.pswdComplexLcase : ""],
      pswdComplexNum: [item ? item.pswdComplexNum : ""],
      pswdComplexSplc: [item ? item.pswdComplexSplc : ""],
      pswdComplexUcase: [item ? item.pswdComplexUcase : ""],
      // pswdComplexity: [item?item.pswdComplexity :false],
      pswdExpiry: [item ? item.pswdExpiry : "", Validators.required],
      pswdReuseAft: [item ? item.pswdReuseAft : "", Validators.required],
      version: [item ? item.version : 0],
      passwordGenerationType: [item ? item.passwordGenerationType : "Manual"],
    });
   
  }

  onSave() {
    console.log(this.securityForm.value);
    this.securityForm.get('loggedInUser').setValue(this.currentUser.userName);
    this.apiService.upsertSecurityPolicy(this.securityForm.value).subscribe((resp) => {
      console.log(resp);
      this.snack.open('Record Saved Successfully', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
      this.router.navigate(["/maintenance/main/securityPolicy"]);
    });
  }

  editRecord(){
    this.readorWrite=false;
  }

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
    payload.id = this.auditLogData.version;
    payload.operation = operation;
    payload.loggedInUser = this.loggedInUser.userName;
    this.apiService.getAuditLogsSecurityPolicy(payload).subscribe((data) => {
      this.auditLogData = data;
      this.dataService.setAuditLogData(this.auditLogData);
      this.dataService.sendClickEvent();
      let message = '';
      switch (operation) {
        case 'open':
          message = 'opened'
          break;
        case 'close':
          message = 'closed'
          break;
        case 'auth':
          message = 'authorized'
          break;
        default:
          break;
      }
      this.snack.open(`Record ${message} successful!'`, "OK", {
        duration: 2000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
      }}))
    
  }

  goBack() {
    this.router.navigate(["/maintenance/main/securityPolicy"]);
  }

}
