import {
  Component,
  ElementRef,
  OnInit,
  ViewChild,
  OnDestroy,
} from '@angular/core';
import jsPDF from 'jspdf';
import 'jspdf-autotable';
//import * as XLSX from 'xlsx';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { rumangoAnimations } from 'app/shared/animations/rumango-animations';
import { ApiService } from 'app/shared/services/api.service';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { Subscription } from 'rxjs';
import { MatPaginator } from "@angular/material/paginator";
import { MatSort, Sort } from "@angular/material/sort";
import { LiveAnnouncer } from "@angular/cdk/a11y";
import { MatTableDataSource } from "@angular/material/table";
import autoTable from 'jspdf-autotable';
import { DownloadService } from "app/shared/services/download.service";
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { denomMaster } from 'app/shared/models/denomMaster';


@Component({
  selector: 'app-demon-master-crud-ngx-table',
  templateUrl: './demon-master-crud-ngx-table.component.html',
  styleUrls: ['./demon-master-crud-ngx-table.component.scss'],
  animations: rumangoAnimations,
})
export class DemonMasterCrudNgxTableComponent implements OnInit {

  @ViewChild('searchVal') searchVal: ElementRef;

  selectedFilterIndex: number = 0;
  displayedColumns: string[] = [
    'entityCode',
    'bankCode',
    'denomCode',
    'denomValue',
    'currency',
    'authStatus',
    'recordStatus',
    'action',
  ];
  filterByOption: { key: string; value: string }[] = [
    { key: 'entityCode', value: 'Entity Code' },
  ];
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  dataSource = new MatTableDataSource();
  public items: any[];
  public users: any[];
  public getItemSub: Subscription;
  denomvalues:string[] = [];
  currency:string[] = [];
  denomCode: string[] = [];
  denomValue: string[] = [];
  bankCode: string[] = [];
  currentUser: string;
  loading: boolean;
  fetchdenomValue: denomMaster;
  constructor(
    private dialog: MatDialog,
    private snack: MatSnackBar,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,
    private api:ApiService,
    private dataService:DataService,
    private router:Router,
    private ls:LocalStoreService,
    private _liveAnnouncer: LiveAnnouncer,
    private downloadService: DownloadService
  ) { }

  ngOnInit() {
    this.currentUser = this.ls.getItem('ICUST_USER');
    this.getBankCode();
    this.getCurrencyValue();
    this.getItems();
  }
  ngOnDestroy() {
    if (this.getItemSub) {
      this.getItemSub.unsubscribe();
    }
  }
  getItems() {
    this.loading = true;
    this.api.getDenomMaster().subscribe((allCountryValues: any) => {
      if (allCountryValues) {
        for(let i=0;i<allCountryValues.length;i++){
          if(allCountryValues[i].authStatus == 'U'){
            allCountryValues[i].authStatus = 'Unauthorized'
          }
          if(allCountryValues[i].authStatus == 'A') {
            allCountryValues[i].authStatus = 'Authorized'
          } 
          if(allCountryValues[i].recordStatus == 'O'){
            allCountryValues[i].recordStatus = 'Open'
          } 
          if(allCountryValues[i].recordStatus == 'C'){
            allCountryValues[i].recordStatus = 'Closed'
          }
        }
        this.dataSource = new MatTableDataSource(allCountryValues);
        this.users = allCountryValues;
        console.log(this.dataSource);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        this.loading = false;
      }
    });
  }
  getCurrencyValue() {
    this.api.getCountryValues().subscribe((currencyValues: any) => {
      this.currency = currencyValues;
    });
  }
  getBankCode() {
    this.api.getBankCode().subscribe((bnkCode: any) => {
      this.bankCode = bnkCode;
    });
  }

  doFilter(filterBy, e) {
    this.filterTable(filterBy);
    this.applyFilter(e);
  }

  filterTable(filterBy) {
    this.dataSource.filterPredicate = (data: any, filter: string): boolean => {
      return data[filterBy].toLocaleLowerCase().includes(filter);
    };
  }

  clearFilter() {
    this.dataSource.filter = '';
    this.searchVal.nativeElement.value = '';
    this.selectedFilterIndex = 0;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  announceSortChange(sortState: Sort) {
    if (sortState.direction) {
      this._liveAnnouncer.announce(`Sorted ${sortState.direction}ending`);
    } else {
      this._liveAnnouncer.announce('Sorting cleared');
    }
  }
  uploadCollateralType(event) {
    console.log("event",event);
  }

  denominationMaster(){
   this.downloadService.downloadFiles("denominationMaster", this.users);
   

}

  deleteItem(row) {
    this.confirmService
      .confirm({ message: `Delete ${row.entityCode}?` })
      .subscribe((res) => {
        if (res) {
          this.loader.open();

          
          this.api.deleteDenomMaster(row.id).subscribe(
            (res: any) => {},
            (err: any) => {
              ('');

              if (err.status == 200) {
                this.loader.close();
                this.getItems();
                this.snack.open('Member deleted!', 'OK', { duration: 4000 });
              }
            }
          );
        }
      });
  }

  addNew() {
    this.router.navigate(['/maintenance/main/denominationMaster/add']);
  }

  openPopUp(row) {

    this.getDenomMasterId(row)

    // this.dataService.sendNavParam(this.fetchdenomValue);
    this.router.navigate([`/maintenance/main/denominationMaster/${row.id}`]);
  }

  getDenomMasterId(row) {
    this.loader.open();

    this.api.fetchDenomMasterById(row.id).subscribe((data : any)=> {
      this.fetchdenomValue=data

      if(this.fetchdenomValue != null){
        localStorage.setItem('fetchdenomValue',JSON.stringify(this.fetchdenomValue));
         this.loader.close();
       }
      });

  }
  goBack() {
    this.router.navigate(['/maintenance/main/']);
  }


  dataByFilter(index,filter){
    return filter.key;
  }


}
