import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { tillVaultStatus } from 'app/shared/models/tillVaultStatus';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-till-vault-status',
  templateUrl: './till-vault-status.component.html',
  styleUrls: ['./till-vault-status.component.scss']
})
export class TillVaultStatusComponent implements OnInit {

  arrayCurrency: string[] = [];
  public tillVaultForm: FormGroup;
  tillVaultResponse: any;
  Id: number;
  currentUser: string;
  vaultStatus: any = {};
  // vaultStatus: {};
  bankCode: string[] = [];
  till: any;
  entity: string[] = [];
  branchCode: string[] = [];
  tillData: any;
  loggedInUser : any;
  auditData : any;
  isAuditData : boolean = false;
  id: any;
  isEdit: boolean = false;
  readorWrite: boolean=true;
  tillVaultStatusDetails:tillVaultStatus[] = []
  constructor(
    // @Inject(MAT_DIALOG_DATA) public data: any,
    // public dialogRef: MatDialogRef<TillVaultStatusComponent>,
    private fb: FormBuilder,
    private apiService: ApiService,
    private loader: AppLoaderService,
    private snack: MatSnackBar,
    private router:Router,
    private ls:LocalStoreService,
    private dataService:DataService,
  ) { }

  ngOnInit() {
    this.getBankCode();
    this.getBranchCode();
    this.getTillId();
    this.getEntityCode();
    this.currentUser = this.ls.getItem("ICUST_USER");
    // this.dataService.getTillVaultStatus.subscribe(data =>{
    //   console.log(data)
    //   this.vaultStatus = typeof this.vaultStatus === 'object' ? data : {};
    // });
    // this.buildItemForm(this.vaultStatus);

    setTimeout(() => {  
      this.vaultStatus = JSON.parse(localStorage.getItem('fetchVaultStatus'));
      if(this.vaultStatus == null || this.vaultStatus == undefined){
        this.readorWrite=false;
        this.buildItemForm({})
      }       
      else if(this.vaultStatus.id != null || this.vaultStatus.id != undefined){
      this.loader.open();
      this.id = this.vaultStatus.id;
      this.buildItemForm(this.vaultStatus)
      this.auditData = this.vaultStatus
      console.log( this.auditData)
      this.isEdit = true;
      this.isAuditData = true;
      this.dataService.setAuditLogData(this.auditData);
      this.loader.close();
      }
    }, 1000)
    this.loggedInUser = this.ls.getItem('ICUST_USER');
  }

  buildItemForm(item) {
    this.tillVaultForm = this.fb.group({
      bankCode: [item.bankCode || '', [Validators.required]],
      branchCode: [item.branchCode || '', [Validators.required, Validators.minLength(1), Validators.maxLength(6)]],
      entityCode: [item.entityCode || '', [Validators.required]],
      tillId: [item.tillId || '', [Validators.required, Validators.minLength(1), Validators.maxLength(19)]],
      userId: [item.userId || '', [Validators.required, Validators.minLength(1), Validators.maxLength(255)]],
      tillVaultStatus: [item.tillVaultStatus || '', [Validators.required, Validators.minLength(1), Validators.maxLength(5)]],
      tvOpenTime : [new Date(item.tvOpenTime) || '', [Validators.required]],
      tvClosingTime : [new Date(item.tvClosingTime) || '', [Validators.required]],
      id: [item.id || null],
      loggedInuser : [item.loggedInUser || '']
    })
  }

  alphakeyPress(event: any) {
    const pattern = /[0-9a-zA-Z]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteTillVaultStatusById(this.auditData.id)
        .subscribe(data => {
          this.router.navigate([`/maintenance/main/tillVaultStatus`]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
          this.router.navigate([`/maintenance/main/tillVaultStatus`]);
        
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
    payload.id = this.id;
    payload.operation = operation;
    payload.loggedInUser = this.loggedInUser.userName;
    this.apiService.getAuditLogsFortillvaultStatus(payload).subscribe((data) => {
      this.auditData = data;
      this.dataService.setAuditLogData(this.auditData);
      this.isAuditData = true;
      this.dataService.sendClickEvent();
      let message = '';
      switch (operation) {
        case 'open':
          message = 'opened'
          break;
        case 'close':
          message = 'closed'
          break;
        case 'auth':
          message = 'authorized'
          break;
        default:
          break;
      }
      this.snack.open(`Record ${message} successfully!'`, "OK", {
        duration: 2000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
      }}))
    
  }

  submit() {
    let  openTime = this.convertDate(this.tillVaultForm.controls.tvOpenTime.value);
    this.tillVaultForm.value['tvOpenTime'] = openTime;

    let  closeTime = this.convertDate(this.tillVaultForm.controls.tvClosingTime.value);
    this.tillVaultForm.value['tvClosingTime'] = closeTime;

    if(this.tillVaultForm.invalid){
      return;
    }else{
      this.loader.open();
      // this.tillVaultForm.value.tvOpenTime =this.convertDate(this.tillVaultForm.value.tvOpenTime);
      // this.tillVaultForm.value.tvClosingTime =this.convertDate(this.tillVaultForm.value.tvClosingTime);
      this.tillData = this.till.find(x => x.tillVaultCode == this.tillVaultForm.value.tillId);
      this.tillVaultForm.value.tillId = this.tillData.id;
      this.tillVaultForm.value.loggedInUser = this.loggedInUser.userName;

      this.apiService.upsertTillVaultStatus(this.tillVaultForm.value).subscribe((resp) => {
      this.loader.close();
      this.router.navigate([`/maintenance/main/tillVaultStatus`]).then(_=>{
          this.snack.open('Data Saved Successfully!', 'OK', 
          { duration: 4000 , 
            verticalPosition: "top",
            horizontalPosition: "right",});
            localStorage.removeItem('fetchVaultStatus');
        });
      },(err:any) =>{
        this.loader.close();
        this.snack.open(`Error!`, 'OK', { duration: 4000,  
          verticalPosition: "top",
          horizontalPosition: "right",});
        }
      )
      }  
    // let payloadObj : any = {};
    // payloadObj = this.tillVaultForm.value;
    // payloadObj.id = this.Id;
    // this.dialogRef.close(payloadObj);
  }

  goBack() {
    localStorage.removeItem('fetchVaultStatus');
    this.router.navigate([`/maintenance/main/tillVaultStatus`]);
  }

  getBankCode() {
    this.apiService.getBankCode().subscribe((resp) => {
      for(let i=0; i<resp.length;i++){
        if(resp[i].authStatus==='A'){
          this.bankCode.push(resp[i]);
        }
      }
      // this.bankCode = resp;
    });
  }
  getBranchCode() {
    this.apiService.getBranchCode().subscribe((resp) => {
      for(let i=0; i<resp.length;i++){
        if(resp[i].authStatus==='A'){
          this.branchCode.push(resp[i]);
        }
      }
      // this.branchCode = resp;
    });
  }
  getTillId() {
  let tillOrVault ="T";
  this.apiService.getTillData(tillOrVault).subscribe((resp) => {
    this.till = resp;
 });
  }

  getEntityCode() {
    this.apiService.getEntityCode().subscribe((resp) => {
      for(let i=0; i<resp.length;i++){
        if(resp[i].authStatus==='A'){
          this.entity.push(resp[i]);
        }
      }
      // this.entity = resp;
    });
  }

  convertDate(value) {
    let date = moment(value).format("YYYY-MMM-DD HH:mm:ss");
    if (date)
      return date;
  }

  trackByEntityCode(data){
    return data.entityCode;
  }

  trackByBankCode(data){
    return data.bankCode;
  }

  trackByBranchCode(data){
    return data.branchCode;
  }

  trackByTill(data){
    return data.tillVaultCode;
  }
}
