import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';

@Component({
  selector: 'app-security-policy',
  templateUrl: './security-policy.component.html',
  styleUrls: ['./security-policy.component.scss']
})
export class SecurityPolicyComponent implements OnInit {

  dataSource = new MatTableDataSource();
  recordExist : number = 0;
  displayedColumns: string[] = [ 'version', 'createdBy', 'authStatus', 'recordStatus','authDate' ,'action'];

  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;

  constructor(private router: Router,private apiService: ApiService,private confirmService: AppConfirmService,
    private loader: AppLoaderService,private snack: MatSnackBar,) { }

  ngOnInit(): void {
    this.fetchSecurityPolicy();
  }
  

  goBack(){
    this.router.navigate(["/maintenance/main"]);
  }

  addNew(){
    this.router.navigate(["/maintenance/main/addEditsecurityPolicy"]);
  }

  editPolicy(id){
    this.router.navigate(["/maintenance/main/addEditsecurityPolicy"],{ queryParams: { id: id } });
  }
    
  deleteItem(Record) {
    this.confirmService.confirm({message: `Delete ${Record.version}?`})
      .subscribe(res => {
        if (res) {
          console.log(res)
          this.apiService.deleteSecurityPolicy(Record.version)
            .subscribe(data => {
            },    err=>{
              if (err.status == 200) {
                this.snack.open('Record Deleted Successfully', 'OK', {
                  duration: 4000,
                  verticalPosition: 'top',
                  horizontalPosition: 'right'
                });
              } 
              this.fetchSecurityPolicy();
          })
        }
      })
  }
  

  fetchSecurityPolicy(){
    this.apiService.getSecurityPolicyDetails().subscribe((resp) =>{
      console.log("SecurityPolicy---",resp);
      this.dataSource = resp;
      this.recordExist = resp.length;
     })


}
}
