import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AuditLogComponent } from '../../audit-log/audit-log.component';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-add-currency-maint',
  templateUrl: './add-currency-maint.component.html',
  styleUrls: ['./add-currency-maint.component.scss']
})
export class AddCurrencyMaintComponent implements OnInit {
  public currencyMaint: FormGroup;
  @ViewChild('auditLog') auditLog : AuditLogComponent
  currencyMaintIsEdit: any;
  isEdit: boolean = false;
  
  loggedInUser : any;
  auditLogData : any;
  countries: string[] = [];
  currencyId: any;
  readorWrite: boolean = true;

  constructor(
    private fb: FormBuilder,
    private route : ActivatedRoute,
    private apiService: ApiService,
    private dataService : DataService,
    private ls : LocalStoreService,
    private snack: MatSnackBar,
    private cdr: ChangeDetectorRef,
    private router:Router,
  ) { }

  ngOnInit() {
    this.buildItemForm({});
    this.route.queryParamMap.subscribe((params: any) => {
      console.log(params);
      this.currencyId = JSON.parse(params.get('currencyId'));
      this.currencyMaintIsEdit = params.get('isEdit');
      console.log("this.currencyMaintIsEdit",this.currencyMaintIsEdit);
      if(this.currencyId){
        this.isEdit = true;
        console.log(this.currencyId);
        this.apiService.editCurrencyMaintById(this.currencyId).subscribe((resp) =>{
           console.log(resp);
           this.buildItemForm(resp);
           this.auditLogData = resp;
           this.dataService.setAuditLogData(this.auditLogData);
           this.dataService.sendClickEvent();
        });
      }
      else{
        this.readorWrite=false;
        this.buildItemForm({});
      }
    });
    this.getCountryName("country");
    this.cdr.markForCheck();
    this.loggedInUser = this.ls.getItem('ICUST_USER');
    console.log(this.loggedInUser);
  }

  buildItemForm(item) {
    this.currencyMaint = this.fb.group({
      currencyCode: [item.currencyId ? item.currencyId.currencyCode : '', Validators.required],
      currencyName: [item ? item.currencyName : '', Validators.required],
      country: [item.currencyId ? item.currencyId.country : '', Validators.required],
      isoCcyCode: [item ? item.isoCcyCode : '', Validators.required]
    });
  }

  getCountryName(key: string) {
    this.apiService.getAllCountries().subscribe((data: any) => {
      console.log('resp',data)
      for(let i=0; i<data.length;i++){
        if(data[i].authStatus==='A'){
          this.countries.push(data[i].countryName);
        }
      }
      // this.countries = data;
    });
  }

  submit() {
    this.currencyMaint.value.loggedInUser = this.loggedInUser.userName;
    console.log(this.currencyMaint.value);
    this.apiService.upsertCurrencyMaintDetails(this.currencyMaint.value).subscribe(data => {
      console.log("data",data);
      if(data && this.isEdit == false ){
        this.snack.open('Data Created Successfully!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.router.navigate([`/maintenance/main/currencyMaint`]);
        this.cdr.markForCheck();
      }
     else if(data != null && this.isEdit == true){
        this.snack.open('Data Updated!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.router.navigate([`/maintenance/main/currencyMaint`]);
        this.cdr.markForCheck();
      }
      else if(data != null && this.isEdit == false){
        this.snack.open('Data Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.router.navigate([`/maintenance/main/currencyMaint`]);
        this.cdr.markForCheck();

      }
      else {
        this.snack.open('Save Failed! Cannot add duplicate records', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      }
    })
    
  }

  goBack() {
    this.router.navigate([`/maintenance/main/currencyMaint`]);
  }

  trackByCountry(country){
    return country;
  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteCurrencyMaint(this.auditLogData.currencyId)
        .subscribe(data => {
            this.router.navigate([`/maintenance/main/currencyMaint`]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
            this.router.navigate([`/maintenance/main/currencyMaint`]);
        
      })
      }}))
   
         
       
  }

 editRecord(){
 this.readorWrite=false;
 }

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        console.log("operation::",operation);
        console.log(this.currencyMaint.value.currencyCode);
        let payload: any = {};
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser.userName;
        payload.currencyId = {};
        payload.currencyId.currencyCode = this.currencyMaint.value.currencyCode;
        payload.currencyId.country = this.currencyMaint.value.country;
        console.log("payload -- ",payload);
        this.apiService.updateAuditLogForCurrencyMaint(payload).subscribe((data) => {
          console.log(data);
          this.auditLogData = data;
          this.dataService.setAuditLogData(this.auditLogData);
          this.dataService.sendClickEvent();
          let message = '';
          switch (operation) {
            case 'open':
              message = 'opened'
              break;
            case 'close':
              message = 'closed'
              break;
            case 'auth':
              message = 'authorized'
              break;
            default:
              break;
          }
          this.snack.open(`Record ${message} successful!'`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        });
      }}))
    
  }
}
