  getItems() {
    this.apiService.fetchRoleSummary().subscribe((roleInfo : any)=> {
      if(roleInfo){  
        for(let i=0;i<roleInfo.length;i++){
          if(roleInfo[i].authStatus == 'U'){
            roleInfo[i].authStatus = 'Unauthorized'
          }
          if(roleInfo[i].authStatus == 'A') {
            roleInfo[i].authStatus = 'Authorized'
          } 
          if(roleInfo[i].recordStatus == 'O'){
            roleInfo[i].recordStatus = 'Open'
          } 
          if(roleInfo[i].recordStatus == 'C'){
            roleInfo[i].recordStatus = 'Closed'
          }
          if(roleInfo[i].oneTimeAuth == 'N'){
            roleInfo[i].oneTimeAuth = 'No'
          }
          if(roleInfo[i].oneTimeAuth == 'Y'){
            roleInfo[i].oneTimeAuth = 'Yes'
          }
          
        }
      this.currencyPairMaintValues = roleInfo;
        this.items =  this.currencyPairMaintValues;
        console.log("items4::", this.items);
        this.dataSource = new MatTableDataSource(roleInfo);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      }
      });
  
  }

  onFileChange(event) {
    console.log('event', event.path[0].files[0]);
    const file: File = event.path[0].files[0];
    const uploadData = new FormData();
    uploadData.append('screenType', 'currencyPair');
    uploadData.append('file', file);
    this.apiService.uploadExelFile(uploadData).subscribe((response) => {
      this.getItems();
      console.log(response);
      this.snack.open(`File Uploaded Successfully`, "OK", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
    
  }
  fetchRole(){
    this.apiService.fetchRoleById(this.id).subscribe((resp) =>{
     console.log("currencyPairById",resp);
     this.fetchRoleById=resp;
    })
    console.log(this.fetchRoleById)
    this.dataService.sendNavParam(this.fetchRoleById);
    this.router.navigate([`/maintenance/main/currencyPairMaintenance/addCurrencyPair/${this.id}`]);
  }
