import { ChangeDetectorRef, Component, Inject, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ApiService } from 'app/shared/services/api.service';
import { TillVaultMasterSummaryComponent } from '../../till-vault-master-summary/till-vault-master-summary/till-vault-master-summary.component';
import { ActivatedRoute, Router } from '@angular/router';
import { MatSnackBar } from "@angular/material/snack-bar";
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AuditLogComponent } from 'app/views/maintenance/audit-log/audit-log.component';
import { tillVaultMaster } from 'app/shared/models/tillvaultmaster';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-till-vault-master-popup',
  templateUrl: './till-vault-master-popup.component.html',
  styleUrls: ['./till-vault-master-popup.component.scss']
})
export class TillVaultMasterPopupComponent implements OnInit {
  @ViewChild('auditLog') auditLog : AuditLogComponent
  public itemForm: FormGroup;
  tillVaultForm:FormGroup;
  isReadOnly: boolean = true;
  isEdit: boolean = false;
  isView: boolean = true;
  show = false;
  editNow : boolean = false;
  entityIsEdit: boolean;
  entityCode: any;
  id: number;
  data:any;
  bankCode: any;
  entity: any;
  loggedInUser : any;
  auditData : any;
  isAuditData : boolean = false;
  readorWrite: boolean=true;


  constructor(
    // @Inject(MAT_DIALOG_DATA) public data: any,
    // public dialogRef: MatDialogRef<TillVaultMasterSummaryComponent>,
     private router:Router, private snack: MatSnackBar,
    private fb: FormBuilder,private api:ApiService, private route : ActivatedRoute,
      private cdr: ChangeDetectorRef, private dataService : DataService,    private ls : LocalStoreService,
      private apiService:ApiService,
  ) { }

  ngOnInit() {
    this.getEntityDropdown();
    this.getBankCode();
    this.buildItemForm({});
    

    this.route.queryParamMap.subscribe((params: any) => {
      this.entityIsEdit = params.get('isEdit');//save
      if(params.get('isEdit') == 'Yes'){
         this.isReadOnly = false;
       this.isView= true;
       this.editNow = true;
      }
      else if(params.get('isEdit') == 'No'){
         this.isReadOnly = true;
         this.isView= true;
        this.editNow = false;
      }
      else if(params.get('isEdit') == 'false'){
        this.isReadOnly = false;
        this.isView= false;
        this.editNow = false;
      }
      else if(params.get('isEdit') == null){
      this.isView= false;
      }
      if(params.get('id')){
        this.isEdit = true;
        this.id = params.get('id');
        this.api.editEntityById(params.get('id')).subscribe((resp) =>{
           this.buildItemForm(resp);
        });
        this.api.getTillVaultById(params.get('id')).subscribe((resp:any) =>{
          this.buildItemForm(resp);
          if( resp.authStatus=="U" ){
            resp.authDate =""
            resp.authBy=""
          }
          this.auditData = resp;
          this.isAuditData = true;
          this.dataService.setAuditLogData(this.auditData);
       });
      }
      else{
        this.readorWrite=false;
        this.buildItemForm({});
      }

    }

    ); 
   
    this.loggedInUser = this.ls.getItem('ICUST_USER').userName;
   

  }
 
  buildItemForm(item) {
    this.itemForm = this.fb.group({
      authStatus: [item.authStatus || ''],
      bankCode: [item.bankCode || '',[Validators.required]],
      tillVaultCode:[item.tillVaultCode || '',[Validators.required]],
      entityCode:[item.entityCode || '',[Validators.required]], 
      tillVaultDesc:[item.tillVaultDesc || '',[Validators.required]],
      isActive:[item.isActive || true],
      tillOrVault:[item.tillOrVault || '',[Validators.required]],
      id: [item.id || ''],
      oneTimeAuth:[item.oneTimeAuth || ''],
      recordStatus:[item.recordStatus || ''],
      version:[item.version || ''],
      primaryVault:[item.primaryVault || '',[Validators.required]],
      loggedInUser:[item.loggedInUser || '']
  
    });
    // this.itemForm.get('entityCode').setValue(this.entityCode);
  
  }

  goBack() {
    this.router.navigate([`/maintenance/main/tillVaultltMaster`]);
  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteTillVaultMaster(this.auditData.id)
        .subscribe(data => {
          this.router.navigate([`/maintenance/main/tillVaultltMaster`]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
          this.router.navigate([`/maintenance/main/tillVaultltMaster`]);
        
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
    payload.id = this.id;
    payload.operation = operation;
    payload.loggedInUser = this.loggedInUser;
    this.apiService.getAuditLogsFortillvaultMaster(payload).subscribe((data) => {         
      if( data.authStatus=="U" ){
      data.authDate = ""
      data.authBy = ""
      }

      this.auditData = data;
      this.dataService.setAuditLogData(this.auditData);
      this.isAuditData = true;
      this.dataService.sendClickEvent();
      let message = '';
      switch (operation) {
        case 'open':
          message = 'opened'
          break;
        case 'close':
          message = 'closed'
          break;
        case 'auth':
          message = 'authorized'
          break;
        default:
          break;
      }
      this.snack.open(`Record ${message} successfully!'`, "OK", {
        duration: 2000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
      }}))
    
  }
  
  submit() {
    if(this.itemForm.invalid){
    return;
    }else{
    this.itemForm.value.loggedInUser = this.loggedInUser;
    this.api.saveTillVaultMaster(this.itemForm.value).subscribe(data => {
      if(data != null && this.isEdit == true){
        this.router.navigate([`/maintenance/main/tillVaultltMaster`]).then(_=>{
                this.snack.open('Data Updated!', 'OK', 
                { duration: 4000 , 
                  verticalPosition: "top",
                  horizontalPosition: "right",});
                });       
        this.cdr.markForCheck();
      }
      else if(data != null || this.isEdit == true){
       // 
        this.router.navigate([`/maintenance/main/tillVaultltMaster`]).then(_=>{
          this.snack.open('Data Saved!', 'OK', 
          { duration: 4000 , 
            verticalPosition: "top",
            horizontalPosition: "right",});
          });
        this.cdr.markForCheck();
       }
    }) 
    }  
  }

  getEntityDropdown(){
    this.api.getEntityDropdown().subscribe((data) => {
      this.entityCode = data;
    });
    
  }
  getBankCode(){
    this.api.getBankCode().subscribe(( bankCode: any)=> {
      this.bankCode=bankCode;
      });
  }

  getEntityCode() {
    this.api.getEntityCode().subscribe((resp) => {
      this.entity = resp;
    });
  }
 
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  keyPressAlphanumeric(event) {
    var inp = String.fromCharCode(event.keyCode);
    if (/[0-9a-zA-Z]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }
  dataByEntity(index,entityC){
    return entityC.entityCode;
  }
  dataByBank(index,bankC){
    return bankC.bankCode;
  }

}
