import { Pipe, PipeTransform } from '@angular/core';
import { Card } from 'app/views/maintenance/main-home/main-home.component';
const { isArray } = Array;

@Pipe({
  name: 'filter'
})
export class FilterPipe implements PipeTransform {
  transform(DATA: Card[], title: string): Card[] {
    if(!DATA ) return [];
    if(!title) return DATA;
    title = title.toLowerCase();
    console.log(DATA, title);
    return search( DATA, title);
   }
}

function search(entries: any[], search: string) {

  search = search.toLowerCase();

  return entries.filter(function (obj) {
    const keys: string[] = Object.keys(obj);
    return keys.some(function (key) {
      const value = obj.title;
      console.log(obj[key]);
      if (isArray(value)) {
        return value.some(v => {
          return v.toLowerCase().includes(search);
        });
      }
      else if (!isArray(value)) {
        return value.toLowerCase().includes(search);
      }
    })
  });
}
