import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { ApiService } from 'app/shared/services/api.service';
import { Location } from '@angular/common';
import { ThisReceiver } from '@angular/compiler';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { DataService } from 'app/shared/services/data.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import Swal from 'sweetalert2';

@Component({ 
  selector: 'app-state-popup',
  templateUrl: './state-popup.component.html',
  styleUrls: ['./state-popup.component.scss']
})
export class StatePopupComponent implements OnInit {
  id: any;
  isEdit: boolean = false;
  showLoader : boolean = false;
  isStatecode: boolean = false;
  isStatecode2: boolean = false;
  countryIsEdit : any;
  countryId : any;
  loggedInUser : any;
  auditLogData : any;
  selectedCountryCode:any;
  readorWrite: boolean=true;


  public stateMaintenance: FormGroup;
  countryIdentity: any;

  constructor(

    private fb: FormBuilder,
    private apiService: ApiService,
    private _location: Location,
    private router: Router,
    private route: ActivatedRoute,
    private dataService : DataService,
    private ls : LocalStoreService,
    private snack: MatSnackBar,
  ) { }

  ngOnInit() {
    this.route.queryParamMap.subscribe((params: any) => {
      this.countryId = params.get('countryId');
      this.countryIsEdit = params.get('countryIsEdit');
      this.selectedCountryCode = params.get('selectedCountryCode');
      this.apiService.getCountryById(params.get('countryId')).subscribe((resp:any) =>{
        this.countryIdentity=resp.id;
      });
      if (params.get('id')) {
        this.isEdit = true;
        this.id = params.get('id');
        this.apiService.getStateById(params.get('id')).subscribe((resp) => {
          this.showLoader = true;
          this.buildItemForm(resp);
          this.showLoader = false;
          this.auditLogData = resp;
          this.dataService.setAuditLogData(this.auditLogData);
        });
      }
      else {
        this.readorWrite=false;
        this.showLoader = true;
        this.buildItemForm({});
        this.showLoader = false;
      }
    }
    );
    this.loggedInUser = this.ls.getItem('ICUST_USER');
  }
  isStatePresent(data: any) {
    this.apiService.isStateCodePresent(data).subscribe((respo) => {
      if(respo == true){
      this.isStatecode = true;
      }else{
        this.isStatecode = false;
      }
    });
  }

  isStateCode2Present(data: any){
    this.apiService.isStateCode2Present(data).subscribe((respo) => {
      if(respo == true){
      this.isStatecode2 = true;
      }else{
        this.isStatecode2 = false;
      }
    });

  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteState(this.auditLogData.id)
        .subscribe(data => {
         this.router.navigate(["/maintenance/main/countryStatepopup"],{ queryParams: { id: this.countryId, isEdit : this.countryIsEdit } });
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
         this.router.navigate(["/maintenance/main/countryStatepopup"],{ queryParams: { id: this.countryId, isEdit : this.countryIsEdit } });
        
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }

  updateRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){ 
        let payload : any = {};
    payload.id = this.id;
    payload.operation = operation;
    payload.loggedInUser = this.loggedInUser.userName;
    this.apiService.getAuditLogsForState(payload).subscribe((data) => {
      this.auditLogData = data;
      this.dataService.setAuditLogData(this.auditLogData);
      this.dataService.sendClickEvent();
      let message = '';
      switch (operation) {
        case 'open':
          message = 'opened'
          break;
        case 'close':
          message = 'closed'
          break;
        case 'auth':
          message = 'authorized'
          break;
        default:
          break;
      }
      this.snack.open(`Record ${message} successful!'`, "OK", {
        duration: 2000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
      }}))
    
  }

  submit(data: any) {
    let details: any = {};
    details = this.stateMaintenance.value;
    details.id = this.id;
    data.countryId=this.countryIdentity;
    data.loggedInUser = this.loggedInUser.userName;

    this.apiService.upsertStateDetails(data).subscribe((resp) => {
      if (resp) {
        this.router.navigate(["/maintenance/main/countryStatepopup"],{ queryParams: { id: this.countryId, isEdit : this.countryIsEdit } });
      }
    });
  }

  keyPressAlphanumeric(event) {
    var inp = String.fromCharCode(event.keyCode);
    if (/[0-9a-zA-Z]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }

  buildItemForm(item) {
    this.stateMaintenance = this.fb.group({
      countryCode: [this.selectedCountryCode?this.selectedCountryCode:item.countryCode, Validators.required],
      countryId: [this.countryId? this.countryId : item.countryId, Validators.required],
      stateCode: [item.stateCode || '', Validators.required], //dropdown
      stateName: [item.stateName || '', Validators.required],
      stateCode2: [item.stateCode2 || '', Validators.required],  //dropdown
      stateNumCode: [item.stateNumCode || '', Validators.required],
      recordStatus: [item.recordStatus || '']
    });
  }

  goBack() {
    this.router.navigate(["/maintenance/main/countryStatepopup"],{ queryParams: { id: this.countryId, isEdit : this.countryIsEdit } });

  }

}
