import { ChangeDetectorRef, Component, ElementRef, Inject, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { MatAutocompleteTrigger } from '@angular/material/autocomplete';
import { MAT_DIALOG_DATA, MatDialogRef, MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';


@Component({
  selector: 'app-holiday-list-dialog',
  templateUrl: './holiday-list-dialog.component.html',
  styleUrls: ['./holiday-list-dialog.component.scss']
})
export class HolidayListDialogComponent implements OnInit {
  holidayList: any;
  myControl = new FormControl();
  markAsHoliday: boolean = false;
  disableSave: boolean;
  dateUpdated: any;


  constructor(@Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<HolidayListDialogComponent>, private cdr: ChangeDetectorRef,
    private formBuilder: FormBuilder,
    private snack: MatSnackBar,
    private ls:LocalStoreService,
    public dialog: MatDialog, private apiService: ApiService) { }
  date: Date;
  isEdit: boolean = false;
  addBranchHoliday: FormGroup;
  options: string[] = ['National', 'Regional', 'Bank', 'Common'];
  filteredOptions: Observable<string[]>;
  ngOnInit(): void {
    if (this.data.date) {
      this.date = this.data.date;
    }

    this.fetchBranchHolidayByCalendarDateDetails();

    this.filteredOptions = this.myControl.valueChanges.pipe(
      startWith(''),
      map(value => this._filter(value)),
    );

    if (Object.keys(this.data.holidayData).length != 0) {
      this.buildItemForm(this.data.holidayData);
    } else {
      this.buildItemForm({});
    }
  }
  buildItemForm(item) {
    this.addBranchHoliday = this.formBuilder.group({
      bankCode: [item.bankCode || ''],
      branchCode: [item.branchCode || ''],
      calenderYear: [item.calenderYear || ''],
      entityCode: [item.entityCode || ''],
      description: [''],
      holidayType: [''],
    })
  }

  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();

    return this.options.filter(option => option.toLowerCase().includes(filterValue));
  }

  onCheckOfMarAsHoiday() {
    this.markAsHoliday = !this.markAsHoliday;
  }

  reset(addBranchHoliday) {
    this.addBranchHoliday.get('description').setValue('');
    this.addBranchHoliday.get('holidayType').setValue('');
    // this.myControl.setValue(undefined);
    // this.filteredOptions = this.myControl.valueChanges.pipe(
    //   startWith(''),
    //   map(value => this._filter(value)),
    // );
    this.markAsHoliday = false;
  }




  fetchBranchHolidayByCalendarDateDetails() {
    this.apiService.fetchBranchHolidayByCalendarDateDetails()
      .subscribe(holidayList => {
        this.holidayList = holidayList;
      });
  }



  saveHoliday(addBranchHoliday) {

    if (this.data.action === 'UPDATE') {
      this.isEdit = true;
    }else{
      this.isEdit = false;
    }
   
    console.log('addBranchHoliday :: ', addBranchHoliday, this.date);
    this.ls.setItem('holidayType',addBranchHoliday.holidayType)
    let details: any[] = [];
    var responseDate = moment(this.date).format('YYYY-MM-DD');
    let payload = {
      entityCode: addBranchHoliday.entityCode,
      bankCode: addBranchHoliday.bankCode,
      branchCode: addBranchHoliday.branchCode,
      calenderYear: addBranchHoliday.calenderYear,
      description: addBranchHoliday.description,
      holidayType: addBranchHoliday.holidayType,
      calenderDate: responseDate
    }
    details.push(payload);
    console.log('details :: ', details);
    this.dateUpdated = details;

    this.apiService.updateBranchHolidayDetails(details, this.isEdit)
      .subscribe(data => {
        this.apiService.setHolidayData(data);
        if (data != null) {
          this.disableSave = true;
          this.snack.open('Data Saved' + ' !', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
          this.cdr.markForCheck();
          this.dateUpdated = responseDate;
        }
        this.goToClose();

      }, error => {
        this.disableSave = true;
        this.snack.open(`${error.error}` + ' !', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right',
          panelClass: 'snackbar-error'
        });

      });
  }
  goToClose() {
    this.dialogRef.close({
      message: 'Confirm',
      dateUpdated: this.dateUpdated
    });
  }

}
