@Component({
  selector: 'app-main-home',
  templateUrl: './main-home.component.html',
  styleUrls: ['./main-home.component.scss'],
})
export class MainHomeComponent implements OnInit {
  filteredStatus: string = '';
  @ViewChild(MatPaginator) paginator: MatPaginator;
  obs: Observable<any>;
  dataSource: MatTableDataSource<Card> = new MatTableDataSource<Card>(DATA);
  maintenanceMenus: any = [];

  compPermissions: String[] = [];

  constructor(
    private cdr: ChangeDetectorRef,
    private router: Router,
    private apiService: ApiService,
    private dataService: DataService,
    private jwtService: JwtAuthService
  ) {}

  ngOnInit() {
    document.getElementById('main-content-wrap').style.backgroundColor =
      'white';
    // this.cdr.detectChanges();
    this.dataSource.paginator = this.paginator;
    this.obs = this.dataSource.connect();

    this.getMainMenus();

    var user = this.jwtService.getUser();
    if (user) {
      this.apiService
        .fetchTellerProfileInfo(user.userId)
        .subscribe((result) => {
          // this.compPermissions = result?.icustRoleInfoModel?.childPermission
          //   .split(',')
          //   .reduce(
          //     (compArr: String[], comp: String) =>
          //       compArr.includes(comp.trim())
          //         ? compArr
          //         : [...compArr, comp.trim()],
          //     []
          //   );
        });
    }
  }

  ngOnDestroy() {
    document.getElementById('main-content-wrap').style.backgroundColor =
      '#fafafa';
    if (this.dataSource) {
      this.dataSource.disconnect();
    }
  }

  checkCompPermission() {}

  navigateToScreen(state: string) {
   
    this.router.navigate([`/maintenance/main/${state}`]);
    // this.dataService.sendMenu(item);
    // this.router.navigate([`/maintenance/main/sub/${item.id}`]);
  }
  alpha() {
    const sortedusers = DATA.sort(function (a, b) {
      var nameA = a.title.toUpperCase();
      var nameB = b.title.toUpperCase();
      if (nameA < nameB) {
        return -1; //nameA comes first
      }
      if (nameA > nameB) {
        return 1; // nameB comes first
      }
      return 0; // names must be equal
    });
   
  }

  getMainMenus() {
    this.apiService.getMaintenanceMenu().subscribe(
      (data: any) => {
       
        this.maintenanceMenus = data;
        this.cdr.markForCheck();
      },
      (err: any) => {
     
      }
    );
  }
  dataByCard(index,item){
    return item;
  }
}
