import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AuditLogComponent } from '../../audit-log/audit-log.component';
import { auditLogInterface, loggedInUserInterface } from './add-product-maintenance';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-add-product-maintenance',
  templateUrl: './add-product-maintenance.component.html',
  styleUrls: ['./add-product-maintenance.component.scss']
})
export class AddProductMaintenanceComponent implements OnInit {
  @ViewChild('auditLog') auditLog : AuditLogComponent
  addProductForm: FormGroup;
  isEdit: boolean = false;
  entityCode: string;
  bankCode: string;
  isProductCode: boolean = false;
  loggedInUser : loggedInUserInterface;
  id: number;
  auditLogData: auditLogInterface;
  entityIsEdit: string;
  readorWrite: boolean=true;


  constructor(private formBuilder: FormBuilder,private apiService:ApiService,
    private router:Router,private snack: MatSnackBar, private route : ActivatedRoute,    private dataService : DataService, 
    private ls : LocalStoreService, private loader: AppLoaderService,

    private cdr: ChangeDetectorRef) {this.addProductForm = this.formBuilder.group({
    bankCode: [ '',[Validators.required]],
    entityCode: [ '',[Validators.required]],
    productName: [ '',[Validators.required]],
    productCode: [ '',[Validators.required]],
  id: [ ''],
}) }
  ngOnInit(): void {

    this.getEntityCode();
    this.route.queryParamMap.subscribe((params: any) => {
      console.log(params.get('id'));
      this.entityIsEdit = params.get('isEdit');
      console.log("this.entityIsEdit",this.entityIsEdit);
      if(params.get('id')){
        this.isEdit = true;
        this.id = params.get('id');
        this.apiService.editEntityById(params.get('id')).subscribe((resp) =>{
           console.log(resp);
           this.getBankCode(resp.entityCode);
           this.buildItemForm(resp);
        });
          this.apiService.getProductById(params.get('id')).subscribe((resp) =>{
          console.log(resp);
          this.buildItemForm(resp);
          this.auditLogData = resp;
          this.dataService.setAuditLogData(this.auditLogData);
          this.dataService.sendClickEvent();
       });
      }
      else{
        this.readorWrite=false;
        this.buildItemForm({});
      }
    }
    ); 
    this.loggedInUser = this.ls.getItem('ICUST_USER');
  }
  buildItemForm(item) {
    console.log("item",item)
      this.addProductForm = this.formBuilder.group({
      bankCode: [item.bankCode || '',Validators.required],
      entityCode: [item.entityCode ||'',Validators.required],
      id: [ item.id ||''],
      loggedInUser: [item.loggedInUser || ''],
      productName: [item.productName ||'',Validators.required],
      productCode: [item.productCode ||'',Validators.required],
    });
    console.log(this.addProductForm);
  }
  goBack(){
    this.router.navigate(["/maintenance/main/product"]);
  }
  getEntityCode() {
    this.apiService.getEntityCode().subscribe((resp) => {
      this.entityCode = resp;
    });
  }
  getBankCode(entityCode:any) {
    this.apiService.getBankInfoByEntityCode(entityCode).subscribe((resp) => {
      this.bankCode = resp;
      console.log(this.bankCode);
    });
  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteProductById(this.auditLogData.id)
        .subscribe(data => {
          this.router.navigate(["/maintenance/main/product"]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
          this.router.navigate(["/maintenance/main/product"]);
        
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
        payload.id = this.id;
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser.userName;
        this.apiService.getAuditLogsForProductMaintenance(payload).subscribe((data) => {
          console.log(data);
          this.auditLogData = data;
          this.dataService.setAuditLogData(this.auditLogData);
          this.dataService.sendClickEvent();
          let message = '';
          switch (operation) {
            case 'open':
              message = 'opened'
              break;
            case 'close':
              message = 'closed'
              break;
            case 'auth':
              message = 'authorized'
              break;
            default:
              break;
          }
          this.snack.open(`Record ${message} successful!'`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        });
      }}))
   
  }

  getEntityDropdown(){
    this.apiService.getEntityDropdown().subscribe((data) => {
      this.entityCode = data;
      console.log("Entity Code",this.entityCode)  
    });   
  }
  submit(){
    this.addProductForm.value.loggedInUser = this.loggedInUser.userName;
    let payload={
      bankCode: this.addProductForm.value.bankCode,
      entityCode: this.addProductForm.value.entityCode,
      id:this.addProductForm.value.id,
      productName:this.addProductForm.value.productName,
      productCode:this.addProductForm.value.productCode,
      loggedInUser:this.addProductForm.value.loggedInUser,
       // this.addProductForm.value.loggedInUser = this.loggedInUser.userName;
      //txnId:101,

    }

    this.loader.open();
    this.addProductForm.value.loggedInUser = this.loggedInUser;
    this.apiService.updateProductDetails(payload).subscribe(response => {
    this.loader.close();
    if(response != null && !this.isEdit){
      this.router.navigate([`/maintenance/main/product`]).then(_=>{
        this.snack.open('Record Added Successfully!', 'OK', 
        { duration: 4000 , 
          verticalPosition: "top",
          horizontalPosition: "right",});
      });
      this.cdr.markForCheck();
    }
    else if(response != null && this.isEdit){
      this.router.navigate([`/maintenance/main/product`]).then(_=>{
        this.snack.open('Record updated Successfully!', 'OK', 
        { duration: 4000 , 
          verticalPosition: "top",
          horizontalPosition: "right",});
      });
      this.cdr.markForCheck();
    }
    else if(response != null || this.isEdit == true){
     // 
      console.log("response",response);
      this.router.navigate([`/maintenance/main/product`]).then(_=>{
        this.snack.open('Record Saved Successfully!', 'OK', 
        { duration: 4000 , 
          verticalPosition: "top",
          horizontalPosition: "right",});
      });
      this.cdr.markForCheck();
     }
  }) 
  
  }
  onProductCodeChange(event:any){
    this.apiService.isProductCodePresent(event.value).subscribe((resp) => {
      console.log("resp::",resp) //true
      if(resp == true){
        // this.isProductCode = true;
        this.isProductCode = false;
        console.log("isProductCode::",this.isProductCode)
        this.snack.open(`This is an existing Product Code!'`, "OK", {
          duration: 2000,
          verticalPosition: "top",
          horizontalPosition: "right",
        });
      }else{
        this.isProductCode = false;
      }
    });
   // console.log("isProductCode::",this.isProductCode)//false
  }
  dataByEntityCode(index,item){
    return item.entityCode
  }
  dataByBankCode(index,item){
   return item.bankCode
  }
}
