import { Component, ElementRef, OnInit} from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { MatTableDataSource } from '@angular/material/table';
import { ViewChild } from '@angular/core';
import {MatSort, Sort} from '@angular/material/sort';
import {LiveAnnouncer} from '@angular/cdk/a11y';
import { MatPaginator } from '@angular/material/paginator';
import {AfterViewInit} from '@angular/core';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { BranchHolidayPopupComponent } from '../branch-holiday/branch-holiday-popup/branch-holiday-popup.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { Subscription } from 'rxjs';
import { SendHolidayScreenService } from 'app/shared/services/send-holiday-screen.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';



@Component({
  selector: 'app-branch-holiday',
  templateUrl: './branch-holiday.component.html',
  styleUrls: ['./branch-holiday.component.scss']
})
export class BranchHolidayComponent implements OnInit {

  @ViewChild('searchVal') searchVal: ElementRef;
  selectedFilterIndex : number = 0;

  dataSource = new MatTableDataSource();
  displayedColumns: string[] = ['entityCode', 'bankCode', 'branchCode', 'calenderYear', 'authStatus', 'recordStatus', 'action'];

  filterByOption: { key: string, value: string }[] = [
    { key: 'entityCode', value: 'entityCode' },
    { key: 'bankCode', value: 'Bank Code' },
    { key: 'branchCode', value: 'Branch Code' }
  ];

  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  branchCode:string[]=[];
  bankCode:string[]=[];
  entityCode:string[]=[];
  public items: any[];
  users: any[] = [];
  public getItemSub: Subscription;
  screen: any;
  // screen: string;

  constructor(private router:Router,private snack: MatSnackBar,private loader: AppLoaderService, private screenService: SendHolidayScreenService, 
    private dialog: MatDialog,private apiService:ApiService,private confirmService: AppConfirmService,private _liveAnnouncer: LiveAnnouncer,
    private ls:LocalStoreService) { }

  ngOnInit(): void {
   this.getBrancheHoliday(); 
   this.getItems();
  //  this.screenService.holidayScr.subscribe(res=>{
  //   console.log(res);
  //   this.screen=res;
  //   console.log(this.screen)
  //  });
  }
  ngOnDestroy() {
    if (this.getItemSub) {
      this.getItemSub.unsubscribe()
    }
  }
  getItems() {
    this.items = this.users;
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }
  addNew(res){
    this.screenService.holidayScr.next(res);
    this.router.navigate(["/maintenance/main/holiday/BranchHoliday/addHoliday"]);
  }
  onFileChange(event) {
    console.log('event', event.path[0].files[0]);
    const file: File = event.path[0].files[0];
    const uploadData = new FormData();
    uploadData.append('screenType', 'BranchHoliday');
    uploadData.append('file', file);
    this.apiService.uploadExelFile(uploadData).subscribe((response) => {
      console.log(response);
      this.snack.open(`File Uploaded Successfully`, "OK", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
    this.getBrancheHoliday();
  }

  getBrancheHoliday() {
    this.apiService.getBranchHolidayInfo().subscribe((data: any) => {
      for(let i=0;i<data.length;i++){
        if(data[i].authStatus == 'U'){
          data[i].authStatus = 'Unauthorized'
        }
        if(data[i].authStatus == 'A') {
          data[i].authStatus = 'Authorized'
        } 
        if(data[i].recordStatus == 'O'){
          data[i].recordStatus = 'Open'
        } 
        if(data[i].recordStatus == 'C'){
          data[i].recordStatus = 'Closed'
        }
      }
      this.users = data;
      this.items = this.users;
      this.dataSource = new MatTableDataSource(data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    });
  }

  
  goBack(){
    this.router.navigate([`/maintenance/main/holiday`]);
  }

  doFilter(filterBy, e){
    this.filterTable(filterBy);
    this.applyFilter(e);
  }
 

  filterTable(filterBy) {
    this.dataSource.filterPredicate = (data: any, filter: string): boolean => {
      return (
        data[filterBy]?.toLocaleLowerCase().includes(filter)
      )
    }
  }

  clearFilter(){
   this.dataSource.filter = '';
   this.searchVal.nativeElement.value = '';
   this.selectedFilterIndex = 0;
  }


  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  openPopUp(data: any = {}, isNew?){
    let title = isNew ? 'Add Branch Specific Holiday' : 'Edit Branch Specific Holiday';
    let dialogRef: MatDialogRef<any> = this.dialog.open(BranchHolidayPopupComponent, {
      width: '400px',
      disableClose: true,
      data: {
        title: title, 
        payload: data,
        branchCode: this.branchCode,
        
      }
    })
    dialogRef.afterClosed()
      .subscribe(res => {
        if(!res) {
          return;
        }
        
        if (isNew) {
           this.apiService.updateBranchHolidayDetails(res,false).subscribe(response => {
            
            this.getBrancheHoliday();
            this.snack.open(`Record Added!'`, "OK", {
              duration: 2000,
              verticalPosition: "top",
              horizontalPosition: "right",
            });
          });
        } else {
          this.apiService.updateBranchHolidayDetails(res,false).subscribe(response => {
            
            this.items = response;
            this.loader.close();
            this.getBrancheHoliday();
            this.snack.open(`Record Updated!'`, "OK", {
              duration: 2000,
              verticalPosition: "top",
              horizontalPosition: "right",
            });
          });
        }
      })

  }
  getBranchCode() {
    this.apiService.getBranchCode().subscribe((resp) => {
      this.branchCode = resp;
    });
  }
  
  deleteItem(element) {
    this.confirmService.confirm({message: `Delete ${element.entityCode}?`})
      .subscribe(res => {
        if (res) {
          this.apiService.deleteCurrencyHoliday(element.id)
            .subscribe(data => {
            },
              err=>{
                if (err.status == 200) {
                  this.snack.open('Record Deleted Successfully', 'OK', {
                    duration: 4000,
                    verticalPosition: 'top',
                    horizontalPosition: 'right'
                  });
                }
      
              
              this.getBrancheHoliday();
              
            })
        }
        
      });
  }
  commonHoliday(){
    this.ls.setItem('SCREEN_NAME','Branch');
    this.router.navigate(["/maintenance/main/holiday/BranchHoliday/commonHoliday"]);
  }
  addBranch(){
    this.router.navigate(["/maintenance/main/holiday/BranchHoliday/addBranch"]);
    
  }
  openEdit(data: any = {},res,isEdit : string) {
    console.log(res);
    this.screenService.holidayScr.next(res);
    this.router.navigate(["/maintenance/main/holiday/BranchHoliday/addHoliday"],{ queryParams: { id: data.id, isEdit : isEdit } });
  }

  announceSortChange(sortState: Sort) {
    if (sortState.direction) {
      this._liveAnnouncer.announce(`Sorted ${sortState.direction}ending`);
    } else {
      this._liveAnnouncer.announce('Sorting cleared');
    }
  }
}
