import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { Form, FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router , ActivatedRoute} from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AuditLogComponent } from '../audit-log/audit-log.component';
import { DataService } from 'app/shared/services/data.service';
import { flow } from 'lodash';
import { flatMap, filter } from 'lodash/fp';
import { select } from '@syncfusion/ej2-schedule';
import { roleAuditLogInterface } from './add-role-maintenance.component';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-role-maintenance',
  templateUrl: './role-maintenance.component.html',
  styleUrls: ['./role-maintenance.component.scss']
})
export class RoleMaintenanceComponent implements OnInit {
  @ViewChild('auditLog') auditLog : AuditLogComponent
  public roleForm: FormGroup;
  selectAllChecked: boolean=false;
  hideSelect: boolean=true; 
  isDisable:boolean = true;
  isEdit:boolean = false;
  auditData : roleAuditLogInterface;
  isAuditData : boolean = false
  roleScreenList: any;
  public selectedtab: string;
  indexdata: any;
  screenName:any = [];
  editFlag = false;
  loggedInUser : any;
  id: any;
  //dataService: any;
  enableFinish: boolean=false;
 // permissions:any [] = [];
  selectedCheckbox: any[] = [];
  getarr: any[] = [];
 // selectedCheckbox:any;
  isReadOnly: boolean = true;
  isView: boolean = true;
  show = false;
  editNow : boolean = false;
  permissions: any[] = [];
  childarr: any[] = [];
  childPer: string="";
  parentPermission: any;
  parentSelector: boolean = false;
  checked = false;
  testvar: any;
  checkedcheckbox:boolean=true;
  tellerRoleName:any=[];
  selectedrole: string;
  selectRole: any;
  readorWrite: boolean=true;
  allRoles: any[] = [];
  selectedTabId: any;
  tabIndex: any;
  isSelectedAll:boolean = false;


  constructor(private apiService: ApiService, private router:Router, private route: ActivatedRoute,
    private snack: MatSnackBar, private ls: LocalStoreService, private fb: FormBuilder, private cdr: ChangeDetectorRef, private dataService: DataService) { }

  ngOnInit(): void {
    this.buildItemForm({});
    this.loggedInUser = this.ls.getItem('ICUST_USER');
    this.getScreenPermissions();

    this.route.queryParamMap.subscribe((params: any) => {
      // this.entityIsEdit = params.get('isEdit');
      // console.log("this.entityIsEdit",this.entityIsEdit);
      if(params.get('isEdit') == 'Yes'){
         this.isReadOnly = false;
       this.isView= true;
       this.editNow = true;
      }
      else if(params.get('isEdit') == 'No'){
         this.isReadOnly = true;
         this.isView= true;
        this.editNow = false;
      }
      else if(params.get('isEdit') == 'false'){
        this.isReadOnly = false;
        this.isView= false;
        this.editNow = false;
      }
      else if(params.get('isEdit') == null){
      this.isView= false;
      }
      if(params.get('id')){
        this.isEdit = true;
        this.id = params.get('id');
        this.apiService.fetchRoleById(params.get('id')).subscribe((resp: any) =>{
           console.log(resp);
          this.selectedrole="Customer Onboarding";
          this.testvar = resp;
          var roleTab
          for(let i=0;i<resp.permissionInfo.length; i++ ){
            this.getPermissionInfo(resp.permissionInfo[i].permissionId.screenId);
          console.log(this.allRoles);
          
           var screen= resp.screenInfo.filter((item)=>{
            return  item.screenId===resp.permissionInfo[i].permissionId.screenId
            })
            console.log(this.childarr,screen);
           this.childarr.push(screen[0].screenName);
           roleTab = resp.tabInfo.filter((tabData)=>{
           return tabData.tabId==screen[0].tabId
           })
            console.log(roleTab);
          }
          //this.selectedrole=roleTab[0].tabName.toUpperCase();
          this.buildItemForm(resp.roleInfo);
          this.tabSelection(roleTab[0].tabId,roleTab[0].tabName.toUpperCase());
  
          this.childPer = resp.childPermission + ',';
          if( resp.roleInfo.authStatus=="U" ){
            resp.roleInfo.authDate =""
            resp.roleInfo.authBy=""
          }

          this.auditData = resp.roleInfo;
          this.isAuditData = true;
          this.dataService.setAuditLogData(this.auditData);
          this.dataService.sendClickEvent();
        
          
        });
      }
      else{
        this.readorWrite=false;
        this.buildItemForm({});
     
      }
    }
    ); 
  }
  
  createPermissionFields(): any {
    return this.fb.group({
      permissionId: {
        roleName: [''],
        screenId: ['']
      },
      version: ['']
    })

  }
 getScreenPermissions(){
  this.apiService.fetchScreenPermissions().subscribe((response:any) => {//parent api
    this.tellerRoleName=response;
for(let i=0; i< response.length; i++){
  this.tellerRoleName[i].tabName=this.tellerRoleName[i].tabName.toUpperCase();
  this.tellerRoleName[i].selected=false;
  this.apiService.fetchRolePermissionByTabID(this.tellerRoleName[i].tabId).subscribe((resp) => {
    console.log(resp);
    for(let i = 0; i < resp.length; i++){
      resp[i].checked = false;
      this.allRoles.push(resp[i]);
    }
  })
} 
  }); 
  console.log(this.allRoles);
}
getPermissionInfo(screenId){
  for(let j = 0; j < this.allRoles.length; j++){
    if(this.allRoles[j].screenId == screenId){
      this.allRoles[j].checked = true;
      for(let k = 0; k < this.tellerRoleName.length; k++){
        if(this.tellerRoleName[k].tabId == this.allRoles[j].tabId){
          this.tellerRoleName[k].selected = true;
          break;
        }
      }
      break;
  }
} 
}
  onChange($event) {
    const screenName = $event.target.value;
    const isChecked = $event.target.checked;
    this.roleScreenList = this.roleScreenList.map((d) => {
      if (d.screenName == screenName) {
        d.checked = isChecked;
        this.parentSelector = true;
        return d;
      }
      if (screenName == -1) {
        d.checked = this.parentSelector;
        return d;
      }
      return d;
    });
  }
 
 

  

  buildItemForm(item) {
    this.roleForm = this.fb.group({
      screenName:[''],
      id: [''],
      authStatus: [''],
      isActive:[true],
      oneTimeAuth:[''],
      recordStatus:[''],
      version:[''],
      loggedInUser:[''],
      authBy:[''],
      authDate: [''],
      permissions: this.fb.array([this.createPermissionFields()]),
      roleId: [item? item.id:''],
      selectAllRole:[false],
      roleName: [item? item.roleName :'',[Validators.required]],
      roleDesc:[item? item.roleDesc : '',[Validators.required]],
      }) 
  }


  

  onChecklistChange(checked, checkbox, index?) {
    console.log('checked',checked)
    console.log('checkbox', checkbox)
    // var roleObj;
    if (checked.checked==true) {
      this.allRoles[index].checked = true
      this.tellerRoleName[this.tabIndex].selected = true; 
      // roleObj = 
      // {
      //   "permissionId": {
      //     "roleName": this.roleForm.value.roleName,
      //     "screenId": checkbox.screenId
      //   },
      //   "version": 0
      // }
      // console.log('roleObj',roleObj)

      // this.permissions.push(roleObj)
    } else {
      this.allRoles[index].checked = false;
      // var index1 = this.permissions.findIndex(x => x.screenName == checkbox.screenName);
      // this.permissions.splice(index1, 1);
    }
    // if(this.permissions.length == this.roleScreenList.length)
    // this.roleForm.controls.selectAllRole.setValue(true);
    // else this.roleForm.controls.selectAllRole.setValue(false);
    let selectedItemLength = this.allRoles.filter(role => role.tabId == this.selectedTabId && role.checked == true).length;
    this.isSelectedAll = this.allRoles.filter(role => role.tabId == this.selectedTabId).length == selectedItemLength;
    if(selectedItemLength < 1){
      this.tellerRoleName[this.tabIndex].selected = false;
    }

  }


  goBack(){
    this.router.navigate(["/maintenance/main/roleMaintenance"]);
  }


  submit(form) {
    console.log('click',this.roleScreenList);
     console.log("Form:",form);
 
    this.roleForm.value.loggedInUser = this.loggedInUser.userName;
    console.log(this.roleForm);
    this.allRoles.map(role => {
      if (role.checked == true) {
        let roleObj;
        roleObj =
        {
          "permissionId": {
            "roleName": this.roleForm.value.roleName,
            "screenId": role.screenId
          },
          "version": 0
        }
        this.permissions.push(roleObj)
      }
    }) 
    if(this.permissions.length == this.allRoles.length){
      this.roleForm.controls.selectAllRole.setValue(true);
    }   
    else{
      this.roleForm.controls.selectAllRole.setValue(false);
    }
    console.log('this.roleForm.value',this.roleForm.value)
      this.roleForm.value.permissions = this.permissions;
    console.log('perm', this.permissions)
    for (let i = 0; i < this.permissions.length; i++){
      console.log(this.permissions[i].permissionId.roleName);
      if (i == this.permissions.length - 1) {
        this.childPer= this.childPer+this.permissions[i].permissionId.roleName; 
      } else {
        this.childPer= this.childPer+this.permissions[i].permissionId.roleName + ',';
      }

      this.parentPermission = "Teller Dashboard";
    
    }
   // this.roleForm.value.childPermission = this.childPer;

    this.roleForm.value.id = this.roleForm.value.roleId; 
    this.roleForm.value.parentPermission=this.selectedrole;  
    this.apiService.saveRole(this.roleForm.value).subscribe(data => {
      console.log("data",data);
      if(data != null && this.isEdit == true){
        this.router.navigate([`/maintenance/main/roleMaintenance`]).then(_=>{
                this.snack.open('Data Updated!', 'OK', 
                { duration: 4000 , 
                  verticalPosition: "top",
                  horizontalPosition: "right",});
                });       
        this.cdr.markForCheck();
      }
      else if(data != null || this.isEdit == true){
       // 
        console.log("data",data);
        this.router.navigate([`/maintenance/main/roleMaintenance`]).then(_=>{
          this.snack.open('Data Saved!', 'OK', 
          { duration: 4000 , 
            verticalPosition: "top",
            horizontalPosition: "right",});
          });
        this.cdr.markForCheck();
       }
    }) 
    //}  
  }
  selectAll(event) {

    
    for (let i = 0; i < this.allRoles.length; i++) {
      if(this.allRoles[i].tabId == this.selectedTabId){
      if (event.checked == true){
        this.allRoles[i].checked = true;
        this.tellerRoleName[this.tabIndex].selected = true;
        this.isSelectedAll = true
        
        // this.roleScreenList[i].checked = true;
        // this.onChecklistChange(event, this.roleScreenList[i]);
      }
      else{
        this.allRoles[i].checked = false;
        this.tellerRoleName[this.tabIndex].selected = true;
        this.isSelectedAll = false
      }
    }
      // else { this.roleScreenList[i].checked = false;
        // this.onChecklistChange(event, this.roleScreenList[i]);
      // }
    }
    let selectedItemLength = this.allRoles.filter(role => role.tabId == this.selectedTabId && role.checked == true).length;
    this.isSelectedAll = this.allRoles.filter(role => role.tabId == this.selectedTabId).length == selectedItemLength;
    if(selectedItemLength < 1){
      this.tellerRoleName[this.tabIndex].selected = false;
    }
  }
  
  

  
  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
    let payload: any = {};
    payload.id = this.id;
    payload.operation = operation;
    payload.loggedInUser = this.loggedInUser.userName;

    console.log(payload)
    this.apiService.getAuditLogsForRoleMaintenance(payload).subscribe((data) => {
      console.log(data);
      if( data.authStatus=="U" ){
        data.authDate = ""
        data.authBy = ""
        }
      this.auditData = data;
      this.dataService.setAuditLogData(this.auditData);
      this.isAuditData = true;
      this.dataService.sendClickEvent();
      let message = '';
      switch (operation) {
        case 'open':
          message = 'opened'
          break;
        case 'close':
          message = 'closed'
          break;
        case 'auth':
          message = 'authorized'
          break;
        default:
          break;
      }
      this.snack.open(`Record ${message} successfully!'`, "OK", {
        duration: 2000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
   }}))
    
  }
  tabSelection(tabId,name,index?){
    this.hideSelect = false;
    this.selectedTabId = tabId
    this.tabIndex = index;
    this.selectedrole=name;
    this.isSelectedAll = this.allRoles.filter(role => role.tabId == this.selectedTabId).length == this.allRoles.filter(role => role.tabId == this.selectedTabId && role.checked == true).length

    //  this.roleForm.controls.selectAllRole.setValue(false);
    //   this.apiService.fetchRolePermissionByTabID(tabId).subscribe((response) => {

      // this.roleScreenList = response;
      // console.log('test', this.roleScreenList)
      // for (let i = 0; i < this.roleScreenList.length; i++){
      //   this.roleScreenList[i].checked = false;
      // }
      // console.log('roleScreenList', this.roleScreenList);
      // for (let k = 0; k < this.childarr.length; k++) { 
      //   this.getarr= this.roleScreenList.filter((val) => {
      //     return val.screenName == this.childarr[k];
      //   })
      //   const index = this.roleScreenList.findIndex(object => {
      //     return object.screenName ==this.childarr[k];
      //   })
      //   console.log(index);
      //   for (let j = 0; j < this.getarr.length; j++){
      //     this.roleScreenList[index].checked = true;
      //   }
      // }
      

      // console.log(this.getarr, this.roleScreenList);
      // this.enableSelectAll(this.roleScreenList);
      //  for(let i = 0;i < response[i];i++){
      //   this.screenName = response[i].screenName;
      //  }
    // });
  }

  enableSelectAll(role){
    var selectAllreturn= role.filter((item)=>{
      return item.checked == false;
         })
      if(selectAllreturn.length==0)
      this.roleForm.controls.selectAllRole.setValue(true);
      else  this.roleForm.controls.selectAllRole.setValue(false);
  }
  deleteRecord(operation) {
    Swal.fire(
      {
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteRole(this.auditData.id)
        .subscribe(data => {
        },
          err=>{
            if (err.status == 200) {
              this.snack.open('Record Deleted Successfully', 'OK', {
                duration: 4000,
                verticalPosition: 'top',
                horizontalPosition: 'right'
              });
             
            }
            
        })
      }}))}
  

}
