import { Component, Inject, OnInit, ɵCodegenComponentFactoryResolver } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { tillDetails } from 'app/shared/models/tillvaultdenom';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-till-vault-denomination',
  templateUrl: './till-vault-denomination.component.html',
  styleUrls: ['./till-vault-denomination.component.scss']
})
export class TillVaultDenominationComponent implements OnInit {

  public itemForm: FormGroup;
  countryCurrency: string[]=[];
  // tillId:any[]=[];
  branchCode:string[]=[];
  bankCode:string[]=[];
  denom:  string[] = [];
  currency: string[] = [];
  till: any[] = [];
  currentUser: string;
  vaultStatus: any={};
 
  entity: tillDetails[] = [];
  tillData: tillDetails;
  loggedInUser : string;
  auditData : tillDetails;
  isAuditData : boolean = false;
  id: number;
  isEdit: boolean = false;
  readorWrite: boolean=true;
  constructor(
    // @Inject(MAT_DIALOG_DATA) public data: any,
    // public dialogRef: MatDialogRef<TillVaultDenominationComponent>,
    private fb: FormBuilder,
    private router:Router,
    private apiService: ApiService,
    private ls:LocalStoreService,
    private dataService:DataService,
    private snack: MatSnackBar,
  ) { }

  ngOnInit() {
    this.currentUser = this.ls.getItem("ICUST_USER");
    // this.dataService.getTillVaultStatus.subscribe(data =>{
   
    //   this.vaultStatus = typeof this.vaultStatus === 'object' ? data : {};
    // });
    
    setTimeout(() => {  

      
      this.vaultStatus = JSON.parse(localStorage.getItem('fetchVaultDenom'));
     
      if(this.vaultStatus == null || this.vaultStatus == undefined){
        this.readorWrite=false;
        this.buildItemForm({})
      }       
      else if(this.vaultStatus.id != null || this.vaultStatus.id != undefined){
      // this.loader.open();
      this.isEdit = true;
     
      this.id = this.vaultStatus.id;
      this.auditData = this.vaultStatus
      this.buildItemForm(this.vaultStatus)
  
      this.isAuditData = true;
      this.dataService.setAuditLogData(this.auditData);
    
      // this.loader.close();
      }
    }, 500)

    this.getCurrency();
    this.getBankCode();
    this.getBranchCode();
    this.getEntityCode();
    this.getDenomCode();
    this.getTillId();
    this.loggedInUser = this.ls.getItem('ICUST_USER').userName;
   
    
  }
  buildItemForm(item) {
    this.itemForm = this.fb.group({
      bankCode: [item?.bankCode || '', Validators.required],
      branchCode: [item?.branchCode || '', Validators.required],
      currency: [item?.currency || '', Validators.required],
      denomCode: [item?.denomCode || '', Validators.required],
      entityCode: [item?.entityCode || '', Validators.required],
      tillId: [item?.tillId || '', Validators.required],
      units: [item?.units || '', Validators.required],
      id: [item?.id || null],
      loggedInuser : [item.loggedInUser || '']
      // isActive: [item.isActive || false]
    })
  }

  submit() {
    // this.dialogRef.close(this.itemForm.value)
    if(this.itemForm.invalid){
      return;
    }else{
      this.tillData = this.till.find(x => x.tillVaultCode == this.itemForm.value.tillId);
      this.itemForm.value.tillId = this.tillData.id;
      this.itemForm.value.loggedInUser=this.loggedInUser;
      this.apiService.saveTellerVaultDenomination(this.itemForm.value).subscribe((resp) => {
     
      this.router.navigate([`/maintenance/main/tillVaultDenom`]).then(_=>{
          this.snack.open('Data Saved Successfully!', 'OK', 
          { duration: 4000 , 
            verticalPosition: "top",
            horizontalPosition: "right",});
        });
      },(err:any) =>{
        this.snack.open(`Error!`, 'OK', { duration: 4000,  
          verticalPosition: "top",
          horizontalPosition: "right",});
        }
      )
    }  
  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteTellerVaultDenomination(this.auditData.id)
        .subscribe(data => {
          this.router.navigate([`/maintenance/main/tillVaultStatus`]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
          this.router.navigate([`/maintenance/main/tillVaultStatus`]);
        
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){

        let payload: any = {};
        payload.id = this.id;
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser;
        this.apiService.getAuditLogsFortillvaultdenomination(payload).subscribe((data) => {
         
          this.auditData = data;
          this.dataService.setAuditLogData(this.auditData);
          this.isAuditData = true;
          this.dataService.sendClickEvent();
          let message = '';
          switch (operation) {
            case 'open':
              message = 'opened'
              break;
            case 'close':
              message = 'closed'
              break;
            case 'auth':
              message = 'authorized'
              break;
            default:
              break;
          }
          this.snack.open(`Record ${message} successfully!'`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        });
      }}))
  
  }

  getCurrency() {
    // this.apiService.getCountryValues().subscribe((resp) => {
     
    //   this.currency = resp;
    // });
    this.apiService.getCurrencyDetails().subscribe(data=>{
      console.log("Auth currecy details", data)
      for(let i=0; i<data.length;i++){
        this.currency.push(data[i].currencyId)
      }
      console.log(this.currency);
    })
  }
  getBankCode() {
    this.apiService.getBankCode().subscribe((resp) => {
   //   this.bankCode = resp;
      for(let i=0;i<resp.length;i++){
        if(resp[i].authStatus == 'A'){
          this.bankCode.push(resp[i]);
        }
      }
    });
  }
  getBranchCode() {
    this.apiService.getBranchCode().subscribe((resp) => {
     // this.branchCode = resp;
        for(let i=0;i<resp.length;i++){
        if(resp[i].authStatus == 'A'){
          this.branchCode.push(resp[i]);
        }
      }
    });
  }
  getTillId() {
    let tillOrVault ="T";
    this.apiService.getTillData(tillOrVault).subscribe((resp) => {
     
      this.till = resp;
    });
  }
  getEntityCode() {
    this.apiService.getEntityCode().subscribe((resp) => {
     
     // this.entity = resp;
      for(let i=0;i<resp.length;i++){
        if(resp[i].authStatus == 'A'){
          this.entity.push(resp[i]);
        }
      }
    });
  }
  getDenomCode() {
    this.apiService.fetchDenomMasterAuthRecs().subscribe((resp: any) => {
      console.log(resp);
      for(let i=0; i<resp.length;i++){
        if(resp[i].authStatus==='A'){
          this.denom.push(resp[i]);
        }
      }
      console.log(this.denom);
    });
  }
  alhanumericValidation(event){
    var inp = String.fromCharCode(event.keyCode);
    if (/[a-zA-Z0-9]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }
  goBack() {
    localStorage.removeItem('fetchVaultDenom');
  

    this.router.navigate(["/maintenance/main/tillVaultDenom"]);
  }
  dataByEntityCode(index,data){
    return data.bankCode;
  }
}
