import {
  Component,
  ElementRef,
  OnInit,
  ViewChild,
  OnDestroy,
} from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { rumangoAnimations } from 'app/shared/animations/rumango-animations';
import { ApiService } from 'app/shared/services/api.service';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { Subscription } from 'rxjs';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort, Sort } from '@angular/material/sort';
import { LiveAnnouncer } from '@angular/cdk/a11y';
import { MatTableDataSource } from '@angular/material/table';
import { DownloadService } from 'app/shared/services/download.service';
import jsPDF from 'jspdf';
import 'jspdf-autotable';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Component({
  selector: 'app-till-vault-branch-map-summary',
  templateUrl: './till-vault-branch-map-summary.component.html',
  styleUrls: ['./till-vault-branch-map-summary.component.scss'],
  animations: rumangoAnimations,
})
export class TillVaultBranchMapSummaryComponent implements OnInit {
  @ViewChild('searchVal') searchVal: ElementRef;
  selectedFilterIndex: number = 0;
  displayedColumns: string[] = [ 'entityCode', 'parentVaultId', 'authStatus','recordStatus','action'];
  filterByOption: { key: string; value: string }[] = [
    { key: 'entityCode', value: 'Entity Code' },
  ];
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;

  dataSource = new MatTableDataSource();

  public items: any[];
  public getItemSub: Subscription;
  users: any[] = [];
  currency: string[] = [];
  till: any[] = [];
  branchCode: string[] = [];
  bankCode: string[] = [];
  id: any;
  fetchBrnMap: any;
  currentUser: string;
  loading: boolean;
  tillData: any;
  vault: any;
  data: any;
  vaultData: any;
  entity: any;
  entityCode: any[] = [];
  constructor(
    private dialog: MatDialog,
    private snack: MatSnackBar,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,
    private apiService: ApiService,
    private router: Router,
    private dataService: DataService,
    private ls: LocalStoreService,
    private _liveAnnouncer: LiveAnnouncer,
    private downloadService: DownloadService
  ) {}

  onFileChange(event) {
    console.log('event',event.path[0].files[0]);
    const file: File = event.path[0].files[0];
    const uploadData = new FormData();
    uploadData.append('screenType', 'tillValutBrnMap');
    uploadData.append('file', file);
    this.apiService.uploadFile(uploadData).subscribe(
      (response) => {
        console.log(response);
        this.getTellVaultBranchMap();
        this.snack.open('File Uploaded Successfully', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right',
        });
      },
      (error) => {
        console.log(error);
        this.snack.open('Upload Failed', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right',
        });
      }
    );
  }

  createPdf() {
    const arr = [];
    const head = [
      ['entityCode', 'parentVaultId', 'authStatus', 'recordStatus'],
    ];
    const body = [];
    this.users.map((user) => {
      const arr = [];
      head[0].map((i) =>
        !!user[i] ? arr.push(user[i].toString()) : arr.push('N/A')
      );
      body.push(arr);
      return;
    });
    const doc = new jsPDF();

    (doc as any).autoTable({
      head,
      body,
      theme: 'grid',
    });

    // below line for Open PDF document in new tab
    doc.output('dataurlnewwindow');

    // below line for Download PDF document
    doc.save('users.pdf');
  }

  ngOnInit() {
    this.currentUser = this.ls.getItem('ICUST_USER');
    this.getTillId();
    this.getParentVault();
    // this.fetchTellVaultBranchMap()
    // this.getItems();
    this.getTellVaultBranchMap();
    this.getCurrency();
    this.getBankCode();
    this.getBranchCode();
    this.getTillId();
    this.getEntityCode();
  }

  ngOnDestroy() {
    if (this.getItemSub) {
      this.getItemSub.unsubscribe();
    }
  }
 
  getItems() {
    this.items = this.users;
  }


  doFilter(filterBy, e) {
    this.filterTable(filterBy);
    this.applyFilter(e);
  }

  filterTable(filterBy) {
    this.dataSource.filterPredicate = (data: any, filter: string): boolean => {
      return data[filterBy].toLocaleLowerCase().includes(filter);
    };
  }

  clearFilter() {
    this.dataSource.filter = '';
    this.searchVal.nativeElement.value = '';
    this.selectedFilterIndex = 0;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  // announceSortChange(sortState: Sort) {
  //   if (sortState.direction) {
  //     this._liveAnnouncer.announce(`Sorted ${sortState.direction}ending`);
  //   } else {
  //     this._liveAnnouncer.announce("Sorting cleared");
  //   }
  // }

  announceSortChange(sortState: Sort) {
    if (sortState.direction) {
      this._liveAnnouncer.announce(`Sorted ${sortState.direction}ending`);
    } else {
      this._liveAnnouncer.announce('Sorting cleared');
    }
  }
 

  addNew() {
    this.router.navigate(['/maintenance/main/tillVaultltBranchMap/add']);
  }

  goBack() {
    this.router.navigate([`/maintenance/main/`]);
  }

  openPopUp(row) {
    console.log(row);
    this.fetchTellVaultBranchMap(row);
    // this.dataService.sendNavParam(this.fetchBrnMap);
    this.router.navigate([`/maintenance/main/tillVaultltBranchMap/${row.id}`]);
  }

  deleteItem(row) {
    this.confirmService
      .confirm({ message: `Delete ${row.entityCode}?` })
      .subscribe((res) => {
        if (res) {
          this.loader.open();
          this.apiService.deleteTellerVaultBrnMap(row.id).subscribe(
            (data) => {},
            (err) => {
              if (err.status === 200) {
                this.loader.close();
                this.snack.open(`Record Deleted!'`, 'OK', {
                  duration: 2000,
                  verticalPosition: 'top',
                  horizontalPosition: 'right',
                });
                this.getTellVaultBranchMap();
              }
            }
          );
        }
      });
  }
  getTellVaultBranchMap() {
    this.loading = true;
    this.apiService.getTellerVaultBranchMap().subscribe((resp) => {
      for(let i=0;i<resp.length;i++){
        if(resp[i].authStatus == 'U'){
          resp[i].authStatus = 'Unauthorized'
        }
        if(resp[i].authStatus == 'A') {
          resp[i].authStatus = 'Authorized'
        } 
        if(resp[i].recordStatus == 'O'){
          resp[i].recordStatus = 'Open'
        } 
        if(resp[i].recordStatus == 'C'){
          resp[i].recordStatus = 'Closed'
        }
      }
      console.log(resp);
      this.users = resp;
      this.items = this.users;
      this.dataSource = new MatTableDataSource(resp);
      console.log(this.dataSource);
      this.dataSource.paginator = this.paginator;
      this.items = resp;
      this.dataSource.sort = this.sort;
      this.loading = false;
    });
  }

  getTillId() {
    let tillOrVault = 'T';
    this.apiService.getTillData(tillOrVault).subscribe((resp) => {
      console.log('tillData', resp);
      this.till = resp;
    });
  }

  getParentVault() {
    let tillOrVault = 'V';
    this.apiService.getTillData(tillOrVault).subscribe((resp) => {
      this.vault = resp;
    });
  }
  fetchTellVaultBranchMap(row) {
    this.loader.open();
    this.apiService.fetchTellerVaultBranchMap(row.id).subscribe((result) => {
      console.log(result);
      this.data = result;
      this.tillData = this.till.find((x) => x.id == this.data.tillId);
      this.data.tillId = this.tillData.tillVaultCode;
      this.vaultData = this.vault.find((x) => x.id == this.data.parentVaultId);
      this.data.parentVaultId = this.vaultData.tillVaultCode;

      this.fetchBrnMap = this.data;

      if (this.fetchBrnMap != null) {
        localStorage.setItem('fetchBrnMap', JSON.stringify(this.fetchBrnMap));
        console.log(this.fetchBrnMap);
        this.loader.close();
      }
    });
  }

  tillVaultBranchMaping() {
    this.downloadService.downloadFiles('tillVaultBranchMaping', this.users);
  }

  getEntityCode() {
    this.apiService.getEntityCode().subscribe((resp) => {
      console.log(resp);
      this.entityCode = resp;
    });
  }

  getCurrency() {
    this.apiService.getCountryValues().subscribe((resp) => {
      this.currency = resp;
    });
  }
  getBankCode() {
    this.apiService.getBankCode().subscribe((resp) => {
      this.bankCode = resp;
    });
  }
  getBranchCode() {
    this.apiService.getBranchCode().subscribe((resp) => {
      this.branchCode = resp;
    });
  }
  dataByFilterByOption(filter) {
    return filter.key;
  }
}
