import { Component, OnInit, Inject } from "@angular/core";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { FormBuilder, Validators, FormGroup } from "@angular/forms";
import { ApiService } from "app/shared/services/api.service";
import { LocalStoreService } from "app/shared/services/local-store.service";
import { DataService } from "app/shared/services/data.service";
import { ActivatedRoute, Router } from "@angular/router";
import { AppLoaderService } from "app/shared/services/app-loader/app-loader.service";
import { MatSnackBar } from "@angular/material/snack-bar";
import { accTypeStatusInterface, loggedInUserObject } from "./acccount-type-table-popup";
import Swal from 'sweetalert2';

@Component({
  selector: "app-account-type-table-popup",
  templateUrl: "./account-type-table-popup.component.html",
  styleUrls: ["./account-type-table-popup.component.scss"],
})
export class AccountTypeTablePopupComponent implements OnInit {
  public itemForm: FormGroup;
  accountTypeId: string;
  currentUser: currentUserObject;
  id: string;
  loggedInUser: loggedInUserObject;
  auditData: any;
  isAuditData: boolean = false;
  show = false;
  isEdit: boolean = false;
  readorWrite: boolean=true;
  accTypeStatus: accTypeStatusInterface;

  constructor(
    private fb: FormBuilder,
    private apiService: ApiService,
    private loader: AppLoaderService,
    private snack: MatSnackBar,
    private router: Router,
    private ls: LocalStoreService,
    private dataService: DataService,
    private route: ActivatedRoute
  ) {}

  ngOnInit() {
    this.currentUser = this.ls.getItem("ICUST_USER");
    this.route.queryParamMap.subscribe((params: any) => {
      if (params.get("id")) {
        this.isEdit = true;
        this.id = params.get("id");
        this.apiService
          .fetchAccountTypeById(params.get("id"))
          .subscribe((resp) => {
            console.log("account status ",resp)
            this.accTypeStatus = resp;
            if (this.accTypeStatus == null || this.accTypeStatus == undefined) {
              this.readorWrite=false;
              this.buildItemForm({});
            } else if (
              this.accTypeStatus.accountTypeId != null ||
              this.accTypeStatus.accountTypeId != undefined
            ) {
              this.loader.open();
              this.id = this.accTypeStatus.accountTypeId;
              this.buildItemForm(this.accTypeStatus);
              this.isEdit = true;
              this.auditData = this.accTypeStatus;
              this.isAuditData = true;
              this.dataService.setAuditLogData(this.auditData);
              this.loader.close();
            }
            this.loggedInUser = this.ls.getItem("ICUST_USER");
          });
      } else {
        this.buildItemForm({});
      }
    });
  }
  buildItemForm(item) {
    this.itemForm = this.fb.group({
      accountTypeName: [item?.accountTypeName || "", Validators.required],
      accountTypeDescription: [
        item?.accountTypeDescription || "",
        Validators.required,
      ],
      accountTypeId: [item?.accountTypeId || null],
      loggedInUser: [item?.creatorId || this.currentUser.userName],
    });
  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteAccountType(this.auditData.id)
        .subscribe(data => {
          this.router.navigate([`/maintenance/main/accountType`]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
          this.router.navigate([`/maintenance/main/accountType`]);
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
        payload.id = this.id;
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser.userName;
        this.apiService.getAuditLogsForAccountType(payload).subscribe((data) => {
          this.auditData = data;
          this.dataService.setAuditLogData(this.auditData);
          this.isAuditData = true;
          this.dataService.sendClickEvent();
          let message = "";
          switch (operation) {
            case "open":
              message = "opened";
              break;
            case "close":
              message = "closed";
              break;
            case "auth":
              message = "authorized";
              break;
            default:
              break;
          }
          this.snack.open(`Record ${message} successfully!'`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        });
      }}))
   
  }

  submit() {
    if (this.itemForm.invalid) {
      return;
    } else {
      this.loader.open();
      this.apiService.upsertAccountType(this.itemForm.value).subscribe(
        (resp) => {
          this.loader.close();
          this.router.navigate([`/maintenance/main/accountType`]).then((_) => {
            this.snack.open("Data Saved Successfully!", "OK", {
              duration: 4000,
              verticalPosition: "top",
              horizontalPosition: "right",
            });
          });
        },
        (err: any) => {
          this.loader.close();
          this.snack.open(`Error!`, "OK", {
            duration: 4000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        }
      );
    }
  }

  goBack() {
    localStorage.removeItem("fetchAccTypeValue");
    this.router.navigate([`/maintenance/main/accountType`]);
  }
}
interface currentUserObject {
  userName: string;
}
