import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { NgxSpinnerService } from 'ngx-spinner';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-add-user-maintenance',
  templateUrl: './add-user-maintenance.component.html',
  styleUrls: ['./add-user-maintenance.component.scss']
})
export class AddUserMaintenanceComponent implements OnInit {
  public userMaintenance: FormGroup;
  RoleInfo: any;
  id: number;
  auditData: any;
  isAuditData:boolean=false;
  isEdit:boolean=false;
  loggedInUser: any;
  UserName: any;
  isPassword:boolean=false;
  hide=true;
  readorWrite: boolean=true;
  authorizedRoleId: any;
  tillData: any;
  till: any[] = [];
  constructor(private router:Router,private route : ActivatedRoute,
    private formBuilder: FormBuilder, private ls:LocalStoreService,
    private api:ApiService,private dataService : DataService,   private spinner: NgxSpinnerService,
    private snack:MatSnackBar,private cdr: ChangeDetectorRef) { }

  ngOnInit(): void {
    this.getRoleId();
    this.buildUserForm();
    this.getTillId();
    this.loggedInUser = this.ls.getItem('ICUST_USER').userName;
    this.route.queryParamMap.subscribe((params: any) => {
      console.log(params);
      if(params.get('id')){
         this.isEdit = true;
        this.id = params.get('id');
        this.api.getUserId(params.get('id')).subscribe((resp) =>{
           this.buildUserForm(resp);
           if(resp.passwordGenerationType=='Manual'){
            this.isPassword=true;
            this.userMaintenance.get('password').setValue(resp.encryptedPassword)
          }else{
           this.isPassword=false;
          }
           this.auditData = resp
           this.isAuditData = true;
           this.dataService.setAuditLogData(this.auditData);
           console.log("this.auditData", this.auditData)
        });
      }
      else{
        this.readorWrite=false;
        console.log("else");
       
      }
    })
    this.loggedInUser = this.ls.getItem('ICUST_USER').userName;
  }

  getRoleId(){
    this.api.fetchAuthrorizedRoleInfo().subscribe((data) => {
      this.RoleInfo=data;
      console.log(this.RoleInfo);
    })
  }
  getTillId() {
    let tillOrVault ="T";
    this.api.getTillData(tillOrVault).subscribe((resp) => {
      this.till = resp;
    });
  }

  buildUserForm(data?){

    var role
    if(data){
      role= this.RoleInfo.filter((item)=>{
        return data.roleId == item.id;
      });
    }
    this.tillData = this.till.find(x => x.id == data.tillId);
    console.log("this.tillData::",this.tillData)
    console.log("role::",role)
    this.userMaintenance = this.formBuilder.group({
      userName: [data ? data.userName : '',[Validators.required]],
      email: [data ? data.email : '',[Validators.required]],
      mobile: [data ? data.mobile : '',[Validators.required]],
      entityCode:[data ? data.entityCode : '',[Validators.required]],
      branchCode:[data ? data.branchCode : '',[Validators.required]],
      bankCode:[data ? data.bankCode : '',[Validators.required]],
      roleId: [data ? role[0].roleName : '',[Validators.required]],
      firstName: [data ? data.firstName : '',[Validators.required]],
      lastName: [data ? data.lastName : '',[Validators.required]],
      language: [data ? data.language : ''],
      userId: [data ? data.userId : ''],
      password: [data ? data.password : ''],
      passwordGenerationType: [data ? data.passwordGenerationType : ''],
      tillId: [data ? this.tillData.tillVaultCode : '',[Validators.required]],
    });

  }
  onInput(event:any){
    console.log(event);
    this.UserName= event.target.value.toLocaleUpperCase();
    this.userMaintenance.get('userName').setValue(this.UserName)
  }
  onToggle(event){
    console.log(event.value);
    if(event.value=='Manual'){
this.isPassword=true;
    }
    else{
      this.isPassword=false;
    }
  }

  submit(){
    console.log(this.userMaintenance.value);
    this.tillData = this.till.find(x => x.tillVaultCode == this.userMaintenance.value.tillId);
    this.userMaintenance.value.tillId = this.tillData.id;
    this.userMaintenance.value.loggedInUser = this.loggedInUser;
    this.userMaintenance.value.userId=parseInt(this.userMaintenance.value.userId);
    this.userMaintenance.value.loggedInUser = this.loggedInUser;
    var role=this.RoleInfo.filter((item)=>{
      return item.roleName==this.userMaintenance.value.roleId
    })
    this.userMaintenance.value.roleId= role[0].id;
    this.api.upsertUserMaintenance(this.userMaintenance.value).subscribe(response => {
      console.log("response status::",response.status)
      if(response != null){
        this.spinner.hide();
        // if(response.status == 200){
          this.snack.open(`User Added!'`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });

          this.router.navigate(["/maintenance/main/userMaintenance"]);
        // } else if (response.status == 400) {
        //     this.snack.open('User already exists for the given role!', 'OK', { 
        //       duration: 4000,
        //       verticalPosition: "top",
        //       horizontalPosition: "right",
        //      })
        // }
        this.cdr.markForCheck();
      }
    });
  }
  goBack(){
    this.router.navigate(["/maintenance/main/userMaintenance"]);
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  deleteRecord(){
   
    this.api.deleteUserById(this.auditData.id)
      .subscribe(data => {
      },
      err=>{
        if (err.status == 200) {
          this.snack.open('Record Deleted Successfully', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
        }
        this.router.navigate(["/maintenance/main/userMaintenance"]);
      
    })
 
}
  editRecord(){
    this.readorWrite=false;
  }

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
        payload.id = this.id;
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser;
        this.api.getAuditLogsForUserMaintenance(payload).subscribe((data) => {
          this.auditData = data;
          this.dataService.setAuditLogData(this.auditData);
          this.isAuditData = true;
          this.dataService.sendClickEvent();
          let message = '';
          switch (operation) {
            case 'open':
              message = 'opened'
              break;
            case 'close':
              message = 'closed'
              break;
            case 'auth':
              message = 'authorized'
              break;
            default:
              break;
          }
          this.snack.open(`Record ${message} successful!'`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        });
      }
      
    }
    ))
   
  }

}
