import { Component, EventEmitter, Input, OnInit, Output, SimpleChanges } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import * as moment from 'moment';
import { Subject } from 'rxjs';
import { HolidayListDialogComponent } from '../holiday-list-dialog/holiday-list-dialog.component';

@Component({
  selector: 'app-custom-calender',
  templateUrl: './custom-calender.component.html',
  styleUrls: ['./custom-calender.component.scss']
})
export class CustomCalenderComponent implements OnInit {
  monthArr: string[][] = [
    ['January', 'February', 'March'],
    ['April', 'May', 'June'],
    ['July', 'August', 'September'],
    ['October', 'November', 'December'],
  ];
  newYearArrayany: any[] = [];
  months: string[] = [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
  ];
  weekdays: string[] = [
    'Sunday',
    'Monday',
    'Tuesday',
    'Wednsday',
    'Thursday',
    'Friday',
    'Saturday',
  ];
  monthEnds = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
  viewDate: Date = new Date();
  yearlyCalendarArr = [];
  currentMonth: string;
  currentMonthDate: number;
  calendarDate: string[] = [];
  dateAlreadyActive : string;
  year: number;
  constructor(public dialog: MatDialog) { }
  @Input() public addBranchHolidayFormData: any;
  @Input() resetFormSubject: Subject<boolean> = new Subject<boolean>();
  @Output() onDatePicked = new EventEmitter<any>();
  ngOnInit() {
    // this.fillInYear(2022);
    // console.log(this.yearlyCalendarArr);
    // console.log(this.newYearArrayany);
    // console.log('calendarDate :: ', this.calendarDate);
    this.onDatePicked.emit(this.calendarDate);
    this.currentMonth = this.months[this.viewDate.getMonth()];
    this.currentMonthDate = this.viewDate.getDate();
  }
  ngOnChanges(changes: SimpleChanges) {
    console.log('addBranchHolidayFormData :: ',this.addBranchHolidayFormData);
    if(this.addBranchHolidayFormData !=null){
      this.fillInYear(this.addBranchHolidayFormData.data.year);
      this.currentMonth = this.months[this.viewDate.getMonth()];
      this.currentMonthDate = this.viewDate.getDate();
    }
  }

  fillInMonth(year: number, month: string, dayAmount: number) {
    this.year=year;
    //do leep year too
    let monthNumsArr = [];
    let endOffSet = 42 - dayAmount;
    //let month_name = document.getElementsByClassName("month-name")[0];
    //if(month_name.innerHTML === month){
    //let calendar_day = document.getElementsByClassName("calendar-box")
    let date = new Date(month + '1, ' + year);
    // console.log('date :: ', date, ' month :: ', month);
    let dateOffSet = date.getDay();

    for (let i = 0; i < dayAmount + endOffSet; i++) {
      // console.log('i :: ', i);

      if (i < dateOffSet) {
        //calendar_day[i].innerHTML = " ";
        monthNumsArr.push(' ');
      } else if (i > dayAmount + dateOffSet - 1) {
        //calendar_day[i].innerHTML = " ";
        monthNumsArr.push(' ');
      } else {
        monthNumsArr.push((i + 1 - dateOffSet).toString());
        //calendar_day[i].innerHTML = ((i+1) - dateOffSet).toString();
      }
    }
    monthNumsArr.forEach((V, K) => {
      if ((K % 7 == 0) || ((8 + K) % 7 == 0)) {
        if (V != ' ') {
          let dateValue = (+V);
          var d = new Date(year, date.getMonth(), dateValue);
          var responseDate = moment(d).format('YYYY-MM-DD');
          this.calendarDate.push(responseDate);
        }
      }
    });
    return monthNumsArr;
    //}
  }


  fillInYear(year: number) {
    let tempMonthArr = [];
    this.yearlyCalendarArr=[];
    this.newYearArrayany=[];
    for (let i = 0; i < 12; i++) {
      // console.log('i-> ', (i + 1) % 3)
      this.yearlyCalendarArr.push(
        this.fillInMonth(year, this.months[i], this.monthEnds[i])
      );

      if ((i + 1) % 3 == 0) {
        this.newYearArrayany.push(this.yearlyCalendarArr);
        this.yearlyCalendarArr = [];
      }
    }
  }

  captureDate(dateFromForm,month,year) {
    // console.log('date :: ', dateFromForm,' m :: ',month, ' y:: ', year);
    let date = new Date(month + '1, ' + year);
    let dateValue = (+dateFromForm);
    var d = new Date(year, date.getMonth(), dateValue);
    var responseDate = moment(d).format('YYYY-MM-DD');
    if(responseDate == this.dateAlreadyActive){
        return
    }
    this.dateAlreadyActive = responseDate;
    this.calendarDate.push(responseDate);
    this.onDatePicked.emit(this.calendarDate);
    // console.log('captured :: ',this.calendarDate);
    const dialogRef = this.dialog.open(HolidayListDialogComponent, {
      data: { date: d},
      position: { right: '0' },
      panelClass: 'customHolidayListStyle',
      hasBackdrop: false
    });

    dialogRef.afterClosed().subscribe(result => {
      this.dateAlreadyActive = null;
      // this.animal = result;
    });
  }



}
