import {
  Component,
  ElementRef,
  OnInit,
  ViewChild,
  OnDestroy,
} from "@angular/core";
import { MatDialog, MatDialogRef } from "@angular/material/dialog";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Router } from "@angular/router";
import { rumangoAnimations } from "app/shared/animations/rumango-animations";
import { ApiService } from "app/shared/services/api.service";
import { AppConfirmService } from "app/shared/services/app-confirm/app-confirm.service";
import { AppLoaderService } from "app/shared/services/app-loader/app-loader.service";
import { DataService } from "app/shared/services/data.service";
import { Subscription } from "rxjs";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort, Sort } from "@angular/material/sort";
import { LiveAnnouncer } from "@angular/cdk/a11y";
import { MatTableDataSource } from "@angular/material/table";

import { BaseDenominationPopupComponent } from "../../base-denomination/base-denomination-popup/base-denomination-popup.component";
import { DownloadService } from "app/shared/services/download.service";



@Component({
  selector: "app-till-vault-denomination-summary",
  templateUrl: "./till-vault-denomination-summary.component.html",
  styleUrls: ["./till-vault-denomination-summary.component.scss"],
  animations: rumangoAnimations,
})
export class TillVaultDenominationSummaryComponent implements OnInit {
  @ViewChild("searchVal") searchVal: ElementRef;
  selectedFilterIndex: number = 0;
  displayedColumns: string[] = [ 'bankCode', 'entityCode', 'authStatus','recordStatus',  'action'];

  filterByOption: { key: string; value: string }[] = [
    { key: "entityCode", value: "Entity Code" },
  ];
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;

  dataSource = new MatTableDataSource();


  public items: any[];
  public getItemSub: Subscription;
  users: any[] = [];
  currency: string[] = [];
  till: any;
  branchCode: string[] = [];
  bankCode: string[] = [];
  fetchVaultDenom: object;
  loading: boolean;
  data: any;
  tillData: any;
  constructor(
    private dialog: MatDialog,
    private snack: MatSnackBar,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,
    private apiService: ApiService,
    private router: Router,
    private dataService: DataService,
    private _liveAnnouncer: LiveAnnouncer,
    private downloadService: DownloadService
  ) {}

  ngOnInit() {
    this.getItems();
    this.getTellVaultDenomination();

    this.getCurrency();
    this.getBankCode();
    this.getBranchCode();
    this.getTillId();
  }

  ngOnDestroy() {
    if (this.getItemSub) {
      this.getItemSub.unsubscribe();
    }
  }
  getItems() {
    this.items = this.users;
  }

  doFilter(filterBy, e) {
    console.log(filterBy, e);
    this.filterTable(filterBy);
    this.applyFilter(e);
  }

  filterTable(filterBy) {
    this.dataSource.filterPredicate = (data: any, filter: string): boolean => {
      return data[filterBy].toLocaleLowerCase().includes(filter);
    };
  }

  clearFilter() {
    this.dataSource.filter = "";
    this.searchVal.nativeElement.value = "";
    this.selectedFilterIndex = 0;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  announceSortChange(sortState: Sort) {
    if (sortState.direction) {
      this._liveAnnouncer.announce(`Sorted ${sortState.direction}ending`);
    } else {
      this._liveAnnouncer.announce("Sorting cleared");
    }
  }
  onFileChange(event) {
    console.log('event', event.path[0].files[0]);
    const file: File = event.path[0].files[0];
    const uploadData = new FormData();
    uploadData.append('screenType', 'tillVaultDenom');
    uploadData.append('file', file);
    this.apiService.uploadExelFile(uploadData).subscribe((response) => {
      console.log(response);
      this.getTellVaultDenomination();
      this.snack.open(`File Uploaded Successfully`, "OK", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
  }
 
  tillVaultDenomination(){
    
    this.downloadService.downloadFiles("tillVaultDenomination", this.users );
    


  }



     

  deleteItem(row) {
    this.confirmService
      .confirm({ message: `Delete ${row.bankCode}?` })
      .subscribe((res) => {
        if (res) {
          this.loader.open();
          this.apiService.deleteTellerVaultDenomination(row.id).subscribe(
            (data) => {},
            (err) => {
              if (err.status === 200) {
                this.loader.close();
                this.snack.open(`Record Deleted!'`, "OK", {
                  duration: 2000,
                  verticalPosition: "top",
                  horizontalPosition: "right",
                });
                this.getTellVaultDenomination();
              }
            }
          );
        }
      });
  }

  addNew() {
    this.router.navigate(["/maintenance/main/tillVaultDenom/add"]);
  }

  openPopUp(row) {
    console.log(row);
    this.fetchTellVaultDenomination(row);
    // this.dataService.sendNavParam(this.fetchVaultDenom);
    this.router.navigate([`/maintenance/main/tillVaultDenom/${row.id}`]);
  }

  goBack() {
    this.router.navigate(["/maintenance/main/"]);
  }

  getTellVaultDenomination() {
    this.loading = true;
    this.apiService.getTellerVaultDenomination().subscribe((resp) => {
     
      for(let i=0;i<resp.length;i++){
        if(resp[i].authStatus == 'U'){
          resp[i].authStatus = 'Unauthorized'
        }
        if(resp[i].authStatus == 'A') {
          resp[i].authStatus = 'Authorized'
        } 
        if(resp[i].recordStatus == 'O'){
          resp[i].recordStatus = 'Open'
        } 
        if(resp[i].recordStatus == 'C'){
          resp[i].recordStatus = 'Closed'
        }
      }
      this.users = resp;
      this.items = this.users;
      this.dataSource = new MatTableDataSource(resp);
      console.log(this.dataSource);
      this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
      this.loading = false;
    });
  }

  fetchTellVaultDenomination(row) {
    this.loader.open();
    this.apiService.getTellerVaultDenomById(row.id).subscribe((resp) => {
      this.data = resp;
      console.log("this.till::", this.till);
      console.log("this.data::", this.data);
      this.tillData = this.till.find((x) => x.id == this.data.tillId);
      console.log("this.data::", this.tillData);
     // this.data.tillId = this.tillData.tillVaultCode;
      this.fetchVaultDenom = this.data;
      // console.log(resp);
      // this.fetchVaultDenom = resp;

      if (this.fetchVaultDenom != null) {
        localStorage.setItem(
          "fetchVaultDenom",
          JSON.stringify(this.fetchVaultDenom)
        );
        console.log(this.fetchVaultDenom);
        this.loader.close();
        // this.dataService.shareAccTypeItem(JSON.stringify(this.fetchAccTypeValue));
      }
    });
  }

  getCurrency() {
    this.apiService.getCountryValues().subscribe((resp) => {
      this.currency = resp;
    });
  }
  getBankCode() {
    this.apiService.getBankCode().subscribe((resp) => {
      this.bankCode = resp;
    });
  }
  getBranchCode() {
    this.apiService.getBranchCode().subscribe((resp) => {
      this.branchCode = resp;
    });
  }
  getTillId() {
    let tillOrVault = "T";
    this.apiService.getTillData(tillOrVault).subscribe((resp) => {
      console.log("tillData", resp);
      this.till = resp;
    });
  }
}
