import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import Swal from 'sweetalert2';
import { AuditLogComponent } from '../../audit-log/audit-log.component';

@Component({
  selector: 'app-add-edit-city',
  templateUrl: './add-edit-city.component.html',
  styleUrls: ['./add-edit-city.component.scss']
})
export class AddEditCityComponent implements OnInit {
  cityId: any;
  cityMaintenance: FormGroup;
  countryArr: any;
  readorWrite: boolean = true;
  stateArr: any;
  loggedInUser: any;
  countryId: number;
  auditLogData: any;

  @ViewChild('auditLog') auditLog: AuditLogComponent
  constructor(
    private router: Router,
    private fb: FormBuilder,
    private apiService: ApiService,
    private route: ActivatedRoute,
    private ls: LocalStoreService,
    private dataService: DataService,
    private snack: MatSnackBar

  ) { }

  ngOnInit(): void {
    this.loggedInUser = this.ls.getItem('ICUST_USER');
    this.getAllCountries();
    this.route.queryParamMap.subscribe((params: any) => {
      if (params.get('cityId')) {
        this.cityId = params.get('cityId');
        this.apiService.getCityById(this.cityId).subscribe(resp => {
          console.log(resp.countryName);
          setTimeout(() => {
            this.getStateName(resp.countryName)
          }, 100);
          this.buildCityForm(resp);
          console.log(this.cityMaintenance);
          this.auditLogData = resp;
          this.dataService.setAuditLogData(this.auditLogData);
        })
      }
      else {
        console.log('else');
        this.readorWrite = false;
        this.buildCityForm({})
      }
    })

  }

  buildCityForm(item) {
    this.cityMaintenance = this.fb.group({
      id: [item ? item.id : ''],
      countryName: [item ? item.countryName : '', Validators.required],
      cityName: [item ? item.cityName : '', Validators.required],
      stateName: [item ? item.stateName : '', Validators.required],
      recordStatus: [item ? item.recordStatus : ''],
      pinCode: [item ? item.pinCode : '', Validators.required]
    });
  }

  getAllCountries() {
    this.apiService.getAllCountries().subscribe(resp => {
      this.countryArr = resp.filter(res => res.authStatus == 'A');
    })
  }

  getStateName(countryName) {
    this.countryId = this.countryArr[this.countryArr.findIndex(country => country.countryName == countryName)].id
    this.apiService.getStateByCountry(this.countryId).subscribe(resp => {
      this.stateArr = resp.filter(res => res.authStatus == 'A')
    })
  }

  _keyPress(event) {
    var inp = String.fromCharCode(event.keyCode);
    if (/[a-zA-Z\s]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }

  _keyPress1(event) {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }
  editRecord() {
    this.readorWrite = false;
  }
  submit(data: any) {
    data.loggedInUser = this.loggedInUser.userName;
    data.countryId = this.countryId
    if (this.cityId) {
      data.cityId = this.cityId;
    }
    console.log(data);

    this.apiService.upsertCities(data).subscribe((resp) => {
      if (resp) {
        this.router.navigate(["/maintenance/main/cityMaintenance"]);
      }
    });
  }
  updateRecord(operation) {
    Swal.fire(
      {
        text: 'You are trying to ' + operation + ' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if (result.isConfirmed == true) {
        let payload: any = {};
        payload.id = this.cityId;
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser;
        this.apiService.getAuditLogsForBranch(payload).subscribe((data) => {

          this.auditLogData = data;
          this.dataService.setAuditLogData(this.auditLogData);
          this.dataService.sendClickEvent();
          let message = '';
          switch (operation) {
            case 'open':
              message = 'opened'
              break;
            case 'close':
              message = 'closed'
              break;
            case 'auth':
              message = 'authorized'
              break;
            default:
              break;
          }
          this.snack.open(`Record ${message} successful!'`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        });
      }
    }))

  }
  goBack() {
    this.router.navigate(["/maintenance/main/cityMaintenance"]);
  }
}
