import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { denomMaster } from 'app/shared/models/denomMaster';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { DemonMasterCrudNgxTableComponent } from '../demon-master-crud-ngx-table.component';
import Swal from 'sweetalert2';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';


@Component({
  selector: 'app-denom-master-table-popup',
  templateUrl: './denom-master-table-popup.component.html',
  styleUrls: ['./denom-master-table-popup.component.scss']
})
export class DenomMasterTablePopupComponent implements OnInit {
  public itemForm: FormGroup;
  

  currentUser: string;
  denomMasterStatus: denomMaster;
  currency:  string[] = [];
  bankCode:  string[] = [];
  entityCode:  string[] = [];
  id: number;
  loggedInUser :string;
  auditData : denomMaster;
  isAuditData : boolean = false;
  show = false;
  isEdit: boolean = false;
  readorWrite: boolean=true;
  patternValidation: boolean=true;
  constructor(
    // @Inject(MAT_DIALOG_DATA) public data: any,
    // public dialogRef: MatDialogRef<DemonMasterCrudNgxTableComponent>,
    private fb: FormBuilder,private api:ApiService,private loader: AppLoaderService,
    private snack: MatSnackBar,
    private router:Router,
    private ls:LocalStoreService,
    private apiService: ApiService,
    private dataService:DataService,
  ) { }

  ngOnInit() {
    this.currentUser = this.ls.getItem("ICUST_USER");
    // this.dataService.getDenomMasterStatus.subscribe(data =>{
    //   this.denomMasterStatus = typeof this.denomMasterStatus === 'object' ? data : {};
    // });

    setTimeout(() => {  
      this.denomMasterStatus = JSON.parse(localStorage.getItem('fetchdenomValue'));
      if(this.denomMasterStatus == null || this.denomMasterStatus == undefined){
        this.readorWrite=false;
        this.buildItemForm({})
      }       
      else if(this.denomMasterStatus.id != null || this.denomMasterStatus.id != undefined){
      this.loader.open();
      this.id = this.denomMasterStatus.id;
      this.buildItemForm(this.denomMasterStatus)
      this.isEdit = true;
      if(this.denomMasterStatus.authStatus == "U"){
        this.denomMasterStatus.authDate = ""
        this.denomMasterStatus.authBy = ""
      };
      this.auditData = this.denomMasterStatus;
      this.isAuditData = true;
      this.dataService.setAuditLogData(this.auditData);
      this.loader.close();
      }
    }, 1000)

    this.getBankCode();
    this.getCurrencyValue();
    this.getEntityCode();
    this.loggedInUser = this.ls.getItem('ICUST_USER').userName;
    // this.countryCurrency=this.data.currencyValues;
    // this.bankCodevalues=this.data.bankCode;
  }
  buildItemForm(item) {
    this.itemForm = this.fb.group({
      authStatus : [item.authStatus || '',],
      bankCode	: [item.bankCode || '', [Validators.required]],
      currency	: [item.currency || '',Validators.required],
      denomCode	: [item.denomCode || '',Validators.required],
      denomDesc	: [item.denomDesc || '',Validators.required],
      denomValue	: [item.denomValue || '',Validators.required],
      entityCode: [item.entityCode || '',[Validators.required]],
      id	: [item.id || null],
      oneTimeAuth	: [item.oneTimeAuth || ''],
      recordStatus	: [item.recordStatus || ''],
      version	: [item.version || ''],

    })
  }

  submit() {
    if(this.itemForm.invalid){
      return;
    }else{
      this.loader.open();
      this.itemForm.value.loggedInUser = this.loggedInUser;
      this.api.saveDenomMaster(this.itemForm.value).subscribe((resp) => {
      this.loader.close();
      this.router.navigate([`/maintenance/main/denominationMaster`]).then(_=>{
          this.snack.open('Data Saved Successfully!', 'OK', 
          { duration: 4000 , 
            verticalPosition: "top",
            horizontalPosition: "right",});
        });
      },(err:any) =>{
        this.loader.close();
        this.snack.open(`Error!`, 'OK', { duration: 4000,  
          verticalPosition: "top",
          horizontalPosition: "right",});
        }
      )
      } 
   
  }

  getCurrencyValue(){
    // this.api.getCountryValues().subscribe((currencyValues : any)=> {
    //   this.currency=currencyValues
    //   });
    this.apiService.getCurrencyDetails().subscribe(data=>{
      console.log("Auth currecy details", data)
      for(let i=0; i<data.length;i++){
        this.currency.push(data[i].currencyId)
      }
      console.log(this.currency);
    })
  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  getBankCode(){
    this.api.getBankCode().subscribe((bnkCode : any)=> {
      console.log(bnkCode);
      for(let i=0; i<bnkCode.length;i++){
        if(bnkCode[i].authStatus==='A'){
          this.bankCode.push(bnkCode[i]);
        }
      }
      console.log(this.bankCode);
      // this.bankCode=bnkCode
      });
  }

  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteDenomMaster(this.auditData.id)
        .subscribe(data => {
         this.router.navigate([`/maintenance/main/denominationMaster`]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
         this.router.navigate([`/maintenance/main/denominationMaster`]);
        
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
        payload.id = this.id;
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser;
        this.apiService.getAuditLogsForDenominationMaster(payload).subscribe((data) => {
          if( data.authStatus=="U" ){
            data.authDate =""
            data.authBy=""
          }
          this.auditData = data;
          this.dataService.setAuditLogData(this.auditData);
          this.isAuditData = true;
          this.dataService.sendClickEvent();
          let message = '';
          switch (operation) {
            case 'open':
              message = 'opened'
              break;
            case 'close':
              message = 'closed'
              break;
            case 'auth':
              message = 'authorized'
              break;
            default:
              break;
          }
    
    
          this.snack.open(`Record ${message} successfully!`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        });
      }}))
   
  }

  _keyPress1(event: any) {
    this.patternValidation=true;
    const pattern = /[A-Z]{2}[0-9]{1,8}$/;
    if(pattern.test(event.target.value) === false){
      this.patternValidation=false;
    }
    else{
      this.patternValidation=true;
    }
  }
  getEntityCode() {
    this.api.getEntityCode().subscribe((resp) => {
      console.log(resp);
      for(let i=0; i<resp.length;i++){
        if(resp[i].authStatus==='A'){
          this.entityCode.push(resp[i]);
        }
      }
      console.log(this.entityCode);
      // this.entityCode = resp;
    });
  }

  goBack(){
    localStorage.removeItem('fetchdenomValue');
    this.router.navigate([`/maintenance/main/denominationMaster`]);
  }
  dataByEntity(index,data){
    return data.entityCode;
  }
  dataByBank(index,data){
    return data.bankCode;
  }
  dataByCurrency(index,data){
    return data.currencyCode;
  }

}
