import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-till-vault-ccy-popup',
  templateUrl: './till-vault-ccy-popup.component.html',
  styleUrls: ['./till-vault-ccy-popup.component.scss']
})
export class TillVaultCcyPopupComponent implements OnInit {

  arrayCurrency: any[] = [];
  countryCurrency: any[]=[];
  // tillId:number[]=[];
  branchCode:string[]=[];
  bankCode:string[]=[];
  currency: string[] = [];
  till: any[] = [];
  public tillccyBalForm: FormGroup;
  tillVaultResponse: any;
  currentUser: any;
  CcyBalStatus: any;
  entity: any;
  entityCode: string[] = [];
  tillData: any;
  entityName: string;
  loggedInUser : any;
  auditData : any;
  isAuditData : boolean = false;
  id: any;
  isEdit: boolean = false;
  readorWrite: boolean=true;
  constructor(
    // @Inject(MAT_DIALOG_DATA) public data: any,
    // public dialogRef: MatDialogRef<TillVaultCcyPopupComponent>,
    private fb: FormBuilder,
    private apiService: ApiService,
    private loader: AppLoaderService,
    private snack: MatSnackBar,
    private router:Router,
    private ls:LocalStoreService,
    private dataService:DataService,
  ) { }

  ngOnInit() {

    this.currentUser = this.ls.getItem("ICUST_USER");
    this.entity ="entityCode"
    // this.dataService.getCcyBal.subscribe(data =>{
    //   console.log(data)
    //   this.CcyBalStatus = typeof this.CcyBalStatus === 'object' ? data : {};
    // });

    setTimeout(() => {  
      this.CcyBalStatus = JSON.parse(localStorage.getItem('fetchCcyBalValue'));
      console.log('output',this.CcyBalStatus);
      if(this.CcyBalStatus == null || this.CcyBalStatus == undefined){
        console.log(this.CcyBalStatus)
        this.readorWrite=false;
        this.buildItemForm({})
      }       
      else if(this.CcyBalStatus.id != null || this.CcyBalStatus.id != undefined){
      this.loader.open();
      console.log(this.CcyBalStatus.id)
      this.id = this.CcyBalStatus.id
      this.buildItemForm(this.CcyBalStatus)
      this.auditData = this.CcyBalStatus
      this.isEdit = true;
      this.isAuditData = true;
      this.dataService.setAuditLogData(this.auditData);
      this.loader.close();
      this.loader.close();
      }
    }, 1000)

    this.getCurrency();
    // this.getBankCode('entityCode');
    // this.getBranchCode('entityCode');
    this.getTillId();
    this.getEntityCode();
    this.getBranchCode();
    this.getBankCode();

    this.loggedInUser = this.ls.getItem('ICUST_USER');
    console.log(this.loggedInUser);
   
  }

  buildItemForm(item) {
    this.tillccyBalForm = this.fb.group({
      entityCode: [item.entityCode || '',[Validators.required]],
      bankCode: [item.bankCode || '', [Validators.required]],
      branchCode: [item.branchCode || '',[Validators.required]],
      tillId: [item.tillId || '',Validators.required],
      currency: [item.currency || '',[Validators.required]],
      glAccount: [item.glAccount || '',Validators.required],
      openBalInAcy: [item.openBalInAcy || '',Validators.required],
      openBalInLcy: [item.openBalInLcy || '',Validators.required],
      balInAcy: [item.balInAcy || '',Validators.required],
      balInLcy: [item.balInLcy || '',Validators.required],
      id:[item.id || null]
    })
  }
  numericValidation(event){
    var inp = String.fromCharCode(event.keyCode);
    if (/[0-9]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }
  alhanumericValidation(event){
    var inp = String.fromCharCode(event.keyCode);
    if (/[a-zA-Z0-9]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }
// Save of tillVaultCurrency Bal
  submit() {
    if(this.tillccyBalForm.invalid){
      return;
    }else{
      this.loader.open();
      this.tillData = this.till.find(x => x.tillVaultCode == this.tillccyBalForm.value.tillId);
      this.tillccyBalForm.value.loggedInUser= this.loggedInUser.userName;
      this.tillccyBalForm.value.tillId = this.tillData.id;
      console.log(this.tillccyBalForm.value);
      this.apiService.saveoftillVaultCurrencyBal(this.tillccyBalForm.value).subscribe((resp) => {
      console.log("data", resp);
      this.loader.close();
      this.router.navigate([`/maintenance/main/tillVaultCcyBal`]).then(_=>{
          this.snack.open('Data Saved Successfully!', 'OK', 
          { duration: 4000 , 
            verticalPosition: "top",
            horizontalPosition: "right",});
        });
      },(err:any) =>{
        this.loader.close();
        this.snack.open(`Error!`, 'OK', { duration: 4000,  
          verticalPosition: "top",
          horizontalPosition: "right",});
        }
      )
      }
  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.removetillVaultCurrencyBal(this.auditData.id)
        .subscribe(data => {
          this.router.navigate([`/maintenance/main/tillVaultCcyBal`]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
          this.router.navigate([`/maintenance/main/tillVaultCcyBal`]);
        
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        console.log(this.id);
        let payload: any = {};
        payload.id = this.id;
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser.userName;
        this.apiService.getAuditLogsForCurrencybal(payload).subscribe((data) => {
          console.log(data);
          this.auditData = data;
          this.dataService.setAuditLogData(this.auditData);
          this.isAuditData = true;
          this.dataService.sendClickEvent();
          let message = '';
          switch (operation) {
            case 'open':
              message = 'opened'
              break;
            case 'close':
              message = 'closed'
              break;
            case 'auth':
              message = 'authorized'
              break;
            default:
              break;
          }
          this.snack.open(`Record ${message} Successfully!'`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        });
      }}))
   
  }

  goBack(){
    localStorage.removeItem('fetchCcyBalValue');
    console.log( localStorage.getItem('fetchCcyBalValue'));

    this.router.navigate([`/maintenance/main/tillVaultCcyBal`]);
  }

  getCurrency() {
    // this.apiService.getCountryValues().subscribe((resp) => {
    //   this.currency = resp;
    // });
    this.apiService.getCurrencyDetails().subscribe(data=>{
      console.log("Auth currecy details", data)
      for(let i=0; i<data.length;i++){
        this.currency.push(data[i].currencyId)
      }
      console.log(this.currency);
    })
  }
  getBankCode() {
    this.apiService.getBankCode().subscribe((resp) => {
      console.log(resp);
      for(let i=0; i<resp.length;i++){
        if(resp[i].authStatus==='A'){
          this.bankCode.push(resp[i]);
        }
      }
      console.log(this.bankCode);
      // this.bankCode = resp;
    });
  }
  // getBankCode(entityCode) {
  //   //  this.entityName = "bankCode"
  //   this.apiService.getBankCode1(entityCode).subscribe((resp) => {
  //     this.bankCode = resp;
  //     console.log(this.bankCode)
  //   });
  // }
  
 getBranchCode() {
    this.apiService.getBranchCode().subscribe((resp) => {
      for(let i=0; i<resp.length;i++){
        if(resp[i].authStatus==='A'){
          this.branchCode.push(resp[i]);
        }
      }
      console.log(this.branchCode);
      // this.branchCode = resp;
    });
  }
  // getBranchCode(bankCode:any) {
   
  //   this.entityName= this.tillccyBalForm.get('entityCode').value ;
  //   this.apiService.getBranchCode1(this.entityName,bankCode).subscribe((resp) => {
  //     this.branchCode = resp;
  //     console.log(this.branchCode)
  //   });
  // }
  getTillId() {
    let tillOrVault ="T";
    this.apiService.getTillData(tillOrVault).subscribe((resp) => {
      this.till = resp;
    });
  }

  getEntityCode() {
    this.apiService.getEntityCode().subscribe((resp) => {
      console.log(resp);
      for(let i=0; i<resp.length;i++){
        if(resp[i].authStatus==='A'){
          this.entityCode.push(resp[i]);
        }
      }
      console.log(this.entityCode);
      console.log("bankCodeData", resp)
      // this.entityCode = resp;
    });
  }
}
