import { Component, OnInit, ElementRef, ViewChild } from "@angular/core";
import { LiveAnnouncer } from "@angular/cdk/a11y";
import { MatPaginator } from "@angular/material/paginator";
import { MatSnackBar } from "@angular/material/snack-bar";
import { MatSort, Sort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Router } from "@angular/router";
import { ApiService } from "app/shared/services/api.service";
import { AppConfirmService } from "app/shared/services/app-confirm/app-confirm.service";
import { DataService } from "app/shared/services/data.service";
import { Subscription } from "rxjs";

@Component({
  selector: "app-currency-paira-rates-maintenance",
  templateUrl: "./currency-paira-rates-maintenance.component.html",
  styleUrls: ["./currency-paira-rates-maintenance.component.scss"],
})
export class CurrencyPairaRatesMaintenanceComponent implements OnInit {
  @ViewChild("searchVal") searchVal: ElementRef;
  selectedFilterIndex: number = 0;

  dataSource = new MatTableDataSource();
  displayedColumns: string[] = ['pairRateId', 'branchCode', 'rateType', 'rateDate','oneTimeAuth', 'authStatus', 'recordStatus','action'];

  filterByOption: { key: string; value: string }[] = [
    { key: "pairRateId", value: "Currency Pair Rate Id" },
    { key: "branchCode", value: "Branch Code" },
  ];

  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  currencyPairId: any = [];
  branchCode: string[] = [];
  rateType: string[] = [];
  users: any[] = [];
  public items: any[];
  entity: any;
  tillVaultMasterValues: any;
  fetchTillVault: any;
  fetchCurrencyPairRates: any;
  currencyPairMaintValues: any;
  public getItemSub: Subscription;
  id: any;

  constructor(
    private router: Router,
    private _liveAnnouncer: LiveAnnouncer,
    private confirmService: AppConfirmService,
    private apiService: ApiService,
    private snack: MatSnackBar,
    private dataService: DataService
  ) {}

  ngOnInit(): void {
    this.fetchCurrencyPair();
    this.getBranchCode();
    this.getItems();
  }

  ngOnDestroy() {
    if (this.getItemSub) {
      this.getItemSub.unsubscribe();
    }
  }

  getItems() {
    this.apiService.getCurrencyPairRates().subscribe((currencyPairRatesInfo: any) => {
        if (currencyPairRatesInfo) {
          for(let i=0;i<currencyPairRatesInfo.length;i++){
            if(currencyPairRatesInfo[i].authStatus == 'U'){
              currencyPairRatesInfo[i].authStatus = 'Unauthorized'
            }
            if(currencyPairRatesInfo[i].authStatus == 'A') {
              currencyPairRatesInfo[i].authStatus = 'Authorized'
            } 
            if(currencyPairRatesInfo[i].recordStatus == 'O'){
              currencyPairRatesInfo[i].recordStatus = 'Open'
            } 
            if(currencyPairRatesInfo[i].recordStatus == 'C'){
              currencyPairRatesInfo[i].recordStatus = 'Closed'
            }
          }
          this.currencyPairMaintValues = currencyPairRatesInfo;
          this.items = this.currencyPairMaintValues;
          console.log("items4::", this.items);
          this.dataSource = new MatTableDataSource(currencyPairRatesInfo);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        }
      });
  }

  onFileChange(event) {
    console.log('event', event.path[0].files[0]);
    const file: File = event.path[0].files[0];
    const uploadData = new FormData();
    uploadData.append('screenType', 'currencyPairRate');
    uploadData.append('file', file);
    this.apiService.uploadExelFile(uploadData).subscribe((response) => {
      this.getItems();
      console.log(response);
      this.snack.open(`File Uploaded Successfully`, "OK", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
   
  }

  fetchCurrencyPair(){
    this.apiService.getCurrencyPair().subscribe((resp) =>{
     console.log("currencyPairById",resp);
     this.currencyPairId=resp;
    })
  }

  getBranchCode(){
    this.apiService.getBranchCode().subscribe(branchCodeResp=>{
      this.branchCode=branchCodeResp;
    });
  }

  fetchCurrencyPairRatesById() {
    this.apiService.getCurrencyPairRatesById(this.id).subscribe((resp) => {
      console.log("currencyPairRatesById", resp);
      this.fetchCurrencyPairRates = resp;
    });
    console.log(this.fetchCurrencyPairRates);
    this.dataService.sendNavParam(this.fetchCurrencyPairRates);
    this.router.navigate([`/maintenance/main/addCurrencyPair/${this.id}`]);
  }

  addNew() {
    this.router.navigate(["/maintenance/main/addCurrencyPairRates"]);
  }

  doFilter(filterBy, e) {
    console.log(filterBy, e);
    this.filterTable(filterBy);
    this.applyFilter(e);
  }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    console.log("filterValue", filterValue);
    this.dataSource.filter = filterValue.trim().toLocaleLowerCase();
  }

  clearFilter() {
    this.dataSource.filter = "";
    this.searchVal.nativeElement.value = "";
    this.selectedFilterIndex = 0;
  }

  filterTable(filterBy) {
    this.dataSource.filterPredicate = (data: any, filter: string): boolean => {
      console.log("data :: ", data[filterBy]);
      if (data[filterBy] != null) {
        return data[filterBy].trim().toLocaleLowerCase().includes(filter);
      }
    };
  }

  announceSortChange(sortState: Sort) {
    if (sortState.direction) {
      this._liveAnnouncer.announce(`Sorted ${sortState.direction}ending`);
    } else {
      this._liveAnnouncer.announce("Sorting cleared");
    }
  }
  openEdit(data: any = {}, isEdit: string) {
    console.log("data", isEdit);
    console.log(".id", data.pairRateId);
    console.log("item1::", data);

    this.router.navigate(["/maintenance/main/addCurrencyPairRates"], {
      queryParams: { id: data.pairRateId, isEdit: isEdit },
    });
  }

  deleteItem(element) {
    console.log("element", element.pairRateId);
    this.confirmService
      .confirm({ message: `Delete  ${element.pairRateId}?` })
      .subscribe((res) => {
        if (res) {
          this.apiService.deleteCurrencyPairRates(element.pairRateId).subscribe(
            (data) => {
              this.getItems();
            },
            (err) => {
              if (err.status == 200) {
                this.snack.open("Record Deleted Successfully", "OK", {
                  duration: 4000,
                  verticalPosition: "top",
                  horizontalPosition: "right",
                });
              }
            }
          );
        }
      });
  }

  goBack() {
    this.router.navigate(["/maintenance/main"]);
  }
}
