import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
@Component({
  selector: 'app-till-vault-ccybal-hispopup',
  templateUrl: './till-vault-ccybal-hispopup.component.html',
  styleUrls: ['./till-vault-ccybal-hispopup.component.scss']
})
export class TillVaultCcybalHispopupComponent implements OnInit {
  arrayCurrency: string[] = [];
  countryCurrency: string[]=[];
  tillId:number[]=[];
  branchCode:string[]=[];
  bankCode:string[]=[];
  public ccybalHistoryForm: FormGroup;
  
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<TillVaultCcybalHispopupComponent>,
    private fb: FormBuilder,
    private apiService: ApiService,
    private loader: AppLoaderService,
    private snack: MatSnackBar,
  ) { }

  ngOnInit() {
    this.buildItemForm(this.data.payload);
    this.countryCurrency=this.data.currency;
    this.tillId=this.data.till;
    this.branchCode=this.data.branchCode;
    this.bankCode=this.data.bankCode;
  }

  buildItemForm(item) {
    this.ccybalHistoryForm = this.fb.group({
      entityCode: [item.entityCode || '',Validators.required],
      bankCode: [item.bankCode || '', Validators.required], 
      branchCode: [item.branchCode || '',Validators.required],
      tillId: [item.tillId || '',Validators.required],
      currency: [item.currency || '',[Validators.required]],
      glAccount: [item.glAccount || '',Validators.required],
      txnDate:[item.txnDate || '',Validators.required],
      openBalInAcy: [item.openBalInAcy || '',Validators.required],
      openBalInLcy: [item.openBalInLcy || '',Validators.required],
      balInAcy: [item.balInAcy || '',Validators.required],
      balInLcy: [item.balInLcy || '',Validators.required],
    })
  }
  numericValidation(event){
    var inp = String.fromCharCode(event.keyCode);
    if (/[0-9]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }
  alhanumericValidation(event){
    var inp = String.fromCharCode(event.keyCode);
    if (/[a-zA-Z0-9]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }
// Save of tillVaultCurrencyBalHistory
  submit() {
    let payload = {
      "entityCode": this.ccybalHistoryForm.value.entityCode,
      "bankCode": this.ccybalHistoryForm.value.bankCode,
      "branchCode": this.ccybalHistoryForm.value.branchCode,
      "currency": this.ccybalHistoryForm.value.currency,
      "tillId": this.ccybalHistoryForm.value.tillId,
      "glAccount": this.ccybalHistoryForm.value.glAccount,
      "txnDate": this.ccybalHistoryForm.value.txnDate,
      "openBalInAcy": this.ccybalHistoryForm.value.openBalInAcy,
      "openBalInLcy": this.ccybalHistoryForm.value.glAccount,
      "balInAcy": this.ccybalHistoryForm.value.openBalInAcy,
      "balInLcy": this.ccybalHistoryForm.value.openBalInAcy,
      
    };
      this.dialogRef.close(this.ccybalHistoryForm.value);
      }
      dataByBankCode(index,item){
        return item.bankCode;
      }
      dataByBranchCode(index,item){
        return item.branchCode;
      }
      dataBytillId(index,item){
        return item.tellerId;
      }
      dataByCountry(index,item){
        return item.countryCode;
      }
}
