import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, Router } from '@angular/router';
import { tillVaultCcyDenomBal } from 'app/shared/models/tillVaultCcy-denomBal';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-add-tillvault-ccy-demonbal',
  templateUrl: './add-tillvault-ccy-demonbal.component.html',
  styleUrls: ['./add-tillvault-ccy-demonbal.component.scss']
})
export class AddTillvaultCcyDemonbalComponent implements OnInit {
  addTillVaultCcyForm:FormGroup;
  isEdit: boolean = false;
  id: number;
  loggedInUser: any;
  denom:  string[] = [];
  tillVaultCcyDenomBal:tillVaultCcyDenomBal[] = [];
  auditData: any;
  isAuditData: boolean = false;
  readorWrite: boolean=true;
  constructor(private formBuilder: FormBuilder,private apiService:ApiService,
    private router:Router,private snack: MatSnackBar, private route : ActivatedRoute,
    private ls:LocalStoreService, private dataService:DataService,
    private cdr: ChangeDetectorRef) { this.addTillVaultCcyForm = this.formBuilder.group({
      denomCode: [ '',[Validators.required]],
      openUnitsBal: [ '',[Validators.required]],
      unitsBal: [ '',[Validators.required]],
      loggedInuser : [''],
    idtvcId: [ ''],
    id: [ ''],

  })}

  ngOnInit(): void {
    this.loggedInUser = this.ls.getItem('ICUST_USER').userName;
    this.route.queryParamMap.subscribe((params: any) => {
      if(params.get('id')){
        this.isEdit = true;
        this.id = params.get('id');
        this.apiService.getTillVaultCcyDemonById(params.get('id')).subscribe((resp) =>{
           this.buildItemForm(resp);
           this.auditData = resp;
           console.log( this.auditData)
           this.isEdit = true;
           this.isAuditData = true;
           this.dataService.setAuditLogData(this.auditData);
           
        });
      }
      else{
        this.readorWrite=false;
        this.buildItemForm({});
      }
    }
    );
    this.getDenomCode();
  }
  buildItemForm(item) {
    this.addTillVaultCcyForm = this.formBuilder.group({
      denomCode: [item.denomCode || '',[Validators.required]],
      openUnitsBal: [item.openUnitsBal ||'',[Validators.required]],
      unitsBal: [item.unitsBal ||'',[Validators.required]],
      id: [ item.id ||''],
      tvcId: [ item.tvcId ||''],

    })
  }
  goBack(){
    this.router.navigate(["/maintenance/main/tillVaultCcyDenomBal"]);
  }
  submit(){
    this.addTillVaultCcyForm.value.unitsBal=parseInt(this.addTillVaultCcyForm.value.unitsBal);
    this.addTillVaultCcyForm.value.openUnitsBal=parseInt(this.addTillVaultCcyForm.value.openUnitsBal);
   
   
    let payload={
      denomCode: this.addTillVaultCcyForm.value.denomCode,
      openUnitsBal: this.addTillVaultCcyForm.value.openUnitsBal,
      unitsBal: this.addTillVaultCcyForm.value.unitsBal,
      id:this.addTillVaultCcyForm.value.id,
      tvcId:this.addTillVaultCcyForm.value.tvcId,
      loggedInUser:this.loggedInUser,
      //txnId:101,

    }
    console.log(payload)
  
  this.apiService.updateTillVaultCcyDemonBalDetails(payload).subscribe(response => {
    if(response != null){
      this.snack.open(`Record Added!'`, "OK", {
        duration: 2000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
      this.cdr.markForCheck();
    }
    this.router.navigate(["/maintenance/main/tillVaultCcyDenomBal"]);
  });

  
  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteTillVaultCcyDemonById(this.auditData.id)
        .subscribe(data => {
          this.router.navigate(["/maintenance/main/tillVaultCcyDenomBal"]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
          this.router.navigate(["/maintenance/main/tillVaultCcyDenomBal"]);
        
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }
  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
    payload.id = this.id;
    payload.operation = operation;
    payload.loggedInUser = this.loggedInUser.userName;
    this.apiService.getAuditLogsForTillVaultCcyDenomBal(payload).subscribe((data) => {
      this.auditData = data;
      this.dataService.setAuditLogData(this.auditData);
      this.isAuditData = true;
      this.dataService.sendClickEvent();
      let message = '';
      switch (operation) {
        case 'open':
          message = 'opened'
          break;
        case 'close':
          message = 'closed'
          break;
        case 'auth':
          message = 'authorized'
          break;
        default:
          break;
      }
      this.snack.open(`Record ${message} successfully!'`, "OK", {
        duration: 2000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
      }}))
    
  }
  getDenomCode() {
    this.apiService.fetchDenomMasterAuthRecs().subscribe((resp: any) => {
      console.log(resp);
      for(let i=0; i<resp.length;i++){
        if(resp[i].authStatus==='A'){
          this.denom.push(resp[i]);
        }
      }
      console.log(this.denom);
    });
  }

}
