import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormBuilder, Validators, FormGroup, FormControl } from '@angular/forms';
import { ApiService } from 'app/shared/services/api.service';
import { Location } from '@angular/common';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort, Sort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { LiveAnnouncer } from '@angular/cdk/a11y';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { DataService } from 'app/shared/services/data.service';
import { AuditLogComponent } from '../../audit-log/audit-log.component';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import { auditLogInterface, loggedInUserInterface } from './country-state-popup';
import Swal from 'sweetalert2';

export interface PeriodicElement {
  name: string;
  CountryCode: string;
  countryIsoCode2: string;
  RegionCode: string;
  IBAN : String;
}

@Component({
  selector: 'app-country-state-popup',
  templateUrl: './country-state-popup.component.html',
  styleUrls: ['./country-state-popup.component.scss']
})
export class CountryStatePopupComponent implements OnInit {

  public countryMaintenance: FormGroup;
  banks: string[] = [];
  currencies: string[] = [];
  countries: string[] = [];
  states: string[] = [];
  cities: string[] = [];
  countryCodeList: any[] = [];
  filteredOptions: Observable<string[]>;
  id: any;
  loggedInUser : any;
  auditLogData : auditLogInterface;
  isEdit: boolean = false;
  isCountrycode: boolean = false;
  isCountrycode2: boolean = false;
  isReadOnly: boolean = true;
  show = false;
  editNow : boolean = false;
    showLoader : boolean = false;
    readorWrite: boolean=true;
  selectedCountryCode : string;
  @ViewChild('searchVal') searchVal: ElementRef;
  selectedFilterIndex : number = 0;

  dataSource = new MatTableDataSource();
  displayedColumns: string[] = ['stateCode', 'stateName', 'stateCode2', 'stateNumCode','recordStatus', 'action'];

  filterByOption: { key: string, value: string }[] = [
    { key: 'stateCode', value: 'State Code' },
    { key: 'stateName', value: 'State Name' },
    { key: 'stateNumCode', value: 'State Numric Code' },
    { key: 'recordStatus', value: 'Record Status' },
  ];

  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild('auditLog') auditLog : AuditLogComponent
  countryIsEdit: string;
  userdata: any;
  downloadService: any;

  constructor(

    private fb: FormBuilder,
    private apiService: ApiService,
    private _location: Location,
    private router:Router,
    private route : ActivatedRoute,
    private _liveAnnouncer: LiveAnnouncer,
    private snack: MatSnackBar,
    private dataService : DataService,
    private ls : LocalStoreService,
    private loader: AppLoaderService,
      private confirmService: AppConfirmService,
      
  ) { }

  ngOnInit() {
    //  this.getAllState();
     this.allCountryCode();
    this.route.queryParamMap.subscribe((params: any) => {
      this.countryIsEdit = params.get('isEdit');
      if(params.get('isEdit') == 'Yes'){
        this.isReadOnly = false;
        this.editNow = true;
      }
      else if(params.get('isEdit') == 'No'){
        this.isReadOnly = true;
        this.editNow = false;
      }
      else if(params.get('isEdit') == 'false'){
        this.isReadOnly = false;
        this.editNow = false;
      }
      if(params.get('id')){
        this.isEdit = true;
        this.id = params.get('id');
        this.apiService.getCountryById(params.get('id')).subscribe((resp:any) =>{
           this.showLoader = true;
           this.buildItemForm(resp);
           this.showLoader = false;
           this.selectedCountryCode = resp.countryCode;
           this.auditLogData = resp;
           this.getAllState();
           this.dataService.setAuditLogData(this.auditLogData);
        });
      }
      else{
        this.readorWrite=false;
        this.showLoader = true;
        this.buildItemForm({});
        this.showLoader = false;

      }
    }
    );
    this.loggedInUser = this.ls.getItem('ICUST_USER');
  }
  editItem(){
    this.isReadOnly = false;
    this.editNow = true;
  }

  Onlynumber(event): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }

  keyPressAlphanumeric(event) {
    var inp = String.fromCharCode(event.keyCode);
    if (/[0-9a-zA-Z]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }

  _keyPress(event: any) {
    const pattern = /[+,1-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  onFileChange(event) {
    console.log('event', event.path[0].files[0]);
    const file: File = event.path[0].files[0];
    const uploadData = new FormData();
    uploadData.append('screenType', 'countryMaint');
    uploadData.append('file', file);
    this.apiService.uploadExelFile(uploadData).subscribe((response) => {
      console.log(response);
      this.getAllCountries();
      this.snack.open(`File Uploaded Successfully`, "OK", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
  }

  buildItemForm(item) {
    this.countryMaintenance = this.fb.group({
      countryCode: [item.countryCode || '', Validators.required], //dropdown
      countryName: [item.countryName || '', Validators.required],
      countryIsoCode2: [item.countryIsoCode2 || '', Validators.required],  //dropdown
      numericCode: [item.numericCode || '', Validators.required],
      // ibanCheckReqd: [item.ibanCheckReqd || false, Validators.required],
      // blacklisted: [item.blacklisted || false, Validators.required],
      ibanCheckReqd: [item.ibanCheckReqd === "Y" ? true : false || "", false],
      blacklisted: [item. blacklisted === "Y" ? true : false || "", false],
      region: [item.region || '', Validators.required],
      isdCode: [item.isdCode || '', Validators.required]
    });
  }


  submit(data : any) {
    let details: any = {};
    details = data;
    data.ibanCheckReqd? details.ibanCheckReqd = 'Y' : details.ibanCheckReqd = 'N';
    data.blacklisted? details.blacklisted = 'Y' : details.blacklisted = 'N';
    details.id = this.id;
    data.loggedInUser=this.loggedInUser.userName;
    this.apiService.upsertCountryDetails(details).subscribe((resp) =>{
      this.loader.close();
      this.router.navigate(['/maintenance/main/countryState']).then(_=>{
        this.snack.open('Data Saved Successfully!', 'OK', 
        { duration: 4000 , 
          verticalPosition: "top",
          horizontalPosition: "right",});
      });
    },(err:any) =>{
      this.loader.close();
      this.snack.open(`Error!`, 'OK', { duration: 4000 });
    })
  }
  isCountryPresent(data){
    this.apiService.isCountryCodePresent(data).subscribe((res) =>{
      if(res == true){
        this.isCountrycode = true;
      }else{
        this.isCountrycode = false;
      }
    });
  }
  isCountryCode2Present(data){
    this.apiService.isCountryCode2Present(data).subscribe((respo) =>{
      if(respo == true){
        this.isCountrycode2 = true;
      }else{
        this.isCountrycode2 = false;
      }
    });
  }
  

  goBack() {
     this.router.navigate([`/maintenance/main/countryState`]);
     
  }
  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }


  getAllState() {
    this.apiService.getStateByCountry(this.id).subscribe((data: any) => {
      for(let i=0;i<data.length;i++){
        if(data[i].authStatus == 'U'){
          data[i].authStatus = 'Unauthorized'
        }
        if(data[i].authStatus == 'A') {
          data[i].authStatus = 'Authorized'
        } 
        if(data[i].recordStatus == 'O'){
          data[i].recordStatus = 'Open'
        } 
        if(data[i].recordStatus == 'C'){
          data[i].recordStatus = 'Closed'
        }
      }
      this.dataSource = new MatTableDataSource(data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    });
  }

  

  openPopUp(data: any = {}) {

    this.router.navigate(["/maintenance/main/statepopup"],{ queryParams: { id: data.id, countryId : this.id, countryIsEdit : this.countryIsEdit,  SelectedCountryCode :this.selectedCountryCode } });
  }

  doFilter(filterBy, e){
    this.filterTable(filterBy);
    this.applyFilter(e);
  }

  filterTable(filterBy) {
    this.dataSource.filterPredicate = (data: any, filter: string): boolean => {
      return (
        data[filterBy].toLocaleLowerCase().includes(filter)
      )
    }
  }

  clearFilter(){
    this.dataSource.filter = '';
    this.searchVal.nativeElement.value = '';
    this.selectedFilterIndex = 0;
   }
   getAllCountries() {
    this.apiService.getAllCountries().subscribe((data: any) => {
      this.dataSource = new MatTableDataSource(data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    });
  }

  onSelectOfCountryCode(obj){
    this.countryMaintenance.get('countryCode').setValue(obj.iso2);
    if(obj.iso2){
      this.isCountryPresent(this.countryMaintenance.get('countryCode').value);
    }
    this.countryMaintenance.get('countryName').setValue(obj.name);
    this.countryMaintenance.get('countryIsoCode2').setValue(obj.iso3);
    if(obj.iso3){
      this.isCountryCode2Present(this.countryMaintenance.get('countryIsoCode2').value);
    }
    this.countryMaintenance.get('numericCode').setValue(obj.numericCode);
    this.countryMaintenance.get('ibanCheckReqd').setValue(true);
    this.countryMaintenance.get('blacklisted').setValue(true);
    this.countryMaintenance.get('region').setValue(obj.region);
    this.countryMaintenance.get('isdCode').setValue(obj.phonecode);

  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteCountry(this.auditLogData.id)
        .subscribe(data => {
          this.router.navigate([`/maintenance/main/countryState`]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
          this.router.navigate([`/maintenance/main/countryState`]);
        
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
    payload.id = this.id;
    payload.operation = operation;
    payload.loggedInUser = this.loggedInUser.userName;
    this.apiService.getAuditLogsForCountry(payload).subscribe((data) => {
      this.auditLogData = data;
      this.dataService.setAuditLogData(this.auditLogData);
      this.dataService.sendClickEvent();
      let message = '';
      switch (operation) {
        case 'open':
          message = 'opened'
          break;
        case 'close':
          message = 'closed'
          break;
        case 'auth':
          message = 'authorized'
          break;
        default:
          break;
      }
      this.snack.open(`Record ${message} successful!'`, "OK", {
        duration: 2000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
      }}))
    
  }

  allCountryCode(){
    this.apiService.allCountryCode().subscribe((data) =>{
      this.countryCodeList = data;
    })
  }

  filter(value: string): string[] {

    const filterValue = value.toLowerCase();
    return this.countryCodeList.filter(option => option.iso2.toLowerCase().includes(filterValue));
  }

  // deleteCountry() {
  //   this.confirmService.confirm({message: `Are you sure? You want to delete?`})
  //     .subscribe(res => {
  //       if (res) {
  //         this.loader.open();
  //         this.apiService.deleteCountry(this.id)
  //           .subscribe(data => {
              
  //           },    err=>{
  //             if (err.status == 200) {
  //               this.loader.close();
  //               this.snack.open('Record Deleted Successfully', 'OK', {
  //                 duration: 4000,
  //                 verticalPosition: 'top',
  //                 horizontalPosition: 'right'
  //               });
  //             }
    
            
  //           this.getAllCountries();
  //         this.router.navigate([`/maintenance/main/countryState`]);

            
  //         })
  //       }
  //     })
  // }

  countryStateMaintenance(){
    this.downloadService.downloadFiles("countryStateMaintenance", this.userdata);
  }
   
  deleteItem(row) {
    this.confirmService.confirm({message: `Delete ${row.stateName}?`})
      .subscribe(res => {
        if (res) {
          this.loader.open();
          this.apiService.deleteState(row.id)
            .subscribe(data => {
              
            },    err=>{
              if (err.status == 200) {
                this.loader.close();
                this.snack.open('Record Deleted Successfully', 'OK', {
                  duration: 4000,
                  verticalPosition: 'top',
                  horizontalPosition: 'right'
                });
              }
            this.getAllState();
            
          })
        }
      })
  }

   

   applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  announceSortChange(sortState: Sort) {
    if (sortState.direction) {
      this._liveAnnouncer.announce(`Sorted ${sortState.direction}ending`);
    } else {
      this._liveAnnouncer.announce('Sorting cleared');
    }
  }

  addNew(){
    this.router.navigate(["/maintenance/main/statepopup"],{ queryParams: {countryId : this.id, countryIsEdit : this.countryIsEdit, selectedCountryCode :this.selectedCountryCode } });

  }
  dataByIso(index,item){
    return item.iso2
  }
  dataByKey(index,item){
    return item.key
  }
}
