import { Component, OnInit, OnDestroy } from '@angular/core';
import { MatDialogRef, MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Subscription } from 'rxjs';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { TransactionTypePopupTableComponent } from '../transaction-type-table-popup/transaction-type-popup-table/transaction-type-popup-table.component';
import { ApiService } from 'app/shared/services/api.service';

@Component({
  selector: 'app-transaction-type-crud-table',
  templateUrl: './transaction-type-crud-table.component.html',
  styleUrls: ['./transaction-type-crud-table.component.scss']
})
export class TransactionTypeCrudTableComponent implements OnInit, OnDestroy {
  public items: any[];
  public getItemSub: Subscription;

  constructor(
    private dialog: MatDialog,
    private snack: MatSnackBar,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,
    private api : ApiService
  ) { }

  ngOnInit(): void {
    this.getItems()
  }

  ngOnDestroy() {
    if (this.getItemSub) {
      this.getItemSub.unsubscribe()
    }
  }
  getItems() {
   // this.items = transactionType;
    this.getItemSub = this.api.fetchTransactionTypeInfo()
      .subscribe(data => {
        this.items = data;
      })
  }

  openPopUp(data: any = {}, isNew?) {
    console.log("data::",data)
    let title = isNew ? 'Add new Transaction Type' : 'Update Transaction Type';
    let dialogRef: MatDialogRef<any> = this.dialog.open(TransactionTypePopupTableComponent, {
      width: '720px',
      disableClose: true,
      data: { title: title, payload: data }
    })
    dialogRef.afterClosed()
      .subscribe(res => {
        if(!res) {
          // If user press cancel
          return;
        }
        this.loader.open();
        if (isNew) {
           this.api.upsertTransactionType(res).subscribe(response => {
            this.loader.close();
            this.getItems();
            this.snack.open(`Record Added!'`, "OK", {
              duration: 2000,
              verticalPosition: "top",
              horizontalPosition: "right",
            });
          });
        } else {
          this.api.upsertTransactionType(res).subscribe(response => {
            this.loader.close();
            this.getItems();
            this.snack.open(`Record Updated!'`, "OK", {
              duration: 2000,
              verticalPosition: "top",
              horizontalPosition: "right",
            });
          });
        }
      })
  }

  deleteItem(row) {
    this.confirmService.confirm({message: `Delete ${row.transactionTypeCode}?`})
      .subscribe(res => {
        if (res) {
          this.loader.open();
          this.api.deleteTransactionType(row.transactionTypeId)
            .subscribe(data => {
              this.getItems();
              this.loader.close();
              this.snack.open('Record deleted!', 'OK', { duration: 4000 })
            })
        }
      })
  }
}
