import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router, ActivatedRoute } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { TransactionTypeCrudTableComponent } from '../../transaction-type/transaction-type-crud-table/transaction-type-crud-table.component';

@Component({
  selector: 'app-menu-dialog',
  templateUrl: './menu-dialog.component.html',
  styleUrls: ['./menu-dialog.component.scss']
})
export class MenuDialogComponent implements OnInit {
  menuForm: FormGroup;
  parentMenus: any = [];
  loggedInUser: any;
  isEdit:boolean=false;
  parentId: number;

  constructor(private cdr: ChangeDetectorRef,
    private apiService: ApiService,
    private fb: FormBuilder,
    private dialogue: MatDialog,
    private dataService: DataService,
    private ls: LocalStoreService,
    private loader: AppLoaderService,
    private snack: MatSnackBar,
    private router: Router,
    private route: ActivatedRoute,) { }

  ngOnInit(): void {
    this.loggedInUser = this.ls.getItem('ICUST_USER');
    this.dataService.getchildMenuDetails().subscribe((data: any)=>{
      console.log(data);
      if(data){
        this.isEdit=true;
        this.buildMenuForm(data);
      }else{
        this.isEdit=false;
        this.buildMenuForm({});
      }     
    })
    this.route.queryParamMap.subscribe((params: any) => {
      this.parentId=params.get('parenId');
      console.log("param",params)
     
    })
    // this.buildMenuForm(this.data);
   
    this.getParentMenus();
  }

  buildMenuForm(item: any) {
    this.menuForm = this.fb.group({
      id: [item.id || null],
      screenCode: [item.screenCode || "", Validators.required],
      screenName: [item.screenName || "", Validators.required],
      parentId: [item.parentId || 0, Validators.required],
      routing: [item.routing || "", Validators.required],
      status: true,
    });
  }

  getParentMenus() {
    this.apiService.getMaintenanceParentMenu().subscribe((res: any) => {
      this.parentMenus = res;
    }, (err: any) => {

    })
  }
  save() {
    if (this.menuForm.invalid) {
      return;
    }
    let payload: any = {};
    payload = this.menuForm.value;
    payload.loggedInUser = this.loggedInUser.userName;
    this.loader.open();
    this.apiService.upsertMenu(this.menuForm.value).subscribe(res => {
      this.loader.close();
      this.snack.open('Data Saved Successfully!', 'OK',
        {
          duration: 4000,
          verticalPosition: "top",
          horizontalPosition: "right",
        });

    }, err => {
      this.loader.close();
      this.snack.open('Unable to Save!', 'Error',
        {
          duration: 4000,
          verticalPosition: "top",
          horizontalPosition: "right",
        });
     
    })
  }

  close() {
   
  }
  goBack(){
    this.router.navigate([`/maintenance/main/menu-config/:id`],{ queryParams: { id: this.parentId } });
  }

}
