import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router, ActivatedRoute } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-add-or-edit-biller-info',
  templateUrl: './add-or-edit-biller-info.component.html',
  styleUrls: ['./add-or-edit-biller-info.component.scss']
})
export class AddOrEditBillerInfoComponent implements OnInit {
  public billerMaintenance: FormGroup;
  id: number;
  isEdit: boolean = false;
  branchCode: string[] = [];
  bankCode: string[] = [];
  entityCode: string[] = [];
  readorWrite: boolean=true;
  auditLogData: any;
  loggedInUser: any;
  constructor(private fb: FormBuilder,private apiService: ApiService,private router:Router, private  ls : LocalStoreService,
    private snack: MatSnackBar,private cdr: ChangeDetectorRef, private dataService : DataService, private route : ActivatedRoute) { }

  ngOnInit(): void {
    this.route.queryParamMap.subscribe((params: any) => {
      if(params.get('id')){
        this.isEdit = true;
        this.id = params.get('id');
        this.apiService.getBillerInfoById(params.get('id')).subscribe((resp) =>{
          this.auditLogData = resp;
          this.dataService.setAuditLogData(this.auditLogData);
           this.dataService.sendClickEvent();
           this.buildItemForm(resp);
        });
      }
      else{
        this.readorWrite=false;
        this.buildItemForm({});
      }
    }
    );  
    this.loggedInUser = this.ls.getItem('ICUST_USER').userName;
    this.buildItemForm({});
    this.getBankCode();
    this.getBranchCode();
    this.getEnityCode();
  }

  buildItemForm(item) {
    this.billerMaintenance = this.fb.group({
      id: [item.id || ''],
      entityCode: [item.entityCode || '', Validators.required],
      bankCode: [item.bankCode || '', Validators.required],
      billerAccBrn: [item.billerAccBrn || ''],
      billerAccount: [item.billerAccount || ''],
      billerCode: [item.billerCode || ''],
      billerName: [item.billerName || ''],
      loggedInUser: [item.loggedInUser || ''],
      lastModifiedDate:[item.lastModifiedDate || ''],
      authStatus: [item.authStatus || ''],
      authBy: [item.authBy || ''],
      authDate: [item.authDate || ''],
      createdBy: [item.createdBy || ''],
      createdDate: [item.createdDate || ''],
      modifiedBy: [item.modifiedBy || ''],
      noOfModification: [item.noOfModification || ''],
      oneTimeAuth: [item.oneTimeAuth || ''],
      recordStatus: [item.recordStatus || ''],
      version: [item.version || ''],
    });
    this.cdr.markForCheck();
    this.cdr.detectChanges();
  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteBillerInfo(this.auditLogData.id)
        .subscribe(data => {
          this.router.navigate([`/maintenance/main/billerInfoMaint`]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
          this.router.navigate([`/maintenance/main/billerInfoMaint`]);
        
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
        payload.id = this.id;
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser;
        this.apiService.updateBiller(payload).subscribe((data) => {
          this.auditLogData = data;
          this.dataService.setAuditLogData(this.auditLogData);
          this.dataService.sendClickEvent();
          let message = '';
          switch (operation) {
            case 'open':
              message = 'opened'
              break;
            case 'close':
              message = 'closed'
              break;
            case 'auth':
              message = 'authorized'
              break;
            default:
              break;
          }
          this.snack.open(`Record ${message} successful!'`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        });
      }
    }))
    
  }

  save(form){
    this.billerMaintenance.value.loggedInUser = this.loggedInUser;
    this.apiService.saveOrUpdateBillerInfo(this.billerMaintenance.value).subscribe(data => {
      if(data != null && this.isEdit == true){
        this.snack.open('Data Updated!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.goBack();
        this.cdr.markForCheck();
      }
      else if(data != null && this.isEdit == false){
        this.snack.open('Data Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.goBack();
        this.cdr.markForCheck();

      }
    })
  }


  getBranchCode(){
    this.apiService.getBranchCode().subscribe(branchCodeResp=>{
      this.branchCode=branchCodeResp;
      console.log(this.branchCode);
    });
  }

  getBankCode(){
    this.apiService.getBankCode().subscribe(bankCodeResp=>{
     // this.bankCode=bankCodeResp;
      //console.log(this.bankCode);
      for(let i=0;i<bankCodeResp.length;i++){
        if(bankCodeResp[i].authStatus==='A'){
          console.log('auth',bankCodeResp[i].authStatus)
          this.bankCode.push(bankCodeResp[i]);
        }
      }
    });
  }

  getEnityCode(){
    this.apiService.fetchEntityDetails().subscribe(entityCodeResp=>{
    // this.entityCode=entityCodeResp;
      console.log(this.entityCode);
      for(let i=0;i<entityCodeResp.length;i++){
        if(entityCodeResp[i].authStatus==='A'){
          console.log('auth',entityCodeResp[i].authStatus)
          this.entityCode.push(entityCodeResp[i]);
        }
      }
    });
  }

  goBack() {
    this.router.navigate([`/maintenance/main/billerInfoMaint`]);
  }
  dataByEntityCode(index,item){
    return item.entityCode;
  }
  dataByBankCode(index,item){
    return item.bankCode;
  }
  dataByBranchCode(index,item){
    return item.branchCode;
  }

  dataBybranchCode(index, branch){
    return branch.branchCode; 
 }


}
