import { Component, OnInit, OnDestroy } from '@angular/core';
import { MatDialogRef, MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Subscription } from 'rxjs';
import { rumangoAnimations } from 'app/shared/animations/rumango-animations';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { Location } from '@angular/common';
import { Data, Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-clearing-holiday',
  templateUrl: './clearing-holiday.component.html',
  styleUrls: ['./clearing-holiday.component.scss']
})
export class ClearingHolidayComponent implements OnInit {
  items: string[];
  loading: boolean;
  

  constructor(
    private dialog: MatDialog,
    private snack: MatSnackBar,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,
    private apiService:ApiService,
    private ls:LocalStoreService,
    private _location: Location,
    private router:Router,
    private http: HttpClient) { }

  ngOnInit(): void {
    this.apiService.getBranchHolidayInfo()
      .subscribe(data => {
        this.items = data;
        this.getClearingHoliday();
    this.loading = false;
      })
  }


  deleteItem(row) {
    this.confirmService.confirm({message: `Delete ${row.entityCode}?`})
      .subscribe(res => {
        if (res) {
          this.loader.open();
          this.apiService.deleteClearingHoliday(row.id)
            .subscribe(data => {
              this.getClearingHoliday() ;
              this.loader.close();
              this.snack.open('ClearingHoliday deleted!', 'OK', { duration: 4000 })
            })
        }
      })
  }
  getClearingHoliday() {
    this.apiService.getClearingHolidayInfo()
      .subscribe(data => {
        this.items = data;
      })
  }

}
