import { Component, OnInit } from "@angular/core";
import { MatDialog, MatDialogRef } from "@angular/material/dialog";
import { MatSnackBar } from "@angular/material/snack-bar";
import { rumangoAnimations } from "app/shared/animations/rumango-animations";
import { ApiService } from "app/shared/services/api.service";
import { AppConfirmService } from "app/shared/services/app-confirm/app-confirm.service";
import { AppLoaderService } from "app/shared/services/app-loader/app-loader.service";
import { Subscription } from "rxjs";
import { TransactionDenominationHistoryComponent } from "../transaction-denomination-history/transaction-denomination-history.component";

@Component({
  selector: 'app-transaction-denomination-summary',
  templateUrl: './transaction-denomination-summary.component.html',
  styleUrls: ['./transaction-denomination-summary.component.scss'],
  animations:rumangoAnimations
})
export class TransactionDenominationSummaryComponent implements OnInit {

  public items: any[];
  public getItemSub: Subscription;
  users: any[] = [];
  currency: string[] = [];
  till: string[] = [];
  branchCode: string[] = [];
  bankCode: string[] = [];
  constructor(
    private dialog: MatDialog,
    private snack: MatSnackBar,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,
    private apiService: ApiService
  ) {}

  ngOnInit() {
    this.getItems();
    this.getTransactionDenomination();
    this.getCurrency();
    this.getBankCode();
    this.getBranchCode();
    this.getTillId();
  }

  ngOnDestroy() {
    if (this.getItemSub) {
      this.getItemSub.unsubscribe();
    }
  }
  getItems() {
    this.items = this.users;
  }

  openPopUp(data: any = {}, isNew?) {
    let title = isNew
      ? "Add New Transaction Denomination"
      : "Update Transaction Denomination";
    let dialogRef: MatDialogRef<any> = this.dialog.open(
      TransactionDenominationHistoryComponent,
      {
        width: "720px",
        disableClose: true,
        data: {
          title: title,
          payload: data,
          currency: this.currency,
          till: this.till,
          branchCode: this.branchCode,
          bankCode: this.bankCode,
        },
      }
    );
    dialogRef.afterClosed().subscribe((res) => {
      if (!res) {
        return;
      }
      this.loader.open();
      if (isNew) {
        this.apiService.saveoftransDenom(res).subscribe((resp) => {
          this.loader.close();
          this.getTransactionDenomination();
          this.snack.open(`Record Added!'`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        });
      } else {
        this.apiService.saveoftransDenom(res).subscribe((resp) => {
          this.getTransactionDenomination();
          this.loader.close();
          this.snack.open(`Record Updated!'`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        });
      }
    });
  }
  deleteItem(row) {
    this.confirmService
      .confirm({ message: `Delete ${row.txtReference}?` })
      .subscribe((res) => {
        if (res) {
          this.loader.open();
          this.apiService.deleteTransactionDenomination(row.id).subscribe(
            (data) => {},
            (err) => {
              if (err.status === 200) {
                this.getTransactionDenomination();
                this.loader.close();
                this.snack.open(`Record Deleted!'`, "OK", {
                  duration: 2000,
                  verticalPosition: "top",
                  horizontalPosition: "right",
                });
              }
            }
          );
        }
      });
  }
  getTransactionDenomination() {
    this.apiService.getTransactionDenomination().subscribe((resp) => {
      this.users = resp;
      this.items = this.users;
    });
  }
  getCurrency() {
    this.apiService.getCountryValues().subscribe((resp) => {
      this.currency = resp;
    });
  }
  getBankCode() {
    this.apiService.getBankCode().subscribe((resp) => {
      this.bankCode = resp;
    });
  }
  getBranchCode() {
    this.apiService.getBranchCode().subscribe((resp) => {
      this.branchCode = resp;
    });
  }
  getTillId() {
    this.apiService.getTillId().subscribe((resp) => {
      this.till = resp;
    });
  }
}
