import { Component, OnInit,OnDestroy, ViewChild, ChangeDetectorRef } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { Location } from '@angular/common';


export interface Card {
  title: string;
  subtitle: string;
  text: string;
  state?:string;
  url:any;
}

const DATA: Card[] = [
  
  {
    title: 'Branch Holiday',
    subtitle: 'Product Details',
    text: 'this covers ordination product details, Product prefrences, Interest Charges and additional attributes.',
    state:"BranchHoliday",
    url:"assets/images/brnchholiday.png"
  },{
    title: 'Currency Holiday',
    subtitle: 'Product Details',
    text: 'This covers create business process and create process stages.',
    state:"currencyholiday",
    url:"assets/images/processing.png"
  },
  {
    title: 'Clearing Holiday',
    subtitle: 'Product Details',
    text: 'This covers Quantitative and Qualitative score card.',
    state:"clearingholiday",
    url:'assets/images/screen.png'
  },

];



@Component({
  selector: 'app-holiday-maintenance',
  templateUrl: './holiday-maintenance.component.html',
  styleUrls: ['./holiday-maintenance.component.scss']
})
export class HolidayMaintenanceComponent implements OnInit {


  @ViewChild(MatPaginator) paginator: MatPaginator;
  obs: Observable<any>;
  dataSource: MatTableDataSource<Card> = new MatTableDataSource<Card>(DATA);

  constructor(private changeDetectorRef: ChangeDetectorRef, private router:Router, private _location: Location,) {
  }

  ngOnInit() {
    document.getElementById('main-content-wrap').style.backgroundColor = 'white' ; 
    this.changeDetectorRef.detectChanges();
    this.dataSource.paginator = this.paginator;
    this.obs = this.dataSource.connect();
  }


  navigateToScreen(state:string){
    this.router.navigate([`/maintenance/main/holiday/${state}`]);
  }
  goBack(){
    this.router.navigate(["/maintenance/main"]);
  }

}