import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { DataService } from 'app/shared/services/data.service';
import * as moment from 'moment';
import { loggedInUserInterface, TrxnCashStatusInterface } from './txn-cash-master-table-popup';
import Swal from 'sweetalert2';

@Component({
  selector: "app-txn-cash-master-table-popup",
  templateUrl: "./txn-cash-master-table-popup.component.html",
  styleUrls: ["./txn-cash-master-table-popup.component.scss"],
})
export class TxnCashMasterTablePopupComponent implements OnInit {
  public txnCashMasterForm: FormGroup;
  countryCurrency: string[] = [];
  tillId: string[] = [];
  branchCode: string[] = [];
  bankCode: string[] = [];
  entity: string;
  currency: string[] = [];
  till: string[] = [];
  txnproductArr =[ 'CHD', 'CHW','CQD' ,'CQW' ];
  currentUser: string;
  data: string;
  entityCode: string[] = [];
  loggedInUser : loggedInUserInterface;
   auditData : TrxnCashStatusInterface;
  isAuditData : boolean = false;
  id: string;
  isEdit: boolean = false;
  readorWrite: boolean=true;
  TrxnCashStatus: TrxnCashStatusInterface;
  constructor(
    private fb: FormBuilder,
    private api: ApiService,
    private loader: AppLoaderService,
    private snack: MatSnackBar,
    private router: Router,
    private apiService: ApiService,
    private ls: LocalStoreService,
    private dataService: DataService
  ) {}

  ngOnInit(): void {
    this.currentUser = this.ls.getItem("ICUST_USER");
 
    setTimeout(() => {
      this.TrxnCashStatus = JSON.parse(
        localStorage.getItem("fetchTrxnCashStatus")
      );
 
      if (this.TrxnCashStatus == null || this.TrxnCashStatus == undefined) {
        this.readorWrite=false;
        this.buildItemForm({});
      } else if (
        this.TrxnCashStatus.id != null ||
        this.TrxnCashStatus.id != undefined
      ) {
        this.loader.open();
        this.id = this.TrxnCashStatus.id;
        this.buildItemForm(this.TrxnCashStatus);
        
        this.auditData =this.TrxnCashStatus;
      this.isEdit = true;
      this.isAuditData = true;
      this.dataService.setAuditLogData(this.auditData);
        this.loader.close();
      }
    }, 1000);

    this.getCurrency();
    this.getBankCode();
    this.getBranchCode();
    this.getTillId();
    this.getEntityCode();

    this.loggedInUser = this.ls.getItem('ICUST_USER');
  }

  buildItemForm(item) {
    console.log('item',item)
    this.txnCashMasterForm = this.fb.group({
      entityCode: [item?.entityCode || "", Validators.required],
      bankCode: [item?.bankCode || "", Validators.required],
      branchCode: [item?.branchCode || "", Validators.required],
      tillId: [item.tillId || "", Validators.required],
      txnProduct: [item?.txnProduct || "", Validators.required],
      txnReference: [item?.txnReference || "", Validators.required],
      account: [item?.account || "", Validators.required],
      accCurrency: [item?.accCurrency || "", Validators.required],
      accBalance: [item?.accBalance || "", Validators.required],
      instrument: [item?.instrument || "", Validators.required],
      txnCurrency: [item?.txnCurrency || "", Validators.required],
      txnAmount: [item?.txnAmount || "", Validators.required],
      ofsCurrency: [item?.ofsCurrency || "", Validators.required],
      ofsAmount: [item?.ofsAmount || "", Validators.required],
      txnDate : [new Date(item.txnDate) || "", [Validators.required]],
      forexRate: [item?.forexRate || "", Validators.required],
      forexNegoRate: [item?.forexNegoRate || "", Validators.required],
      chargeAmount: [item?.chargeAmount || "", Validators.required],
      remarks: [item?.remarks || "", Validators.required],
      isActive: [item?.isActive || false],
      id: [item?.id || null],
      loggedInuser : [item.loggedInUser || ''],
    });
  }
  dataByEntityCode(index,item){
    return item.entityCode;
  }
  dataByBankCode(index,item){
    return item.bankCode;
  }
  dataByBranchCode(index,item){
    return item.branchCode;
  }
  dataByTillVaultCode(index,item){
  //  
    return item.tillVaultCode
  }
  dataByCurrencyCode(index,item){
    return item.currencyCode
  }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteTxnCashMaster(this.auditData.id)
        .subscribe(data => {
          this.router.navigate([`/maintenance/main/transactionCashMaster`]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
          this.router.navigate([`/maintenance/main/transactionCashMaster`]);
        
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }
  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
    payload.id = this.id;
    payload.operation = operation;
    payload.loggedInUser = this.loggedInUser.userName;
    this.apiService.getAuditLogsForTransactionCashMaster(payload).subscribe((data) => {
      this.auditData = data;
      this.dataService.setAuditLogData(this.auditData);
      this.isAuditData = true;
      this.dataService.sendClickEvent();
      let message = '';
      switch (operation) {
        case 'open':
          message = 'opened'
          break;
        case 'close':
          message = 'closed'
          break;
        case 'auth':
          message = 'authorized'
          break;
        default:
          break;
      }
      this.snack.open(`Record ${message} successfully!'`, "OK", {
        duration: 2000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
      }}))
    
  }

  submit() {
    let date = this.convertDate(this.txnCashMasterForm.controls.txnDate.value);
    this.txnCashMasterForm.value["txnDate"] = date;
    if (this.txnCashMasterForm.invalid) {
      return;
    } else {
      this.loader.open();
      this.txnCashMasterForm.value.loggedInUser = this.loggedInUser.userName;
      this.api.upsertTxnCashMaster(this.txnCashMasterForm.value).subscribe(
        (resp) => {
          this.loader.close();
          this.router
            .navigate([`/maintenance/main/transactionCashMaster`])
            .then((_) => {
              this.snack.open("Data Saved Successfully!", "OK", {
                duration: 4000,
                verticalPosition: "top",
                horizontalPosition: "right",
              });
              localStorage.removeItem("fetchTrxnCashStatus");
            });
        },
        (err: any) => {
          this.loader.close();
          this.snack.open(`Error!`, "OK", {
            duration: 4000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        }
      );
    }
  }

  getCurrency() {
    // this.api.getCountryValues().subscribe((resp) => {
    //   this.currency = resp;
    // });
    this.apiService.getCurrencyDetails().subscribe(data=>{
      console.log("Auth currecy details", data)
      for(let i=0; i<data.length;i++){
        this.currency.push(data[i].currencyId)
      }
      console.log(this.currency);
    })
  }
  getBankCode() {
    this.api.getBankCode().subscribe((resp) => {
    //  this.bankCode = resp;
      for(let i = 0; i<resp.length; i++){
        if(resp[i].authStatus == 'A'){
          this.bankCode.push(resp[i]);
        }
      }
    });
  }
  getBranchCode() {
    this.api.getBranchCode().subscribe((resp) => {
      //this.branchCode = resp;
      for(let i = 0; i<resp.length; i++){
        if(resp[i].authStatus == 'A'){
          this.branchCode.push(resp[i]);
        }
      }
    });
  }
  getTillId() {
    let tillOrVault = "T";
    this.api.getTillData(tillOrVault).subscribe((resp) => {
      this.till = resp;
    });
  }

  convertDate(value) {
    let date = moment(value).format("YYYY-MMM-DD HH:mm:ss");
    if (date) return date;
  }

  getEntityCode() {
    this.api.getEntityCode().subscribe((resp) => {
   //   this.entityCode = resp;
      for(let i = 0; i<resp.length; i++){
        if(resp[i].authStatus == 'A'){
          this.entityCode.push(resp[i]);
        }
      }
    });
  }

  goBack() {
    localStorage.removeItem("fetchTrxnCashStatus");
    this.router.navigate([`/maintenance/main/transactionCashMaster`]);
  }
}
