import { Component, Inject, OnInit } from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { tillVaultConfig } from 'app/shared/models/tillVaultConfig';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-till-vault-popup',
  templateUrl: './till-vault-popup.component.html',
  styleUrls: ['./till-vault-popup.component.scss']
})
export class TillVaultPopupComponent implements OnInit {
  branchCode: string[] = [];
  bankCode: string[] = [];
  public tillVaultForm: FormGroup;
  vaultConfig: tillVaultConfig;
  currency: string[] = [];
  till: any[] = [];
  entity: string[] = [];
  tillData: any;
  loggedInUser : string;
  auditData : tillVaultConfig;
  isAuditData : boolean = false;
  id: number;
  isEdit: boolean = false;
  minCashHoldingValue: number;
  constructor(
    // @Inject(MAT_DIALOG_DATA) public data: any,
    // public dialogRef: MatDialogRef<TillVaultPopupComponent>,
    private fb: FormBuilder,
    private apiService: ApiService,
    private loader: AppLoaderService,
    private snack: MatSnackBar,
    private router: Router,
    private ls: LocalStoreService,
    private dataService: DataService,

  ) { }

  ngOnInit() {
    // this.dataService.getTillVaultConfig.subscribe(data => {
    //   console.log(data)
    //   this.vaultConfig = typeof this.vaultConfig === 'object' ? data : {};
    // });

     setTimeout(() => {  
        this.vaultConfig = JSON.parse(localStorage.getItem('fetchVaultConfig'));
        console.log(" this.vaultConfig", this.vaultConfig)
        if(this.vaultConfig == null || this.vaultConfig == undefined){
          this.buildItemForm({})
        }       
        else if(this.vaultConfig.id != null || this.vaultConfig.id != undefined){
        this.loader.open();
        this.id = this.vaultConfig.id;
        this.buildItemForm(this.vaultConfig);
        this.auditData = this.vaultConfig;
        this.isEdit = true;
        this.isAuditData = true;
        this.dataService.setAuditLogData(this.auditData);
        this.loader.close();
        }
      }, 1000)

    this.getCurrency();
    this.getBankCode();
    this.getBranchCode();
    this.getTillId();
    this.getEntityCode();

    this.loggedInUser = this.ls.getItem('ICUST_USER').userName;
    // this.buildItemForm(this.data.payload);
    // this.countryCurrency=this.data.currency;
    // this.tillId=this.data.till;
    // this.branchCode=this.data.branchCode;
    // this.bankCode=this.data.bankCode;
  }
  getValue(val: any) {
    this.minCashHoldingValue = val;
  }

  buildItemForm(item) {
    this.tillVaultForm = this.fb.group({
      bankCode: [item.bankCode || '', [Validators.required]],
      branchCode: [item.branchCode || '', [Validators.required]],
      currency: [item.currency || '', [Validators.required]],
      entityCode: [item.entityCode || '', [Validators.required]],
      id: [item.id || null],
      // maxCashHolding: [item.maxCashHolding || '',[Validators.required, Validators.min(this.minCashHoldingValue)]],
      maxCashHolding: [item.maxCashHolding || '', [Validators.required, (control: AbstractControl) => Validators.min(this.minCashHoldingValue)(control)]],
      minCashHolding: [item.minCashHolding || '', Validators.required],
      tillId: [item.tillId || '', [Validators.required]],
    })
  }
  numericValidation(event) {
    var inp = String.fromCharCode(event.keyCode);
    if (/[0-9]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }
  alhanumericValidation(event) {
    var inp = String.fromCharCode(event.keyCode);
    if (/[a-zA-Z0-9]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }

  // Save of tillVaultConfig
  submit() {
    if (this.tillVaultForm.invalid) {
      return;
    } else {
        this.tillData = this.till.find(x => x.tillVaultCode == this.tillVaultForm.value.tillId);
        this.tillVaultForm.value.tillId = this.tillData.id;
        this.tillVaultForm.value.loggedInUser=this.loggedInUser;
        this.apiService.saveoftillVaultConfig(this.tillVaultForm.value).subscribe((resp) => {
        this.router.navigate([`/maintenance/main/tillVaultConfig`]).then(_ => {
          this.snack.open('Data Saved Successfully!', 'OK',
            {
              duration: 4000,
              verticalPosition: "top",
              horizontalPosition: "right",
            });
            localStorage.removeItem('fetchVaultConfig');
        });
      }, (err: any) => {
        this.snack.open(`Error!`, 'OK', {
          duration: 4000,
          verticalPosition: "top",
          horizontalPosition: "right",
        });
      }
      )
    }
  }

  updateRecord(operation) {
    let payload: any = {};
    payload.id = this.id;
    payload.operation = operation;
    payload.loggedInUser = this.loggedInUser;
    this.apiService.getAuditLogsFortillvaultconfig(payload).subscribe((data) => {
      console.log(data);
      this.auditData = data;
      this.dataService.setAuditLogData(this.auditData);
      this.isAuditData = true;
      this.dataService.sendClickEvent();
      let message = '';
      switch (operation) {
        case 'open':
          message = 'opened'
          break;
        case 'close':
          message = 'closed'
          break;
        case 'auth':
          message = 'authorized'
          break;
        default:
          break;
      }

      this.snack.open(`Record ${message} successfully!`, "OK", {
        duration: 2000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
  }

  goBack() {
    localStorage.removeItem('fetchVaultConfig');
    this.router.navigate(["/maintenance/main/tillVaultConfig"]);
  }

  getCurrency() {
    // this.apiService.getCountryValues().subscribe((resp) => {
    //   this.currency = resp;
    // });
    this.apiService.getCurrencyDetails().subscribe(data=>{
      console.log("Auth currecy details", data)
      for(let i=0; i<data.length;i++){
        this.currency.push(data[i].currencyId)
      }
      console.log(this.currency);
    })
  }
  getBankCode() {
    this.apiService.getBankCode().subscribe((resp) => {
      console.log(resp);
      for(let i=0; i<resp.length;i++){
        if(resp[i].authStatus==='A'){
          this.bankCode.push(resp[i]);
        }
      }
      console.log(this.bankCode);
      // this.bankCode = resp;
    });
  }
  getBranchCode() {
    this.apiService.getBranchCode().subscribe((resp) => {
      console.log(resp);
      for(let i=0; i<resp.length;i++){
        if(resp[i].authStatus==='A'){
          this.branchCode.push(resp[i]);
        }
      }
      console.log(this.branchCode);
      // this.branchCode = resp;
    });
  }
  getTillId() {
    let tillOrVault ="T";
    this.apiService.getTillData(tillOrVault).subscribe((resp) => {
      console.log("tillData",resp)
      this.till = resp;
   });
  }

  getEntityCode() {
    this.apiService.getEntityCode().subscribe((resp) => {
      console.log(resp);
      for(let i=0; i<resp.length;i++){
        if(resp[i].authStatus==='A'){
          this.entity.push(resp[i]);
        }
      }
      console.log(this.entity);
      // this.entity = resp;
    });
  }
  dataByBankCode(index,item){
    return item.bankCode;
  }
  dataByBranchCode(index,item){
    return item.branchCode;
  }
  dataByEntityCode(index,item){
    return item.entityCode;
  }
  dataByCurrencyCode(index,item){
    return item.currencyCode;
  }
  dataByTillVaultCode(index,item){
    return item.currencyCode;
  }
  

}
