import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { baseDenom } from 'app/shared/models/auditlogs';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-base-denomination-popup',
  templateUrl: './base-denomination-popup.component.html',
  styleUrls: ['./base-denomination-popup.component.scss']
})

export class BaseDenominationPopupComponent implements OnInit {
  arrayCurrency: string[] = [];
  public baseDenomForm: FormGroup;
  baseDenominationId: number;
  baseDenomStatus: any = {};
  id: number;
  loggedInUser : string;
  auditData : baseDenom;
  isAuditData : boolean = false;
  show = false;
  isEdit: boolean = false;
  currency: string[] = [];
  readorWrite: boolean=true;
  denom: string[] = [];
  
  constructor(
    // @Inject(MAT_DIALOG_DATA) public data: any,
    // public dialogRef: MatDialogRef<BaseDenominationPopupComponent>,
    private fb: FormBuilder,
    private apiService: ApiService,
    private loader: AppLoaderService,
    private snack: MatSnackBar,
    private router:Router,
    private ls:LocalStoreService,
    private dataService:DataService,
  ) { }

  ngOnInit() {
    // this.currentUser = this.ls.getItem("ICUST_USER");
    // this.dataService.getBaseDenom.subscribe(data =>{
    //   this.baseDenomStatus = typeof this.baseDenomStatus === 'object' ? data : {};
    // });
    
    setTimeout(() => {  
      this.baseDenomStatus = JSON.parse(localStorage.getItem('fetchBaseDenomValue'));
      if(this.baseDenomStatus == null || this.baseDenomStatus == undefined){
        this.readorWrite=false;
        this.buildItemForm({})
      }       
      else if(this.baseDenomStatus.baseDenominationId != null || this.baseDenomStatus.baseDenominationId != undefined) {
        this.loader.open();
        this.isEdit = true;
      this.id = this.baseDenomStatus.baseDenominationId;
      this.buildItemForm(this.baseDenomStatus)
      this.auditData = this.baseDenomStatus;
      this.isAuditData = true;
      this.dataService.setAuditLogData(this.auditData);
      this.loader.close();
      }
    }, 1000)
    this.getDenomCode();
    this.getCurrencyValue();
    this.loggedInUser = this.ls.getItem('ICUST_USER').userName;
  }
  getCurrencyValue(){
    // this.apiService.getCountryValues().subscribe((currencyValues : any)=> {
    //   this.currency=currencyValues;
    //   });
    this.apiService.getCurrencyDetails().subscribe(data=>{
      console.log("Auth currecy details", data)
      for(let i=0; i<data.length;i++){
        this.currency.push(data[i].currencyId)
      }
      console.log(this.currency);
    })
  }
  getDenomCode() {
    this.apiService.fetchDenomMasterAuthRecs().subscribe((resp: any) => {
      console.log(resp);
      for(let i=0; i<resp.length;i++){
        if(resp[i].authStatus==='A'){
          this.denom.push(resp[i].denomCode);
        }
      }
      console.log(this.denom);
    });
  }
  buildItemForm(item) {
    this.baseDenomForm = this.fb.group({
      denomination: [item?.denomination || '', [Validators.required, Validators.minLength(1), Validators.maxLength(5)]],
      currency: [item?.currency || '', [Validators.required]],
      isActive: [item?.isActive || false],
      baseDenominationId: [item?.baseDenominationId || null],
      loggedInuser : [item.loggedInUser || '']
    })
  }

  // alphakeyPress(event: any) {
  //   const pattern = /[0-9a-zA-Z]/;
  //   let inputChar = String.fromCharCode(event.charCode);
  //   if (!pattern.test(inputChar)) {
  //     event.preventDefault();
  //   }
  // }
  deleteRecord(operation){
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.removeBaseDenominationById(this.auditData.baseDenominationId)
        .subscribe(data => {
          this.router.navigate([`/maintenance/main/tillVaultStatus`]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
          this.router.navigate([`/maintenance/main/tillVaultStatus`]);
        
      })
      }}))
   
         
       
  }
  editRecord(){
    this.readorWrite=false;
  }

  updateRecord(operation) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        let payload: any = {};
        payload.id = this.id;
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser;
        this.apiService.getAuditLogsForBaseDenomination(payload).subscribe((data) => {
          this.auditData = data;
          this.dataService.setAuditLogData(this.auditData);
          this.isAuditData = true;
          this.dataService.sendClickEvent();
          let message = '';
          switch (operation) {
            case 'open':
              message = 'opened'
              break;
            case 'close':
              message = 'closed'
              break;
            case 'auth':
              message = 'authorized'
              break;
            default:
              break;
          }
          this.snack.open(`Record ${message} successfully!'`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        });
      }}))
   
  }

  submit() {
    if(this.baseDenomForm.invalid){
      return;
    }else{
      this.loader.open();
      this.baseDenomForm.value.loggedInUser = this.loggedInUser;
      this.apiService.saveBaseDenomination(this.baseDenomForm.value).subscribe((resp) => {
      this.loader.close();
      this.router.navigate([`/maintenance/main/baseDenomination`]).then(_=>{
          this.snack.open('Data Saved Successfully!', 'OK', 
          { duration: 4000 , 
            verticalPosition: "top",
            horizontalPosition: "right",});
        });
      },(err:any) =>{
        this.loader.close();
        this.snack.open(`Error!`, 'OK', { duration: 4000,  
          verticalPosition: "top",
          horizontalPosition: "right",});
        }
      )
    }
    
  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }

  goBack(){
    localStorage.removeItem('fetchBaseDenomValue');
    this.router.navigate([`/maintenance/main/baseDenomination`]);
  }
 

  dataByCurrency(index,data){
    return data.currencyCode;
  }

}
