import { ChangeDetectorRef, Component, Inject, OnInit, OnDestroy, Input, ViewChild, HostListener, ElementRef } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Subscription } from 'rxjs';
import { Data, Router } from '@angular/router';
import { FormArray, FormBuilder, FormGroup } from '@angular/forms';
import { Validators } from '@angular/forms';
import { BranchHolidayComponent } from '../branch-holiday.component';
import { ApiService } from 'app/shared/services/api.service';
import * as moment from 'moment';

@Component({
  selector: 'app-branch-holiday-popup',
  templateUrl: './branch-holiday-popup.component.html',
  styleUrls: ['./branch-holiday-popup.component.scss']
})
export class BranchHolidayPopupComponent implements OnInit {
  holidaypopupForm: FormGroup;
  holidaypopupResponse: any;
  branchCodeArr: string[] = ['SBIN123', 'SBIN231', 'SBIN212', 'SBIN235', 'SBIN879'];
  bankCodeArr: string[] = ['BRDS1NCH', 'SBI2NJ', 'BOB66SS', 'SBI44GH', 'SBI21SJ'];
  entityCodeArr: string[] = ['BRDS1NCH', 'SBI2NJ', 'BOB66SS', 'SBI44GH', 'SBI21SJ'];
  month: string;
  public getItemSub: Subscription;
  public items: string[] = [];
  branchCode:string[]=[];
  bankCode:string[]=[];
  entityCode:string[]=[];
  branchHolidayDetails:string[] = [];

  constructor(@Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<BranchHolidayComponent>,
    private formBuilder: FormBuilder, private apiService: ApiService,
    private router: Router,
  ) {

  }

  ngOnInit(): void {

    this.buildItemForm(this.data.payload);
    this.branchCode = this.data.branchCode;
    this.bankCode = this.data.bankCode;
    this.entityCode = this.data.entityCode;

    this.getBranchCode();

  }
  getBranchCode() {
    this.apiService.getBranchCode().subscribe((resp) => {
      this.branchCode = resp;
    });
  }
  buildItemForm(item) {
    this.holidaypopupForm = this.formBuilder.group({
      branchCode: [item.branchCode || '', [Validators.required]],
      transactionDate: [item.transactionDate || '', [Validators.required]],
      bankCode: [item.bankCode || '', [Validators.required]],
      year: [''],
      id: [item.id || ''],
      entityCode: [item.entityCode || '', [Validators.required]],

    })
  }

  trackByFnEntityCodeArr(index, item) {
    return index; 
  }

  trackByFnBankCodeArr(index, item) {
    return index; 
  }

  trackByFnBranchCode(index, item) {
    return index; 
  }


  goBack() {
    this.router.navigate([`/maintenance/main/holiday/BranchHoliday`]);
  }

  saveHoliday() {
    let now = new Date();
    now = this.holidaypopupForm.value.transactionDate;
    var dateStringWithTime = moment(now).format('YYYY-MM-DD HH:MM:SS');
    var convertedyear = dateStringWithTime.slice(0, 4);
    var convertMonth = dateStringWithTime.slice(5, 7);
    var conv1Month = parseInt(convertMonth);
    var convertCalender = dateStringWithTime.slice(8, 10);
    var conv1Calender = parseInt(convertCalender);

    let payload = [{
      entityCode: this.holidaypopupForm.value.entityCode,
      bankCode: this.holidaypopupForm.value.bankCode,
      branchCode: this.holidaypopupForm.value.branchCode,
      transactionDate: this.holidaypopupForm.value.transactionDate,
      year: convertedyear,
      id: this.holidaypopupForm.value.id,
      month: conv1Month,
      // calendar:calendar
      calendar: conv1Calender.toString()

    }
    ]
    this.dialogRef.close(payload);

  }
}
