import { LiveAnnouncer } from '@angular/cdk/a11y';
import { ChangeDetectorRef, Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort, Sort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppConfirmService } from 'app/shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { DownloadService } from 'app/shared/services/download.service';

@Component({
  selector: 'app-biller-info-maintenance',
  templateUrl: './biller-info-maintenance.component.html',
  styleUrls: ['./biller-info-maintenance.component.scss']
})
export class BillerInfoMaintenanceComponent implements OnInit {

  @ViewChild('searchVal') searchVal: ElementRef;
  selectedFilterIndex: number = 0;
  public users: any[];
  dataSource = new MatTableDataSource();
  displayedColumns: string[] = ['entityCode', 'billerCode', 'billerName', 'billerAccBrn', 'authStatus', 'recordStatus', 'action'];

  filterByOption: { key: string, value: string }[] = [
    { key: 'entityCode', value: 'Entity Code' },
    { key: 'billerCode', value: 'Biller Code' }
  ];
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  data: any = [];
  // { id: number; entityCode: string; bankCode: string; billerCode: string; billerName: string; billerAccBrn: string; billerAccount: string; createdDate: string; lastModifiedDate: string; oneTimeAuth: string; authStatus: string; recordStatus: string; version: number; authBy: string; authDate: string; modifiedBy: string; createdBy: string; noOfModification: number; }[];

  constructor(private confirmService: AppConfirmService,
     private snack: MatSnackBar,
    private apiService: ApiService, 
    private router: Router,
    private cdr: ChangeDetectorRef, 
    private _liveAnnouncer: LiveAnnouncer,
    private loader: AppLoaderService,
    private downloadService: DownloadService
    ) { }

  ngOnInit(): void {

    this.fetchBillerInfo();

  }

  onFileChange(event) {
    console.log('event', event.path[0].files[0]);
    const file: File = event.path[0].files[0];
    const uploadData = new FormData();
    uploadData.append('screenType', 'billerInfoMaint');
    uploadData.append('file', file);
    this.apiService.uploadExelFile(uploadData).subscribe((response) => {
      console.log(response);
      this.fetchBillerInfo();
      this.snack.open(`File Uploaded Successfully`, "OK", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
   
  }

  fetchBillerInfo() {
    this.apiService.getAllBillerInfoId().subscribe((resp) => {
      for(let i=0;i<resp.length;i++){
        if(resp[i].authStatus == 'U'){
          resp[i].authStatus = 'Unauthorized'
        }
        if(resp[i].authStatus == 'A') {
          resp[i].authStatus = 'Authorized'
        } 
        if(resp[i].recordStatus == 'O'){
          resp[i].recordStatus = 'Open'
        } 
        if(resp[i].recordStatus == 'C'){
          resp[i].recordStatus = 'Closed'
        }
      }
      this.data = resp;
      this.dataSource = new MatTableDataSource(this.data);
      console.log(this.dataSource);
      this.users = this.data;
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    });
  }
  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  addNew() {
    this.router.navigate(["/maintenance/main/addOrEditBillerInfo"]);
  }

  goBack() {
    this.router.navigate([`/maintenance/main`]);
  }
  doFilter(filterBy, e) {
    this.filterTable(filterBy);
    this.applyFilter(e);
  }
  filterTable(filterBy) {
    this.dataSource.filterPredicate = (data: any=[], filter: string): boolean => {
      return (
        data[filterBy]?.toLocaleLowerCase().includes(filter)
      )      
    }
  }
  clearFilter() {
    this.dataSource.filter = '';
    this.searchVal.nativeElement.value = '';
    this.selectedFilterIndex = 0;
  }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  announceSortChange(sortState: Sort) {
    if (sortState.direction) {
      this._liveAnnouncer.announce(`Sorted ${sortState.direction}ending`);
    } else {
      this._liveAnnouncer.announce('Sorting cleared');
    }
  }

  editItem(data: any = {}, isEdit: string) {
    console.log("data to edit:: ", data);
    this.router.navigate(["/maintenance/main/addOrEditBillerInfo"], { queryParams: { id: data.id, isEdit: isEdit } });
  }

  billerInfo(){

    this.downloadService.downloadFiles("billerInfo" , this.users);
  }
  deleteItem(row) {
    this.confirmService.confirm({ message: `Are you sure? You want to delete?` })
      .subscribe(res => {
        if (res) {
          console.log("data to delete :: ", row);
          this.loader.open();
          this.apiService.deleteBillerInfo(row.id)
            .subscribe(data => {
              this.loader.close();
              this.fetchBillerInfo();
            },
              err => {
                if (err.status == 200) {
                  this.snack.open('Record Deleted Successfully', 'OK', {
                    duration: 4000,
                    verticalPosition: 'top',
                    horizontalPosition: 'right'
                  });
                  
                }  
              })
        }
      });
  }

  dataByFilterByOption(index, filter){
    return filter.value; 
 }

}
