import {
  Component,
  ElementRef,
  OnInit,
  ViewChild,
  OnDestroy,
} from "@angular/core";
import { MatDialogRef, MatDialog } from "@angular/material/dialog";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Subscription } from "rxjs";
import { AppConfirmService } from "app/shared/services/app-confirm/app-confirm.service";
import { AppLoaderService } from "app/shared/services/app-loader/app-loader.service";
import { ApiService } from "app/shared/services/api.service";
import { DataService } from "app/shared/services/data.service";
import { Router } from "@angular/router";
import { LocalStoreService } from "app/shared/services/local-store.service";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort, Sort } from "@angular/material/sort";
import { LiveAnnouncer } from "@angular/cdk/a11y";
import { MatTableDataSource } from "@angular/material/table";
import { rumangoAnimations } from "app/shared/animations/rumango-animations";
import { DownloadService } from "app/shared/services/download.service";
import { fetchTrxnStatus } from "app/shared/models/fetchTrxnStatus";

@Component({
  selector: "app-txn-cash-master-crud-table",
  templateUrl: "./txn-cash-master-crud-table.component.html",
  styleUrls: ["./txn-cash-master-crud-table.component.scss"],
  animations: rumangoAnimations
})
export class TxnCashMasterCrudTableComponent implements OnInit, OnDestroy {
  @ViewChild("searchVal") searchVal: ElementRef;
  selectedFilterIndex: number = 0;
  displayedColumns: string[] = ['entityCode', 'bankCode', 'branchCode','authStatus','recordStatus','action'];
  filterByOption: { key: string; value: string }[] = [
    { key: "entityCode", value: "Entity Code" },
  ];
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;

  dataSource = new MatTableDataSource();

  public items: any[];
  public getItemSub: Subscription;
  currency: string[] = [];
  till: any[] = [];
  branchCode: string[] = [];
  bankCode: string[] = [];
  entity: string[] = [];
  fetchTrxnCashStatus: fetchTrxnStatus;
  loading: boolean;
  data: any;
  tillData: any;

  constructor(
    private dialog: MatDialog,
    private snack: MatSnackBar,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,
    private api: ApiService,
    private dataService: DataService,
    private router: Router,
    private ls: LocalStoreService,
    private _liveAnnouncer: LiveAnnouncer,
    private downloadService: DownloadService
  ) {}

  ngOnInit(): void {
    this.getItems();
    this.getEntityCode();
    this.getCurrency();
    this.getBankCode();
    this.getBranchCode();
    this.getTillId();
  }
  ngOnDestroy() {
    if (this.getItemSub) {
      this.getItemSub.unsubscribe();
    }
  }
  getItems() {
    // this.items = transactionType;
    this.loading = true;
    this.getItemSub = this.api.fetchTxnCashMasterInfo().subscribe((data) => {
    
      for(let i=0;i<data.length;i++){
        if(data[i].authStatus == 'U'){
          data[i].authStatus = 'Unauthorized'
        }
        if(data[i].authStatus == 'A') {
          data[i].authStatus = 'Authorized'
        } 
        if(data[i].recordStatus == 'O'){
          data[i].recordStatus = 'Open'
        } 
        if(data[i].recordStatus == 'C'){
          data[i].recordStatus = 'Closed'
        }
      }
      this.items = data;
      this.dataSource = new MatTableDataSource(data);
      this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
      this.loading = false;
    });
  }

  onFileChange(event) {
    console.log('event', event.path[0].files[0]);
    const file: File = event.path[0].files[0];
    const uploadData = new FormData();
    uploadData.append('screenType', 'transactionCashMaster');
    uploadData.append('file', file);
    this.api.uploadExelFile(uploadData).subscribe((response) => {
      this.getItems();
      console.log(response);
      this.snack.open(`File Uploaded Successfully`, "OK", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
  }

  getCurrency() {
    this.api.getCountryValues().subscribe((resp) => {
      this.currency = resp;
    });
  }
  getBankCode() {
    this.api.getBankCode().subscribe((resp) => {
      this.bankCode = resp;
    });
  }
  getBranchCode() {
    this.api.getBranchCode().subscribe((resp) => {
      this.branchCode = resp;
    });
  }
  getEntityCode() {
    this.api.getEntityCode().subscribe((resp) => {
      this.entity = resp;
    });
  }
  getTillId() {
    let tillOrVault = "T";
    this.api.getTillData(tillOrVault).subscribe((resp) => {
      this.till = resp;
    });
  }

  doFilter(filterBy, e) {
    this.filterTable(filterBy);
    this.applyFilter(e);
  }

  filterTable(filterBy) {
    this.dataSource.filterPredicate = (data: any, filter: string): boolean => {
      return data[filterBy].toLocaleLowerCase().includes(filter);
    };
  }

  clearFilter() {
    this.dataSource.filter = "";
    this.searchVal.nativeElement.value = "";
    this.selectedFilterIndex = 0;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  announceSortChange(sortState: Sort) {
    if (sortState.direction) {
      this._liveAnnouncer.announce(`Sorted ${sortState.direction}ending`);
    } else {
      this._liveAnnouncer.announce("Sorting cleared");
    }
  }

  txnCashMaster(){
    this.downloadService.downloadFiles("txnCashMaster" , this.items);
  }

  deleteItem(row) {
    this.confirmService
      .confirm({ message: `Delete ${row.entityCode}?` })
      .subscribe((res) => {
        if (res) {
          this.loader.open();
          this.api.deleteTxnCashMaster(row.id).subscribe((data) => {
            this.getItems();
            this.loader.close();
            this.snack.open("Record deleted!", "OK", { duration: 4000 });
          });
        }
      });
  }

  openPopUp(row) {
    this.getTxnCashMasterById(row);
    // this.dataService.sendNavParam(this.fetchTrxnCashStatus);
    this.router.navigate([`/maintenance/main/transactionCashMaster/${row.id}`]);
  }

  getTxnCashMasterById(row) {
    this.loader.open();
    this.api.fetchTxnCashMasterById(row.id).subscribe((result) => {
      this.fetchTrxnCashStatus = result;

      // this.data = result;
      // if(this.data != null){
      //   console.log("this.till::",this.till)
      //   console.log("this.data::",this.data)

      //   this.tillData = this.till.find(x => x.id == this.data.tillId);
      //   console.log("this.data::",this.tillData)
      //   if(this.tillData != null){
      //     this.data.tillId = this.tillData.tillVaultCode;
      //   }
      //   this.fetchTrxnCashStatus =  this.data;
      // }

      if (this.fetchTrxnCashStatus != null) {
        localStorage.setItem(
          "fetchTrxnCashStatus",
          JSON.stringify(this.fetchTrxnCashStatus)
        );
        console.log(this.fetchTrxnCashStatus);
        this.loader.close();
      }
    });
  }

  addNew() {
    this.router.navigate(["/maintenance/main/transactionCashMaster/add"]);
  }

  goBack() {
    this.router.navigate(["/maintenance/main/"]);
  }
  dataByFilter(index,item){
    return item.key

  }
}
